import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../constants/domain';
function LargestSpenderCard() {

  const [isVisible, setVisibility] = useState(true)
  const [tableData, setTableData] = useState([]);
  useEffect(() => {
      getData();
  }, [])

  function getData(){
    axios.get(`${domain}/index.php?action=getLargestSpender`)
    .then(function(response){
      setTableData(response.data);
    }).catch(error => console.error('Error fetching data: ', error));
  }

  const toggleVisibility = () => {
    setVisibility((prevIsVisible) => !prevIsVisible);
  };
  
  return (
      <div className='mt-2 mx-1 ms-3'>
        <div className={`${dst.secondCard} p-1`}>
        <div className='row'>
          <div className='col-10'>
            <p className="m-3 mb-4 fw-bold" style={{textAlign: 'left'}}>Largest Spender</p>
          </div>
          <div className='col-1'>
            <p className='text-end p-3' onClick={toggleVisibility}>
            <i
              className={`fa-solid ${isVisible ? 'fa-eye' : 'fa-eye-slash'} fa-sm`}
              style={{ fontSize: '16px', cursor: 'pointer' }}
            ></i>
            </p>
          </div>
        </div>
        <div className='row'>
          <div className='col-12'>
          {isVisible === true && (<div>
          <table
            className="table text-center position-relative"
            style={{
              border: '#F0F1F3',
              color: '#48505E',
              height: 'auto',
              fontSize: 'small'
            }}
          >
            <thead>
              <tr>
                <th scope="col" >
                  Name
                </th>
                <th scope="col" >
                  Spending Total
                </th>
              </tr>
            </thead>
            <tbody>
            {tableData.slice(0, 3).map((data, index) => (
                <tr key={index}>
                <td>
                {data ? (
                    <>
                      {data.pdl_lastname && data.pdl_lastname.toUpperCase()},{' '}
                      {data.pdl_firstname} {data.pdl_middlename}
                    </>
                  ) : (
                    ''
                  )}
                </td>
                <td>
                  PHP {data.formatted_alltime_spend}
                </td>
              </tr>
              ))}
              {tableData.length === 0 && (
              <tr>
                <td style={{padding: '43px 0'}} colSpan={2}>Nothing to show</td>
              </tr>
              )}
            </tbody>
          </table>
          </div>)}
          {isVisible === false && (
            <div>
              <div className='row'>
                  <div className='col-4'>
                  </div>
                  <div className='col-4' style={{padding: '69px 0'}}>
                    Details Hidden
                  </div>
                  <div className='col-4'>
                  </div>
                </div>
            </div>
          )}
          </div>

        </div>
          
        </div>
      </div>
  );
}

export default LargestSpenderCard;
