import React, {useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import Success from './PurchaseSuccess';
import Fingerprint from './FingerprintIDScan';
import axios from 'axios';
import domain from '../../constants/domain';

function PurchaseDetails({onClose, priceVal, posElements, pdlData}) {

  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [fPostData, setFPostData] = useState({});
  const newBalance = parseFloat(parseFloat(pdlData.balance) - parseFloat(priceVal)).toFixed(2);
  const openSuccessModal = () => {
    
  if(parseFloat(priceVal) <= parseFloat(pdlData.balance)){
      const postData = {
        posElements: posElements,
        pdlData: pdlData,
        priceVal: parseFloat(priceVal).toFixed(2),
        email: localStorage.getItem("email"),
        action: 'purchaseItems',
        newBalance: newBalance
      }
      axios.post(domain, postData).then(response => {
        if(response.data.weeklySpend){
          setErrorMessage(response.data.message + ' - PHP' + response.data.spendingValue);
        }
        else{
          setSuccessModalOpen(true);
        }
        // Handle the response if needed
        
      })
      .catch(error => {
        console.error('Error:', error);
        // Handle errors if needed
      });    }
    else{
      setErrorMessage('Insufficient balance');
    }
  };
  const closeSuccessModal = () => {
    setSuccessModalOpen(false);
    onClose();
  };

  const [isFingerprintIDModalOpen, setFingerprintIDModalOpen] = useState(false);
  const openFingerprintModal = () => {
    if(pdlData.fingerprint_id){
      if((parseFloat(priceVal) <= parseFloat(pdlData.balance))){
        const postData = {
          posElements: posElements,
          pdlData: pdlData,
          priceVal: parseFloat(priceVal).toFixed(2),
          email: localStorage.getItem("email"),
          action: 'purchaseItems',
          newBalance: newBalance
        }
        setFPostData(postData);

        setFingerprintIDModalOpen(true);
      }
      else{
        setErrorMessage('Insufficient balance');
      }
    }
    else{
      setErrorMessage('No fingerprint data available');
    }
  };
  const closeFingerprintModal = () => {
    setFingerprintIDModalOpen(false);
    onClose();
  };

  
  return (
    <div className={dst.modal} id="exampleModal" tabIndex="-1">
    <div className={dst['modal-content']} style={{ width: '600px', marginTop: '12%' }}>
      <div className={dst['modal-header']} style={{ border: 'none', textAlign: 'left' }}>
        <div className='row'>
          <h1 className="modal-title fs-5 fw-bold" id="exampleModalLabel">
            Purchase Details
          </h1>
        </div>
        <hr />
      </div>
      <div className={dst['modal-body']} style={{ padding: '0', fontSize: '12px' }}>
        <div className="row">
          <table className="table">
            <thead>
                <tr>
                  <th scope="col">Item No.</th>
                  <th scope="col">Product Type</th>
                  <th scope="col">Product Name</th>
                  <th scope="col">Category</th>
                  <th scope="col">Quantity</th>
                  <th scope="col">Total Price</th>
                </tr>
              </thead>
              <tbody>
              {posElements.map((item, index) => (
                <tr key={index}>
                  <td>{`I-${item.item_id}`}</td>
                  <td>{item.prodType}</td>
                  <td>{item.prodName}</td>
                  <td>{item.category}</td>
                  <td>{`${item.quantity}pcs`}</td>
                  <td>{`PHP${item.formatted_price} 
                  x ${item.quantity} = 
                  ${(parseFloat(item.quantity)* parseFloat(item.formatted_price)).toFixed(2)}`}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        <hr />
        <div className="row g-3 align-items-center p-2 ">
              <div className='col-8 text-end'>
                <h6>Total Price:</h6>
              </div>
              <div className='col-4 text-end '>
                <h2 className='fw-bold'>PHP{priceVal.toFixed(2)}</h2>
              </div>
            </div>
            <div className="row align-items-end p-2">
              <div className='col-9 text-end'>
                <p>PDL Balance:</p>
              </div>
              <div className='col-3 text-end '>
                <h4>PHP{parseFloat(pdlData.balance).toFixed(2)}</h4>
              </div>
            </div>
        <div className={dst['modal-footer']} style={{ border: 'none' }}>
          <button
            type="button"
            className="btn"
            onClick={onClose}
            style={{ borderColor: '#D0D3D9', marginLeft: '135px' }}
          >
            Back
          </button>
          <button
            type="button"
            className="btn"
            style={{
              backgroundColor: '#505050',
              color: '#fff',
              marginLeft: '10px'
            }}
            onClick={openSuccessModal}
          >
            Pay with Cash
          </button>
          <button
            type="button"
            className="btn"
            style={{
              backgroundColor: '#222',
              color: '#fff',
              marginLeft: '10px'
            }}
            onClick={openFingerprintModal}
          >
            Pay with Biometrics
          </button>
          <p className='mt-1 mt-2' style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
        </div>
      </div>
    </div>
    {isSuccessModalOpen && <Success onClose={closeSuccessModal} newBalance={newBalance}/>}
    {isFingerprintIDModalOpen && <Fingerprint onClose={closeFingerprintModal} typeOfFinScan={'purchase'} pdlData={pdlData} postData={fPostData}/>}
  </div>
  );
}

export default PurchaseDetails;
