import React, {useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import { Link } from 'react-router-dom';
import axios from 'axios';
import domain from '../../constants/domain';

function Verification({email, fromWhere, onClose}) {

  const [verificationCode, getVerificationCode] = useState('');
  const {code} = verificationCode;

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    const action = 'accountVerification';
    getVerificationCode(values => ({...values, [name]: value, ['email']: email, ['action']: action}));
};
  const handleSubmit = async (e) => {
    e.preventDefault();
    if(!verificationCode){
      alert('Nothing is in the textbox');
    }
    else{
      try{
        const response = await axios.post(domain, verificationCode // Pass the email received as a prop to the component
        )
        if (response.data.success) {
          alert(response.data.message);
          window.location.href = '/login';
        } else {
          alert(response.data.message);
        }
      }
      catch (error){
        console.error('Error:', error);
      }
    }
      
    // Send verification code to the server
    
    
    // Handle the response from the server (e.g., show a message to the user)
  };

  return (
    <div className={dst.modal} id="successModal" tabIndex="-1">
      <div>
        <div className={dst['modal-content']} style={{width: '500px', marginTop: '15%'}}>

          <div className={`text-center ${dst['modal-body']}`}>
            <i className='fa-solid fa-triangle-exclamation' style={{fontSize: '40px', marginBottom: '20px'}}></i>
            <h3>Verification Sent</h3>
            <p className={dst['color-a1a1a1']}>Check {email} for the OTP</p>

            <div className={`modal-footer d-flex justify-content-center ${dst['modal-footer']} ${dst['d-flex']} ${dst['justify-content-center']}`} style={{ border: 'none' }}>
                <div className='row'>
                  <div className='col-12'>
                    <input id='verificationCode' name='verificationCode'
                      style={{width: '200px', marginBottom: '15px', fontSize: '28px', textAlign: 'center'}}
                      onChange={handleChange}
                    >
                    </input>
                    <div className='row'>
                      <div className='col-6'>
                      
                      {fromWhere === 'regForm' && (<Link to="/">
                        <button type="button" className={`btn btn-secondary`} style={{ backgroundColor: '#fff', border: '1px solid #ccc', color: '#505050', width: 'stretch'}}>Later</button>
                      </Link>)}
                      {fromWhere === 'loginForm' && (
                        <button type="button" onClick={onClose} className={`btn btn-secondary`} style={{ backgroundColor: '#fff', border: '1px solid #ccc', color: '#505050', width: 'stretch'}}>Close</button>)}
                      </div>
                      <div className='col-6'>
                          <button type="submit" onClick={handleSubmit}className={`btn btn-secondary`} style={{ backgroundColor: '#505050', color: '#fff', width: 'stretch'}}>Verify</button>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
          </div>
        </div>
      </div>
      
    </div>
  );
}

export default Verification;
