
import React, { useState, useEffect } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import { Link, useLocation } from 'react-router-dom';
import UserProfile from '../modals/UserProfileModal';
import ConfirmArchive from '../modals/ConfirmArchive';
import PageNavigator from '../navbars/PageNavigator';
import axios from 'axios';
import domain from '../../constants/domain';

function UserList() {
  
  const [pdlData, setPdlData] = useState([]);
  const [selectedPdlId, setSelectedPdlId] = useState(null);
  const [primaryID, setPrimaryID] = useState(null);
  const [sortedPdlData, setSortedPdlData] = useState([]);
  const [selectedOption, setSelectedOption] = useState(''); // Set the default option to "option4"
  const [userProfileModal, setUserProfileModal] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [entriesPerPage] = useState(10);
  const [userEmail, setUserEmail] = useState(null);
  useEffect(() => {
    // Make an API call to fetch PDL data
    axios.get(`${domain}/index.php?action=getUserList`)
      .then(response => {
      // Set the fetched data to the state
      setPdlData(response.data);
      setSortedPdlData(response.data); 
    })
    .catch(error => console.error('Error fetching PDL data:', error));

  }, []); // Empty dependency array means this effect runs once on component mount


  

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
    // Implement logic to load data for the new page here
  };
  
  
  const openUserProfileModal = (email, e) => {
    setUserEmail(email);
    setUserProfileModal(true);
  };
  const closeUserProfileModal = () => {
    setUserProfileModal(false);
  };


  const [isArchiveModalOpen, setArchiveModalOpen] = useState(false);
  const openArchiveModal = (email, e, primID) => {
    setArchiveModalOpen(true);
    setUserEmail(email);
    setPrimaryID(primID);
    
  };
  const closeArchivePDLModal = (id) => {
    setArchiveModalOpen(false);
  };


  const handleOptionChange = (event) => {
    const option = event.target.value
    setSelectedOption(option);
    const sortedData = sortPdlData(option);
    setSortedPdlData(sortedData)
  };

  
  const sortPdlData = (option) => {
    let sortedData = [...pdlData];
  
    switch (option) {
      case 'option1': // Sort alphabetically
        sortedData.sort((a, b) => {
          const nameA = `${a.pdl_lastname} ${a.pdl_firstname} ${a.pdl_middlename}`.toUpperCase();
          const nameB = `${b.pdl_lastname} ${b.pdl_firstname} ${b.pdl_middlename}`.toUpperCase();
          return nameA.localeCompare(nameB);
        });
        break;
  
      case 'option2': // Sort by highest balance
        sortedData.sort((a, b) => b.balance - a.balance);
        break;
  
      case 'option3': // Sort by lowest balance
        sortedData.sort((a, b) => a.balance - b.balance);
        break;
  
      case 'option4': // Sort by PDL number
        sortedData.sort((a, b) => a.id - b.id);
        break;
  
      default: // Default: Sort by PDL number
        sortedData.sort((a, b) => a.id - b.id);
        break;
    }
  
    return sortedData;
  };

    const totalEntries = sortedPdlData ? sortedPdlData.length : 0;
    const totalPages = Math.ceil(totalEntries / entriesPerPage);
    let entriesToShow = [];
    if (Array.isArray(sortedPdlData)) {
      entriesToShow = sortedPdlData.slice(
        (currentPage - 1) * entriesPerPage,
        currentPage * entriesPerPage
      );
  }

  const generateReport = () => {
      if(pdlData){
        const action = "generatePDLReport";
        const actionData = { action };
        
        axios.post(domain, actionData)
            .then(response => {
                // Handle successful response, if needed
                 // Response.data will contain the file URL
                 const fileUrl = response.data.fileUrl;
                  const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/reports/'));
                // Open the PDF in a new tab
                window.open(formattedFileUrl, '_blank');
            })
            .catch(error => {
                // Handle error, if needed
                console.error('Error generating report:', error);
            });
      }
  }

  return (
    <div className="container-fluid p-5" >
      <div className={dst.con}>
        
        <div className={`${dst.row} mt-1`}>
        <div style={{display: 'flex', padding: '10px'}}>
          <div className='col-3'>
          <p className={`fw-bold ms-3 textColor`} style={{textAlign: 'left', marginTop: '15px'}}>Users</p>
          </div>
          <div className='col-9 d-flex justify-content-end'>
          
          </div>
        </div>
        <div className='row'>
          <div className='col-12' style={{ width: '100%', padding: 0 }}>
          <table
            className={dst.tableZebra}
            
            style={{
              border: '#F0F1F3',
              color: '#48505E',
              height: 'auto',
              fontSize: 'small',
              padding: '0',
              textAlign: 'left',
              width: '93.5%', // Set the table width to 100%
              margin: '0 auto',
            }}
          >
            <thead>
              <tr >
                <th scope="col">
                  User ID
                </th>
                <th scope="col" >
                  Name
                </th>
                <th scope="col" >
                  Email Address
                </th>
                <th scope="col" >
                  Position
                </th>
                <th scope="col" >
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className={dst.shrinkMedia}>
            {sortedPdlData && entriesToShow.map((pdl, index) => (
                <tr key={index}>
                  <td>User #{pdl.id}</td>
                  <td>{pdl ? (
                    <>
                      {pdl.lastName && pdl.lastName.toUpperCase()},{' '}
                      {pdl.firstName} {pdl.middleName}
                    </>
                  ) : (
                    ''
                  )}</td>
                  <td>{pdl.emailAdd}</td>
                  <td>{pdl.position}</td>
                  <td>
                  <div className={`${dst.actionButtons}`}>
                      <p onClick={(e) => openUserProfileModal(pdl.emailAdd, e)}><i className="fa-solid fa-eye fa-sm"></i></p>
                      {localStorage.getItem("email") != pdl.emailAdd && (<>
                      <p onClick={(e) => openArchiveModal(pdl.emailAdd, e, pdl.userid)}> <i className="fa-solid fa-box-archive fa-sm"></i></p></>)}
                    </div>
                  </td>
                </tr>
              ))}
            {sortedPdlData && entriesToShow.length === 0 && (
              <tr>
                <td style={{padding: '150px', textAlign: 'center' }} colSpan={6}>Nothing to show</td>
              </tr>
              )}
            </tbody>
          </table>
          </div>
          <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '15%', marginBottom: '20px'}}>
          <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
          </div>
          </div>
        </div>
      </div>
      {userProfileModal && <UserProfile onClose={closeUserProfileModal} userEmail={userEmail}/>}
      {isArchiveModalOpen && <ConfirmArchive onClose={closeArchivePDLModal} primaryUserEmail={userEmail} primaryUserID={primaryID} archiveType="User"/>}
    </div>
    
  );
}

export default UserList;
