import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import PageNavigator from '../navbars/PageNavigator';
import axios from 'axios';
import domain from '../../constants/domain';


function RecentTransactions({onClose, pdlId}) {

  const [recentTransactions, setRecentTransactions] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [entriesPerPage] = useState(7);
  useEffect(() => {
    if (pdlId) {
      axios
        .get(`${domain}/index.php?action=getAllRecentTransactions&pdlNo=${pdlId}`)
        .then(function (response) {
          setRecentTransactions(response.data);
        })
        .catch((error) => console.error('Error fetching transaction data:', error));
    }
  }, [pdlId]);  // Add userData as a dependency
  
  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
    // Implement logic to load data for the new page here
  };
  
  const [selectedOption, setSelectedOption] = useState('');
  const handleOptionChange = (event) => {
    setSelectedOption(event.target.value);
  };
  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const openSuccessModal = () => {
    
    setSuccessModalOpen(true);
  };
  const closeSuccessModal = () => {
    setSuccessModalOpen(false);
    onClose();
  };

    const handleBackdropClick = (e) => {
      // Check if the click target is the backdrop element
      if (e.target.id === 'exampleModal') {
        onClose(); // Close the modal
      }
    };
  
    const totalEntries = recentTransactions ? recentTransactions.length : 0;
    const totalPages = Math.ceil(totalEntries / entriesPerPage);
    let entriesToShow = [];
    if (Array.isArray(recentTransactions)) {
      entriesToShow = recentTransactions.slice(
        (currentPage - 1) * entriesPerPage,
        currentPage * entriesPerPage
      );
}
  const handleViewID = (e, id_path) => {
      const formattedIDURL = id_path.substring(id_path.indexOf('/receipts/'));
      window.open(formattedIDURL, '_blank');
  }
  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="exampleModal" tabIndex="-1">
            <div className={dst['modal-content']} style={{width: '600px', marginTop: '9%'}}>
            <div className={dst['modal-header']} style={{ border: 'none' }}>
            <div className='row'>
                <div className='col-12'>
                <h1 className="fs-5 fw-bold" id="exampleModalLabel" style={{ alignItems: 'flex-end', marginTop: '5px', lineHeight: '1.5' }}>
                    Recent Transactions
                </h1>
                </div>
            </div>
            </div>
            <div className={dst['modal-body']}>
            <table
                className={dst.tableZebra}
                style={{
                border: '#F0F1F3',
                color: '#48505E',
                height: 'auto',
                fontSize: 'small',
                width: '97%',
                padding: '0',
                textAlign: 'left'
                }}
            >
                <thead>
                <tr>
                    <th scope="col" >
                    Transaction ID
                    </th>
                    <th scope="col" >
                    Date and Time
                    </th>
                    <th scope="col" >
                    Description
                    </th>
                    <th scope="col" >
                    Actions
                    </th>
                </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {entriesToShow.length > 0 ? (
                    entriesToShow.map((data, index) => (
                        <tr key={index}>
                        <td>{data ? (
                            <>
                            T-{data.id}
                            </>
                        ) : (
                            ''
                        )}</td>
                        <td>{data.date_time}</td>
                        <td>{data.description}</td>
                        <td>
                     
                        <div className={`${dst.actionButtons}`}>
                            <p onClick={(e) => handleViewID(e, data.transaction_path)}><i className="fa-solid fa-eye fa-sm"></i></p>
                            </div>
                        </td>
                        </tr>
                    ))
                ) : (
                    <tr>
                    <td style={{ padding: '150px', textAlign: 'center' }} colSpan={4}>Nothing to show</td>
                    </tr>
                )}
                </tbody>
            </table>
                </div>
                <div className='row' style={{alignItems: 'center', justifyContent: 'center', marginTop: '15%'}}>
                <div style={{ textAlign: 'center', width: '100%' }}> 
                    <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
                </div>
                </div>
            </div>

        </div>
  );
}

export default RecentTransactions;
