import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import Success from './SuccessfulAction';
import axios from 'axios';
import domain from '../../constants/domain';

function StockEntryModal({onClose, itemId}) {

  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const [stockData, setStockData] = useState({});
  useEffect (() => {
      getStock();
  }, [])
  
function getStock(){
  axios.get(`${domain}/index.php?action=displaySpecificStock&itemId=${itemId}`)
  .then(function(response) {
      setStockData(response.data[0]);
  }).catch(error => console.error('Error fetching stock data: ', error));
}


  const openSuccessModal = () => {
    
    setSuccessModalOpen(true);
  };
  const closeSuccessModal = () => {
    setSuccessModalOpen(false);
    onClose();
  };

    const handleBackdropClick = (e) => {
      // Check if the click target is the backdrop element
      if (e.target.id === 'exampleModal') {
        onClose(); // Close the modal
      }
    };
  
  
  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="exampleModal" tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '600px', marginTop: '12%'}}>
          <div className={dst['modal-header']} style={{ border: 'none', display: 'flex'}}>
            <h1 className="modal-title fs-5" id="exampleModalLabel">
              Stock Details
            </h1>
          </div>
          <div className={dst['modal-body']}>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                  Item No.: 
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                  {stockData.id}
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="productType" className="col-form-label">
                  Product Type:
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="productType" className="col-form-label">
                {stockData.prodType}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                  Product Name:
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                  {stockData.prodName}
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="productType" className="col-form-label">
                  Category:
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="productType" className="col-form-label">
                {stockData.category}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                  Remaining Stocks:
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                {stockData.remainingStock}
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                  Critical Stock Threshold:
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                {stockData.criticalStock}
                </label>
              </div>
              
            </div>
            <div className="row g-3 align-items-center mb-2">
            <div className="col-3">
                <label htmlFor="productType" className="col-form-label">
                  Price:
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="productType" className="col-form-label">
                  PHP {stockData.formatted_price}
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                  Date Added:
                </label>
              </div>
              <div className="col-3">
                <label htmlFor="itemNo" className="col-form-label">
                {stockData.dateAdded}
                </label>
              </div>
            </div>
            <div className={dst['modal-footer']} style={{ border: 'none' }}>
              <button
                type="button"
                className="btn"
                onClick={onClose}
                style={{ borderColor: '#D0D3D9', marginLeft: '455px' }}
              >
                Back
              </button>
            </div>
          </div>
        </div>
        {isSuccessModalOpen && <Success onClose={closeSuccessModal}/>}
    </div>
  );
}

export default StockEntryModal;
