import React, {useEffect, useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import domain from '../../constants/domain';
import axios from 'axios';

function EnterItemCode({searchStock, itemSearch, errorMessage, setItemSearch}){
  const [optionDisplay, setOptionDisplay] = useState([]);
  useEffect (() => {
    axios.get(`${domain}/index.php?action=displayStock`)
    .then(response => {
      if (!response.data.fail) {
        // Update the state with the fetched PDL values
        setOptionDisplay(response.data);
      }
    })
    .catch(error => console.error('Error fetching PDL values:', error));
  
  }, [])
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    const action = 'checkDetails';
    setItemSearch(values => ({...values, action, [name]: value}))
    return;
  }
    return(
        <div className={dst.con} style={{width: '100%', height: 'fit-content'}}>
            <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="name" className="col-form-label text-start ms-3">Enter Item Code:</label>
                </div>
                <div className="col-7">
                  <select
                    name="searchItem"
                    className="form-select"
                    style={{ width: '100%', boxShadow: 'none' }}
                    onChange={handleChange}
                    value={itemSearch.searchItem || ''}
                    >
                    <option value="" hidden disabled>Select Stock</option>   
                    {Array.isArray(optionDisplay) && optionDisplay.map((item, index) => (
                        <option key={index} value={item.id}>
                        {item.prodType} - {item.prodName} - {item.id} 
                        </option>
                    ))}
                    </select>
                </div>
                <div className="col-3">
                <button type="submit" className="btn" onClick={searchStock} style={{ backgroundColor: '#505050', color: '#fff'}}>Search</button>
                </div>
                <p className='mt-1 mb-2' style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
              </div>
              
        </div>
    );

}
export default EnterItemCode;