import React, { useState, useEffect } from "react";
import dst from "../../styles/DashboardStyle.module.css"; // Import your CSS module
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
import domain from "../../constants/domain";

function StockEntryModal({ onClose, itemId, primaryID }) {
  const navigate = useNavigate();
  const [inputs, setInputs] = useState({});
  const [selectCategory, setSelectCategory] = useState("");
  const { id } = useParams();
  const [errorMessage, setErrorMessage] = useState("");

    useEffect (() => {
      getStock();
  }, [])

  function getStock(){
  axios.get(`${domain}/index.php?action=editStock&primId=${primaryID}`)
  .then(function(response) {
      setInputs(response.data[0]);
  }).catch(error => console.error('Error fetching stock data: ', error));
  }
  const action = "updateStocks";
  
  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    if (name === "category") {
      setSelectCategory(value); // Update maritalStatus directly
      setInputs((values) => ({ ...values, action, category: value }));
      return;
    } else {
      setInputs((values) => ({ ...values, action, [name]: value }));
      return;
    }

  };
  const handleSubmit = (event) => {
    event.preventDefault();
    const {prodType, prodName, category, price, dateAdded, } = inputs;
    const updatedInputs = {
      ...inputs, action: action,
    };
    if(!prodType || !prodName || !category || !price || !dateAdded){
      setErrorMessage('Please fill in all required fields!');
      return;
    }
    else{
      setErrorMessage('');
      axios.post(`${domain}/index.php?action=updateStocks&entry=${primaryID}`, updatedInputs)
      .then(function (response) {
        window.location.reload();
      });
    }

    
  };
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === "exampleModal") {
      onClose(); // Close the modal
    }
  };
  return (
    <div
      className={dst.modal}
      onClick={handleBackdropClick}
      id="exampleModal"
      tabIndex="-1"
    >
      <div
        className={dst["modal-content"]}
        style={{ width: "800px", marginTop: "12%" }}
      >
        <div
          className={dst["modal-header"]}
          style={{ border: "none", display: "flex" }}
        >
          <h1 className="modal-title fs-5" id="exampleModalLabel">
            Edit Stock Entry
          </h1>
          <button
            type="button"
            className="btn-close"
            style={{ boxShadow: "none", marginLeft: "75%" }}
            onClick={onClose}
          ></button>
        </div>
        <div className={dst["modal-body"]}>
          <form onSubmit={handleSubmit}>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="prodType" className="col-form-label">
                  Product Type:
                </label>
              </div>
              <div className="col">
                <input
                  type="text"
                  id="prodType"
                  name="prodType"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter product type"
                  value={inputs.prodType}
                  onChange={handleChange}
                />
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="prodName" className="col-form-label">
                  Product Name:
                </label>
              </div>
              <div className="col">
                <input
                  type="text"
                  id="prodName"
                  name="prodName"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter product name"
                  value={inputs.prodName}
                  onChange={handleChange}
                />
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="category" className="col-form-label">
                  Category:
                </label>
              </div>
              <div className="col">
                <select
                  id="category"
                  name="category"
                  className="form-control"
                  style={{ boxShadow: "none", color: "#666" }}
                  value={inputs.category}
                  onChange={handleChange}
                >
                  <option value="" disabled hidden>
                    Select category
                  </option>
                  <option value="Personal Hygiene">Personal Hygiene</option>
                  <option value="Consumables">Consumables</option>
                  <option value="Toiletries">Toiletries</option>
                </select>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="price" className="col-form-label">
                  Price:
                </label>
              </div>
              <div className="col">
                <input
                  type="number"
                  id="price"
                  name="price"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter a number"
                  value={inputs.price}
                  onChange={handleChange}
                  step={.01}
                />
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="dateAdded" className="col-form-label">
                  Date Added:
                </label>
              </div>
              <div className="col">
                <input
                  type="date"
                  id="dateAdded"
                  name="dateAdded"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  value={inputs.dateAdded}
                  onChange={handleChange}
                />
              </div>
            </div>
            <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
            <div className={dst["modal-footer"]} style={{ border: "none" }}>
              <button
                type="button"
                className="btn"
                onClick={onClose}
                style={{ borderColor: "#D0D3D9", marginLeft: "385px" }}
              >
                Discard
              </button>
              <button
                type="submit"
                className="btn"
                style={{
                  backgroundColor: "#505050",
                  color: "#fff",
                  marginLeft: "10px",
                }}
              >
                Submit
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default StockEntryModal;
