import React from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path

function SuccAcc({ onClose, newBalance }) {

    const closeSuccessModal = () => {
      localStorage.removeItem("pdlData");
      localStorage.removeItem("searchData");
      localStorage.removeItem("pdlPOSImageSrc");
      localStorage.removeItem("posPDL");
      localStorage.removeItem("responseData");
      window.location.reload();
      
    }
  return (
    <div className={dst.modal} id="successModal" tabIndex="-1">
      <div>
        <div className={dst['modal-content']} style={{width: '500px', marginTop: '15%'}}>

          <div className={`text-center ${dst['modal-body']}`}>
            <i className={`fa-regular fa-circle-check`} style={{fontSize: '40px', marginBottom: '20px'}}></i>
            <h3>Action Success</h3>
            <p className={dst['color-a1a1a1']}>Your remaining balance is <b>"PHP {newBalance}"</b></p>

            <div className={`modal-footer d-flex justify-content-center ${dst['modal-footer']} ${dst['d-flex']} ${dst['justify-content-center']}`} style={{ border: 'none' }}>
                <button type="submit" className={`btn btn-secondary`} style={{ backgroundColor: '#505050', color: '#fff', width: 'stretch'}} onClick={closeSuccessModal}>OK</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default SuccAcc;
