import React, { useEffect, useState } from 'react';

import DashboardSidebar from '../components/navbars/DashboardSideNavbar';
import TopNavBar from '../components/navbars/TopNavBar';
import PDLOvDet from '../components/sections/PDLOverallDetails';
import PrisonerProf from '../components/sections/PrisonerProfile';
import PriProfImg from '../components/sections/PrisonerProfileImg';
import BalAndPur from '../components/sections/BalanceAndPurchases';
import MostPurchases from '../components/sections/MostPurchases';
import { Helmet } from 'react-helmet-async';
import { useNavigate, useLocation } from 'react-router-dom';
import axios from 'axios';
import domain from '../constants/domain';

function PDLProfile({ isSidebarOpen, toggleSidebar }){
  const [userData, setUserData] = useState([]);
  const [imageSrc, setImageSrc] = useState(null);
  const history = useNavigate();
  const location = useLocation();
  const pdlId = new URLSearchParams(location.search).get('pdlId');
  const id = new URLSearchParams(location.search).get('id');
  const pdlNo = new URLSearchParams(location.search).get('pdlNo');
  useEffect(() => {
    // Check if user is logged in
    const token = localStorage.getItem('token');
    
    if (!token) {
      alert("User not logged in. Please login to continue");
      // If not logged in, redirect to login page
      history('/');
    }
    getUser();
  }, [history]);
  function getUser(){
    if(pdlId){
      axios.get(`${domain}/index.php?action=readPDL&pdlNo=${pdlId}`)
      .then(function(response) {
        setUserData(response.data[0]);
        const imageUrl = response.data[0].pdl_image;
        const fileName = imageUrl.split('/').pop();
        setImageSrc(fileName);
      })
      .catch(error => console.error('Error fetching PDL data:', error));
    }
    else if(id){
      axios.get(`${domain}/index.php?action=readPDL&pdlNo=${id}`)
    .then(function(response) {
      setUserData(response.data[0]);
      const imageUrl = response.data[0].pdl_image;
      const fileName = imageUrl.split('/').pop();
      setImageSrc(fileName);
    })
    .catch(error => console.error('Error fetching PDL data:', error));
    }
    else if(pdlNo){
      axios.get(`${domain}/index.php?action=readPDLAlt&entry=${pdlNo}`)
      .then(function(response) {
      setUserData(response.data[0]);
      const imageUrl = response.data[0].pdl_image;
      const fileName = imageUrl.split('/').pop();
      setImageSrc(fileName);
    })
    .catch(error => console.error('Error fetching PDL data:', error));
    }
    
 } 
  const calculateMargin = () => {
    if (window.innerWidth <= 1368) {
      return isSidebarOpen ? '170px' : '78px';
    } else {
      return isSidebarOpen ? '250px' : '78px';
    }
  };
  const [margin, setMargin] = useState(calculateMargin);

const updateMargin = () => {
  setMargin(calculateMargin());
};

useEffect(() => {
  window.addEventListener('resize', updateMargin);
  return () => {
    window.removeEventListener('resize', updateMargin);
  };
}, [isSidebarOpen]);

useEffect(() => {
  updateMargin(); // Update margin when sidebar state changes
}, [isSidebarOpen]);
  const contentStyle = {
  marginLeft: margin,
  transition: 'margin .5s ease',
  backgroundColor: '#e4e9f7',
  height: '100vh'
  };
    return(
        
        <div>
        <Helmet>
            <title>BJMP | PDL Profile</title>
        </Helmet>
        <DashboardSidebar isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}/>
        <div style={contentStyle}>
            <TopNavBar />
            <div className='row'>
              <div className='col-8'>
              <PDLOvDet userData={[userData]} />
              <PrisonerProf userData={[userData]}/>
              </div>
              <div className='col-3 mt-3'>
                <PriProfImg userData={[userData]} imageSrc={imageSrc}/>
              </div>
            </div>
            <div className='row'>
              <div className='col-6'>
                <BalAndPur pdlId={userData.id}/>
              </div>
              <div className='col-5'>
                <MostPurchases pdlId={userData.id} />
              </div>
            </div>
        </div>
        </div>
    );
    
}
export default PDLProfile;