import React, {useEffect, useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import { Line } from "react-chartjs-2";
import Chart from "chart.js/auto";
import axios from 'axios';
import domain from '../../constants/domain';
import Success from '../modals/SuccessfulAction';
function InventoryDetails({onClose, itemId}) {
  const [selectedPeriod, setSelectedPeriod] = useState('Weekly');
  const [invData, setInvData] = useState([]);
  const [successModal, setSuccessModal] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [itemData, setItemData] = useState([]);
  const action = "updateInventoryStocks";
  const [remStock, setRemStock] = useState('');
  useEffect(() => {
    if(itemId){
      axios.get(`${domain}/index.php?action=displaySpecificStock&itemId=${itemId}`)
      .then(response => {
        setInvData(response.data[0]);
        setRemStock(response.data[0].remainingStock);
      }).catch(error => console.error('Error getting record: ', error));
      axios.get(`${domain}/index.php?action=getItemSalesData&itemId=${itemId}`)
      .then(response => {
        setItemData(response.data[0]);
      }).catch(error => console.error('Error getting record: ', error));
    }
  }, [itemId]);


  const incrementQuantity = () => {
      setInvData(values => ({...values, remainingStock: parseInt(values.remainingStock, 10)+ 1}));
  };

  const decrementQuantity = () => {
    if (invData.remainingStock > 0) {
      setInvData(values => ({...values, remainingStock: parseInt(values.remainingStock, 10)- 1}));
    }
  };
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value
    setInvData(values => ({...values, action, 
      itemId: itemId, 
      [name]: value
    }));
    if(name === 'quantity'){
      setInvData(values => ({...values, 
        action, 
        itemId: itemId,
        remainingStock: parseInt(value)
      }));
    }
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    const {remainingStock, criticalStock} = invData;
    const formData = {
      remainingStock: remainingStock,
      criticalStock: criticalStock,
      action: action,
      itemId: itemId
    }
    if(!remainingStock || !criticalStock){
      setErrorMessage('Please enter a value');
    }
    else{
      axios.post(domain, formData)
      .then(response => {
        if(response.data.success){
          openSuccessModal();
        }
      })
    }
  }

  const openSuccessModal = () => {
    setSuccessModal(true);
  }
  const closeSuccessModal = () => {
    window.location.reload();
    setSuccessModal(false);
  }
  
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === 'exampleModal') {
      onClose(); // Close the modal
    }
  };



  return (
    <div className={dst.modal} id="exampleModal" onClick={handleBackdropClick} tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '800px', marginTop: '12%'}}>
          <div className={dst['modal-header']} style={{ border: 'none', display: 'flex'}}>
            <h1 className="modal-title fs-5 fw-semibold" id="exampleModalLabel">
              Inventory Details
            </h1>
            
          </div>
          <hr style={{margin: '0'}}></hr>
          {invData && (<div className={dst['modal-body']} style={{textAlign: 'left', fontSize: 'small'}}>
              <div className='row'>
                  
                  <div className='col-4'>
                    <div className='row'>
                      <div className='col-6 fw-semibold'>Product Type:</div>
                      <div className='col-6'>{invData.prodType}</div>
                    </div>
                    <hr style={{margin: '7px 0'}}></hr>
                    <div className='row'>
                      <div className='col-6 fw-semibold'>Product Name:</div>
                      <div className='col-6'>{invData.prodName}</div>
                    </div>
                    <hr style={{margin: '7px 0'}}></hr>
                    <div className='row'>
                      <div className='col-6 fw-semibold'>Category:</div>
                      <div className='col-6'>{invData.category}</div>
                    </div>
                    <hr style={{margin: '7px 0'}}></hr>
                    <div className='row'>
                      <div className='col-6 fw-semibold'>Price:</div>
                      <div className='col-6'>PHP {invData.formatted_price}</div>
                    </div>
                    <hr style={{margin: '7px 0'}}></hr>
                    <div className='row'>
                      <div className='col-6 fw-semibold'>Date Added:</div>
                      <div className='col-6'>{invData.dateAdded}</div>
                    </div>
                    <hr style={{margin: '7px 0'}}></hr>
                    <div className='row'>
                      <div className='col-6 fw-semibold'>Stock Quantity:</div>
                      <div className='col-6 d-flex'><button onClick={decrementQuantity} style={{background: 'none', border: '1px solid #ccc', padding: '4px', height: '25px'}}>-</button>
                                          <input
                                            type="number"
                                            name="remainingStock"
                                            id="remainingStock"
                                            style={{ width: '55px', height: '10px' }}
                                            value={parseInt(invData.remainingStock)}
                                            min="0"
                                            onChange={handleChange}
                                          />
                                          <button onClick={incrementQuantity} style={{background: 'none', border: '1px solid #ccc', padding: '4px', height: '25px'}}>+</button>
                                        </div>
                    </div>
                    <hr></hr>
                    <div className='row'>
                      <div className='col-6 fw-semibold'>Critical Limit:</div>
                      <div className='col-6 d-flex'>
                                        <input
                                            type="number"
                                            name="criticalStock"
                                            id="criticalStock"
                                            style={{height: '10px' }}
                                            value={parseInt(invData.criticalStock)}
                                            min="0"
                                            onChange={handleChange}
                                          />
                                        </div>
                    </div>
                  </div>
                  <div className='col-8'>
                      <div className='row'>
                          <div className='col-12'>
                              <h5 className='fw-bold text-center'>Stock Quantity</h5>
                          </div>
                      </div>
                      <div className='row'>
                          <div className='col-12'>
                              <h2 className='fw-bold text-center' style={{fontSize: '70px'}}>{remStock}</h2>
                          </div>
                      </div>
                      <hr style={{margin: '0 0 10px 0'}}></hr>
                      <div className='row'>
                          <div className='col-12'>
                              <h5 className='fw-bold text-center'>Total Sales</h5>
                          </div>
                      </div>
                      <div className='row'>
                          <div className='col-12'>
                              <h2 className='fw-bold text-center' style={{fontSize: '70px'}}>{Array.isArray(itemData) ? itemData.items_qty_sum : 0}</h2>
                          </div>
                      </div>
                  </div>
              </div>
          </div>)}
          <div className={dst['modal-footer']}>
            <hr></hr>
          <div className='col-12 d-flex justify-content-end'>
          <button className={`${dst.add} me-2`} style={{left: '0'}}type='button' onClick={handleSubmit}>Save Data
          </button>
          </div>
          </div>
        </div>
        {successModal && <Success onClose={closeSuccessModal}/>}
    </div>
  );
}

export default InventoryDetails;
