import React, { useEffect, useState, useRef } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import axios from 'axios';
import domain from '../../constants/domain';
import Success from './SuccessfulAction';
function UserProfile({ onClose, loggedUserData, imageSrc, setLoggedUserData, userEmail }) {
  
  const [editMode, setEditMode] = useState(false);
  const [changeImageSrc, setChangeImageSrc] = useState(null);
  const [formData, setFormData] = useState([]);
  const [tempImgSrc, setTempImageSrc] = useState(null);
  const [errorMessage, setErrorMessage] = useState('');
  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const [userDetails, setUserDetails] = useState([]);
  const fileInputRef = useRef(null);
  const action = 'updateUserInfo';
  const rectWidth = 162 ; // Adjust width based on imageSrc
  const rectHeight = 162 ; // Adjust height based on imageSrc

  useEffect(() => {
    if(loggedUserData){
      setFormData(loggedUserData[0]);
    }
    else if(userEmail){
      axios.get(`${domain}/index.php?action=getLoggedUserData&email=${userEmail}`)
      .then(
        response => {
          if(response.data){
            setUserDetails(response.data);
            const imageUrl = response.data[0].valid_ID;
            const fileName = imageUrl.split('/').pop();
            setTempImageSrc(fileName);
          }
        }
      )
      .catch(error => console.error("Error getting user data: ", error))
    }
  }, [])

  const handleSvgClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click(); // Trigger click on the file input
    }
  };
  const enableEditMode = () => {
    setEditMode(true);
    setTempImageSrc(imageSrc);
  }

  const disableEditMode = () => {
    setEditMode(false);
  }
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === 'addPdl') {
      onClose(); // Close the modal
    }
  };

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.type === 'file' ? e.target.files[0] : e.target.value;
  
    if (e.target.type === 'file') {
      const reader = new FileReader();
  
      reader.onload = (e) => {
        setChangeImageSrc(e.target.result);
        // Keep tempImgSrc if not editing the image
        setTempImageSrc(null);
        setFormData(values => ({ ...values, action, valid_ID: value })); // Use e.target.files[0]
      };
      if (value) {
        reader.readAsDataURL(value);
      } else {
        setChangeImageSrc('');
      }
      return;
    }
  
    setFormData(values => ({ ...values, action, [name]: value }));
  };
  const handleSubmit = async (e) => {
    e.preventDefault();
    const { bjmp_branch, complete_address, contact_number, firstName, middleName, lastName, valid_ID } = formData;
    const updatedFormData = {
      ...formData,
      action: action, // or whatever action you want
    };
    const fileUp = new FormData();
    fileUp.append('valid_ID', valid_ID);
    fileUp.append('userID', formData.userid);
    fileUp.append('action', 'updateUserImage');
    if (!bjmp_branch || !complete_address || !contact_number || !firstName || !middleName || !lastName || !valid_ID ) {
      setErrorMessage('Please fill in all required fields!');
      return;
    } 
    else {
        setErrorMessage('');
        axios.post(`${domain}/index.php?action=updateUserInfo&entry=${formData.userid}`, updatedFormData)
        .then(response => {
          if(response.data){
            axios.post(`${domain}/index.php?action=updateUserImage&entry=${formData.userid}`, fileUp)
            .then(respo => {
                if(respo.data){
                  openSuccessModal();
                }
            }).catch(error => console.error("Error: ", error));
          }
        }).catch(error => console.error('Error: ', error));
      }
  };

  const openSuccessModal = () => {
      setSuccessModalOpen(true);
      disableEditMode();
  };
    const closeSuccessModal = () => {
      setSuccessModalOpen(false);
    };

  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="addPdl" tabIndex="-1">
      <div className="">
        <div className={dst['modal-content']} style={{marginTop: '15vh', width: '500px'}}>
          <div className={dst['modal-header']} style={{ border: 'none', display: 'flex', padding: '0' }}>
            <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none', marginLeft: '95%' }}></button>
          </div>
          {Array.isArray(loggedUserData) ? loggedUserData.map((user) => (
          <div className={dst['modal-body']} style={{padding: '0'}}>
           
              <div className="row d-flex justify-content-center" style={{display: 'flex'}}>
                <div className="col-12" >
                    {editMode === false ?
                    (<><img className='mb-3' src={`../images/users/${imageSrc}`} alt="profileImage" style={{borderRadius: '250px'}} />
                    </>) 
                    : (<>
                    <svg xmlns="http://www.w3.org/2000/svg" style={{margin: '0', padding: '0'}} width={rectWidth+1} height={rectHeight+1} viewBox={`"0 0 ${rectWidth+1} ${rectHeight+1}"`} onClick={handleSvgClick} fill="none">
                    <rect x="0.5" y="0.5" width={rectWidth} height={rectHeight} rx="41" fill="white" stroke="#9D9D9D"
                      strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                      {tempImgSrc && (
                          <image
                            x="20%"
                            y="20%"
                            xlinkHref={`../images/users/${tempImgSrc}`}
                            width="100"
                            height="100"
                          />
                        
                      )}
                      {imageSrc && (
                          <image
                            x="20%"
                            y="20%"
                            xlinkHref={changeImageSrc}
                            width="100"
                            height="100"
                          />
                      )}
                      {!imageSrc && !tempImgSrc && (
                        <text x="50%" y="50%" textAnchor="middle" alignmentBaseline="middle" fill="#000000" fontSize="14">
                        Upload image here
                      </text>
                      )}     
                  </svg>
                  <input
                    type="file"
                    name='valid_ID'
                    id='valid_ID'
                    ref={fileInputRef}
                    style={{ display: 'none' }}
                    accept="image/*"
                    onChange={handleChange}
                  />
                    
                    </>)}
                    <hr style={{margin: '10px 0 10px 0'}}></hr>
                    {editMode === false ? (
                    <>
                    <p style={{fontSize: '24px', fontWeight: 'bold', margin: '0'}}>{
                    (user.lastName).toUpperCase()
                    }, {user.firstName} {user.middleName}</p>
                    <p style={{color: '#666'}}> {user.position}</p>
                    </>) : 
                    (<>
                    <div className='row mb-3'>
                      <div className='col-4'>
                        <input onChange={handleChange} value={formData.lastName} 
                        id='lastName' name='lastName' style={{padding: '0 10px', fontSize: '20px'}}></input> 
                      </div>
                      <div className='col-4'>
                        <input onChange={handleChange} value={formData.firstName} 
                        id='firstName' name='firstName' style={{padding: '0 10px', fontSize: '20px'}}></input> 
                      </div>
                      <div className='col-4'>
                        <input onChange={handleChange} value={formData.middleName} 
                        id='middleName' name='middleName' style={{padding: '0 10px', fontSize: '20px'}}></input> 
                      </div>
                    </div>
                    </>
                    )}
                  
                    
                </div>
              </div>
              {editMode === false ? (<div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                  <label htmlFor="emailAdd" className="col-form-label">
                  <i className="fa-solid fa-envelope"></i> Email Address:</label>
                </div>
                <div className="col-6">
                  <label htmlFor="emailAdd" className="col-form-label">{user.emailAdd}</label>
                  </div>
                </div>) : (<></>)}
                {editMode === true ? (<div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                  <label htmlFor="emailAdd" className="col-form-label">
                  <i className="fa-solid fa-user"></i> Position:</label>
                </div>
                <div className="col-6">
                <input onChange={handleChange} value={formData.position} id='position' name='position' style={{padding: '0 10px'}}></input>
                  </div>
                </div>) : (<></>)}
              <div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                    
                  <label htmlFor="contact_number" className="col-form-label">
                  <i className="fa-solid fa-phone"></i> Contact Number:</label>
                </div>
                <div className="col-6">
                {editMode === false ? (
                  <label htmlFor="contact_number" className="col-form-label">{user.contact_number}</label>
                  ) : 
                  (<input onChange={handleChange} value={formData.contact_number} id='contact_number' name='contact_number' style={{padding: '0 10px'}}></input> )}
                </div>
                </div>
                
                <div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                    
                  <label htmlFor="loc" className="col-form-label">
                  <i className="fa-solid fa-map-marker"></i> Address:</label>
                </div>
                <div className="col-6">
                {editMode === false ? (
                  <label htmlFor="complete_address" className="col-form-label">{user.complete_address}</label>
                  ) : 
                  (<input onChange={handleChange} value={formData.complete_address} id='complete_address' name='complete_address' style={{padding: '0 10px'}}></input> )}
                
                </div>
                </div>
                <div className="row g-3 align-items-center mb-2">
                    <div className="col-12 fw-bold">
                        
                        <label htmlFor="contNum" className="col-form-label">
                            Company Details</label>
                    </div>
                </div>
                <div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                    
                  <label htmlFor="branch" className="col-form-label">
                  <i className="fa-solid fa-home"></i> BJMP Branch Location:</label>
                </div>
                <div className="col-6">
                {editMode === false ? (
                  <label htmlFor="bjmp_branch" className="col-form-label">{user.bjmp_branch}</label>
                  ) : 
                  (<input onChange={handleChange} value={formData.bjmp_branch} id='bjmp_branch' name='bjmp_branch' style={{padding: '0 10px'}}></input> )}
                </div>
                </div>
          </div>)) : Array.isArray(userDetails) ? userDetails.map((user) => (
          <div className={dst['modal-body']} style={{padding: '0'}}>
           
              <div className="row d-flex justify-content-center" style={{display: 'flex'}}>
                <div className="col-12" >
                    <img className='mb-3' src={`../images/users/${tempImgSrc}`} alt="profileImage" style={{borderRadius: '250px'}} />
                    
                    <hr style={{margin: '10px 0 10px 0'}}></hr>
                    {editMode === false ? (
                    <>
                    <p style={{fontSize: '24px', fontWeight: 'bold', margin: '0'}}>{
                    (user.lastName).toUpperCase()
                    }, {user.firstName} {user.middleName}</p>
                    <p style={{color: '#666'}}> {user.position}</p>
                    </>) : 
                    (<>
                    <div className='row mb-3'>
                      <div className='col-4'>
                        <input onChange={handleChange} value={formData.lastName} 
                        id='lastName' name='lastName' style={{padding: '0 10px', fontSize: '20px'}}></input> 
                      </div>
                      <div className='col-4'>
                        <input onChange={handleChange} value={formData.firstName} 
                        id='firstName' name='firstName' style={{padding: '0 10px', fontSize: '20px'}}></input> 
                      </div>
                      <div className='col-4'>
                        <input onChange={handleChange} value={formData.middleName} 
                        id='middleName' name='middleName' style={{padding: '0 10px', fontSize: '20px'}}></input> 
                      </div>
                    </div>
                    </>
                    )}
                  
                    
                </div>
              </div>
              {editMode === false ? (<div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                  <label htmlFor="emailAdd" className="col-form-label">
                  <i className="fa-solid fa-envelope"></i> Email Address:</label>
                </div>
                <div className="col-6">
                  <label htmlFor="emailAdd" className="col-form-label">{user.emailAdd}</label>
                  </div>
                </div>) : (<></>)}
                {editMode === true ? (<div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                  <label htmlFor="emailAdd" className="col-form-label">
                  <i className="fa-solid fa-user"></i> Position:</label>
                </div>
                <div className="col-6">
                <input onChange={handleChange} value={formData.position} id='position' name='position' style={{padding: '0 10px'}}></input>
                  </div>
                </div>) : (<></>)}
              <div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                    
                  <label htmlFor="contact_number" className="col-form-label">
                  <i className="fa-solid fa-phone"></i> Contact Number:</label>
                </div>
                <div className="col-6">
                {editMode === false ? (
                  <label htmlFor="contact_number" className="col-form-label">{user.contact_number}</label>
                  ) : 
                  (<input onChange={handleChange} value={formData.contact_number} id='contact_number' name='contact_number' style={{padding: '0 10px'}}></input> )}
                </div>
                </div>
                
                <div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                    
                  <label htmlFor="loc" className="col-form-label">
                  <i className="fa-solid fa-map-marker"></i> Address:</label>
                </div>
                <div className="col-6">
                {editMode === false ? (
                  <label htmlFor="complete_address" className="col-form-label">{user.complete_address}</label>
                  ) : 
                  (<input onChange={handleChange} value={formData.complete_address} id='complete_address' name='complete_address' style={{padding: '0 10px'}}></input> )}
                
                </div>
                </div>
                <div className="row g-3 align-items-center mb-2">
                    <div className="col-12 fw-bold">
                        
                        <label htmlFor="contNum" className="col-form-label">
                            Company Details</label>
                    </div>
                </div>
                <div className="row g-3 align-items-center mb-2">
                <div className="col-6">
                    
                  <label htmlFor="branch" className="col-form-label">
                  <i className="fa-solid fa-home"></i> BJMP Branch Location:</label>
                </div>
                <div className="col-6">
                {editMode === false ? (
                  <label htmlFor="bjmp_branch" className="col-form-label">{user.bjmp_branch}</label>
                  ) : 
                  (<input onChange={handleChange} value={formData.bjmp_branch} id='bjmp_branch' name='bjmp_branch' style={{padding: '0 10px'}}></input> )}
                </div>
                </div>
          </div>)) : null}
          <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
          <div className="row p-1">
                  <div className='col-8'>

                  </div>
                  <div className='col-4 d-flex justify-content-end'>
                    {Array.isArray(loggedUserData) ? (<>{editMode === false ? (
                      <button type="button" className="btn btn-primary" style={{ backgroundColor: '#505050', border: 'none' }} onClick={enableEditMode}>
                        Edit
                      </button>
                    ) : (
                      <>
                        <button type="button" className="btn btn-primary" style={{ backgroundColor: '#505050', border: 'none' }} onClick={handleSubmit}>
                          Submit
                        </button>
                        <button type="button" className="btn btn-primary mx-1" style={{ backgroundColor: '#505050', border: 'none' }} onClick={disableEditMode}>
                          Close
                        </button>
                      </>
                    )}</>) : null}
                  </div>

              </div>
        </div>
        
      </div>
      {isSuccessModalOpen && <Success onClose={closeSuccessModal}/>}
    </div>
  );
}

export default UserProfile;
