import React, {useEffect, useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import domain from '../../constants/domain';
import axios from 'axios';

function EnterPDLID({setPdlData, pdlData,}){
  const [optionDisplay, setOptionDisplay] = useState([]);
  const [searchData, setSearchData] = useState({});
  const [errorMessage, setErrorMessage] = useState('');
  const [imageSrc, setImageSrc] = useState(localStorage.getItem('pdlPOSImageSrc') || '');
  
  
  useEffect (() => {
    axios.get(`${domain}/index.php?action=getTableData`)
    .then(response => {
      if (!response.data.fail) {
        // Update the state with the fetched PDL values
        setOptionDisplay(response.data);
        if(searchData){
          setSearchData(localStorage.getItem('posPDL'));
        }
        
      }
    })
    .catch(error => console.error('Error fetching PDL values:', error));
  
  }, [])
  useEffect(() => {
    localStorage.setItem('pdlData', JSON.stringify(pdlData));
  }, [pdlData]);

  useEffect(() => {
    localStorage.setItem('pdlPOSImageSrc', imageSrc);
  }, [imageSrc]);

  
  const onSearch = () => {
    if (!searchData) {
        setErrorMessage('Please fill in all required fields');
        return;
    }

    const { pdlNo } = searchData;

    if (!pdlNo || pdlNo === null) {
        setErrorMessage('Please fill in all required fields');
    } else {
        axios.get(`${domain}/index.php?action=readPDLAlt&entry=${pdlNo}`)
        .then(function(response){
            if(response.data.fail){
                setErrorMessage(response.data.message);
                setPdlData({});
                setImageSrc(null);
            }
            else{
                setErrorMessage('');
                setPdlData(response.data[0]); 
                const imageUrl = response.data[0].pdl_image;
                const fileName = imageUrl.split('/').pop();
                setImageSrc(fileName);
                
            }
            
        })
    }
}

const handleChange = (e) => {
  const name = e.target.name;
  const value = e.target.value;
  const action1 = 'readPDLAlt'
  setSearchData(values => ({...values, action1, [name]: value}));
  return;
}
    return(
        <div className={dst.con} style={{width: '100%', height: 'max-content', padding: '7px'}}>
            <div className="row g-3 align-items-center mb-2">
                <div className="row ms-2 mt-3" style={{display: 'flex'}}>
                <div className="col-7 d-flex flex-column align-items-center justify-content-center">
                  <div className="row" style={{ display: 'flex' }}>
                      <label htmlFor="pdlNo" className="form-label mt-2 text-start">
                        Enter PDL No.:
                      </label>
                      <select
                        name="pdlNo"
                        className="form-select"
                        style={{ width: '100%', boxShadow: 'none' }}
                        onChange={handleChange}
                        value={searchData && searchData.pdlNo ? searchData.pdlNo : ''}
                      >
                        <option value="" hidden disabled>
                          Select PDL
                        </option>
                        {Array.isArray(optionDisplay) ? (
                        optionDisplay.map((pdl, index) => (
                          <option key={index} value={pdl.id}>
                            {pdl.pdl_lastname}, {pdl.pdl_firstname} {pdl.pdl_middlename} - {pdl.id}
                          </option>
                        ))
                      ) : null}
                      </select>
                      <div className='row'>
                      <p className='mt-1 mb-2' style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
                        <div className='col-10'>

                        </div>
                        <div className='col-2'>
                        <button
                          type="button"
                          className="btn mt-2 ml-auto"
                          onClick={onSearch}
                          style={{
                            backgroundColor: '#505050',
                            color: '#fff',
                            width: 'fit-content', // Adjusted width to 100%
                          }}
                        >
                          Search
                        </button>
                        </div>
                        
                      </div>
                      
                  </div>
                </div>
                  <div className='col-5'>
                  <div className={`${dst['pdl-prof-pic']} mt-1`} style={{width: '100%', height: '225px'}}>
                    <div className={`d-flex flex-column align-items-center justify-content-between h-100`}>
                      {imageSrc && (
                        <img
                          src={`../images/pdls/${imageSrc}`}
                          className="mt-2 rounded-circle"
                          style={{ width: '95px', height: '95px', margin: '0', padding: '0' }}
                        />
                      )}
                      {!imageSrc && (
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          className="mt-3"
                          style={{ margin: '0', padding: '0' }}
                          width="93"
                          height="93"
                          viewBox="0 0 92 92"
                          fill="none"
                        >
                          <rect
                            x="0.5"
                            y="0.5"
                            width="92"
                            height="92"
                            rx="41"
                            fill="white"
                            stroke="#9D9D9D"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeDasharray="5 5"
                          />
                        </svg>
                      )}

                          <div className="text-center fs-6 mt-3">
                            {pdlData.pdl_lastname ? (
                              `${pdlData.pdl_lastname.toUpperCase()}, ${pdlData.pdl_firstname} ${pdlData.pdl_middlename}`
                            ) : (
                              'XXXXXXXXX'
                            )}
                          </div>
                          <div className="text-center" style={{ fontSize: 'small' }}>
                            {pdlData.id ? `PDL-${pdlData.id}` : 'PDL-XXXXXX'}
                          </div>
                          <hr style={{width: '90%', marginBottom: '0'}}></hr>
                          <div className="text-center fs-6 fw-semibold mb-3">
                            Balance: PHP {pdlData.pdl_lastname ? pdlData.formatted_balance : 'XX.XX'}
                          </div>
                        </div>
                      </div>
                    </div>
                </div>           
            </div>
            
        </div>
    );

}
export default EnterPDLID;