
import React, { useEffect, useState } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import { Link } from 'react-router-dom';
import CreateStock from '../modals/CreateStockEntry';
import EditStock from '../modals/EditStockEntry';
import ConfirmArchive from '../modals/ConfirmArchive';
import AuditDetails from '../modals/LogDetails';
import PageNavigator from '../navbars/PageNavigator';
import axios from 'axios';
import domain from '../../constants/domain';



function PDLList() {

  const [auditLogData, setAuditLogData] = useState([]);
  const [selectedLogId, setSelectLogId] = useState(null);
  const ROWS_PER_PAGE = 8;
  const [currentPage, setCurrentPage] = useState(1);
  const startIndex = (currentPage - 1) * ROWS_PER_PAGE;
  const endIndex = Math.min(startIndex + ROWS_PER_PAGE, auditLogData.length);
  const visibleRows = auditLogData && auditLogData.slice(startIndex, endIndex);
  useEffect(() => {
    axios.get(`${domain}/index.php?action=displayAudit`)
    .then((response) => {
      if(response.data){
        setAuditLogData(response.data);
      }
      else{
        setAuditLogData({});
      }
    }).catch(error => console.error('Error fetching audit log data: ', error));

  }, [])

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
  };
  const [selectedOption, setSelectedOption] = useState('');
  const [isDetailModalOpen, setDetailModalOpen] = useState(false);
  const openDetailModal = (primId, e) => {
    setDetailModalOpen(true);
    setSelectLogId(primId);
    const newUrl = `?logId=${primId}`
    window.history.pushState({}, null, newUrl);
  };
  const closeDetailModal = () => {
    setDetailModalOpen(false);
    const newUrl = `?`
    window.history.pushState({}, null, newUrl);
  };

  const generateReport = () => {
    if(auditLogData){
      const action = "generateAuditLog";
      const actionData = { action };
      axios.post(domain, actionData)
          .then(response => {
              // Handle successful response, if needed
               // Response.data will contain the file URL
               const fileUrl = response.data.fileUrl;
              const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/reports/'));
              const newWindow = window.open(formattedFileUrl, '_blank');

              // Ensure the new window is fully loaded before attempting to print
              newWindow.onload = () => {
                  newWindow.print();
              };
          })
          .catch(error => {
              // Handle error, if needed
              console.error('Error generating report:', error);
          });
    }
}
  return (
    <div className="container-fluid p-5">
      <div className={dst.con} style={{top: '20px'}}>
        
        <div className={`${dst.row} mt-1`}>
        <div style={{display: 'flex', padding: '10px'}}>
        <div className='col-3'>
          <p className={`fw-bold ms-3 textColor`} style={{textAlign: 'left', marginTop: '15px'}}>Audit Log</p>
          </div>
          <div className='col-9 d-flex justify-content-end'>
          <button className={`${dst.add} me-2`} style={{left: '0', width: 'fit-content', padding: '0 10px', backgroundColor: '#222'}}type='button' onClick={generateReport}>Generate Report
          </button>
          </div>
        </div>
        <div className='row'>
          <div className='col-12' style={{ width: '100%', padding: 0 }}>
          <table
            className={dst.tableZebra}
            style={{
              border: '#F0F1F3',
              color: '#48505E',
              height: 'auto',
              fontSize: 'small',
              padding: '0',
              textAlign: 'left',
              width: '93.5%', // Set the table width to 100%
              margin: '0 auto',
            }}
          >
            <thead>
              <tr>
                <th scope="col" >
                  Date and Time
                </th>
                <th scope="col" >
                  Email
                </th>
                <th scope="col" >
                  Transaction Type
                </th>
                <th scope="col" >
                  Transaction Details
                </th>
                <th scope="col" >
                  Action
                </th>
              </tr>
            </thead>
            <tbody className={dst.shrinkMedia}>
            {visibleRows.map((audit, index) => (
              <tr key={index}>
                    <td>{audit.date_time}</td>
                    <td>{audit.email}</td>
                    <td>{audit.transaction_type}</td>
                    <td>{audit.description}</td>
                  <td>
                    <div className={`${dst.actionButtons}`}>
                      <p onClick={(e) => openDetailModal(audit.transaction_id, e)}><i className="fa-solid fa-eye fa-sm"></i></p>
                    </div>
                  </td>
              </tr>
              ))}
              
              {visibleRows.length === 0 && (
              <tr>
                <td style={{padding: '150px', textAlign: 'center'}} colSpan={6}>Nothing to show</td>
              </tr>
              )}
            </tbody>
          </table>
          </div>

        </div>
        
          <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', marginTop: '5%'}}>
            <PageNavigator totalPages={Math.ceil(auditLogData.length / ROWS_PER_PAGE)} currentPage={currentPage} onPageChange={handlePageChange}/>
          
          </div>
        </div>
        
      </div>
      {isDetailModalOpen && <AuditDetails onClose={closeDetailModal} logId={selectedLogId}/>}
    </div>
    
  );
}

export default PDLList;
