import React from 'react';
import lrst from '../../styles/LoginRegStyle.module.css'; // Import your CSS module

function ProgressBar({ currentStep, totalSteps }) {
  // Calculate the percentage progress based on the current step and total steps
  const progressPercentage = (currentStep / totalSteps) * 100;

  return (
    <div className={lrst.progressBarContainer}>
      <div className={lrst.progressBar}>
        <div
          className={lrst.progressBarFill}
          style={{ width: `${progressPercentage}%` }}
        />
      </div>
    </div>
  );
}

export default ProgressBar;
