import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import axios from 'axios';
import domain from '../../constants/domain';

function ArchiveAccount({ successfulArchive, onClose, primaryPDLID, primaryStockID, primaryUserEmail, primaryUserID, primaryLenderID, archiveType }) {
  const [pdlData, setPdlData] = useState([]);
  const [stockData, setStockData] = useState([]);
  const [userData, setUserData] = useState([]);
  const [userID, setUserID] = useState('');
  const [lenderData, setLenderData] = useState([]);
  const [relatedPDLID, setRelatedPDLData] = useState('');
    const handleBackdropClick = (e) => {
      
        // Check if the click target is the backdrop element
        if (e.target.id === 'logoutModal') {
          onClose(); // Close the modal
        }
      };
    useEffect(() => {
        if(primaryPDLID){
          getPDL();
        }
        else if(primaryStockID){
          getStock();
        }
        else if(primaryUserEmail){
          getUser();
        }
        else if(primaryLenderID){
          getLender();
        }
         
      }, []);
  const archiveData = 1;
     function getPDL(){
        axios.get(`${domain}/index.php?action=readPDL&pdlNo=${primaryPDLID}`)
        .then(function(response) {
          setPdlData(response.data[0]);
        })
        .catch(error => console.error('Error fetching PDL data:', error));
     } 
     function getStock(){
      axios.get(`${domain}/index.php?action=displaySpecificStock&itemId=${primaryStockID}`)
      .then(function(response) {
        setStockData(response.data[0]);
      }).catch(error => console.error('Error fetching stock data: ', error));
      }
      function getUser(){
        axios.get(`${domain}/index.php?action=getLoggedUserData&email=${primaryUserEmail}`)
        .then(function(response) {
          setUserData(response.data[0]);
          setUserID(response.data[0].userid);
        }).catch(error => console.error('Error fetching user data: ', error));
        }
      function getLender(){
        axios.get(`${domain}/index.php?action=getSpecificLenderDataAlt&lenID=${primaryLenderID}`)
        .then(function(response) {
          setLenderData(response.data[0]);
          setRelatedPDLData(response.data[0].related_pdl_id);
          
        }).catch(error => console.error('Error fetching lender data: ', error));
        }
      const handleArchive = async (e) =>{
        if(archiveType === 'PDL'){
          axios.post(`${domain}/index.php?action=archivePDL&pdlNo=${primaryPDLID}`, { 
            action: 'archivePDL',
            pdlNo: primaryPDLID,
            email: localStorage.getItem("email"),
            archiveData })
          .then(response => {
            window.location.reload();
          })
          .catch(error => console.error('Error archiving PDL:', error));
          }
          else if (archiveType === 'Stock'){
            axios.post(`${domain}/index.php?action=archiveStock&itemId=${primaryStockID}`, { 
              action: 'archiveStock',
              itemId: primaryStockID,
              archiveData })
            .then(response => {      window.location.reload();
            })
            .catch(error => console.error('Error archiving stock:', error));
          }
          else if (archiveType === 'User'){
            axios.post(`${domain}/index.php?action=archiveUser&userID=${primaryUserID ? primaryUserID : userID}`, { 
              action: 'archiveUser',
              userEmail: primaryUserEmail,
              userID: primaryUserID ? primaryUserID : userID,
              archiveData })
            .then(response => {
              if(successfulArchive){
                onClose();
                successfulArchive('ArchiveAccount');
              }
              else{
                window.location.reload();
              }
            })
            .catch(error => console.error('Error archiving user:', error));
          }
          else if (archiveType === 'Lender'){
            axios.post(`${domain}/index.php?action=archiveLender&lenderID=${primaryLenderID}`,
            {
              action: 'archiveLender',
              lenderID: primaryLenderID,
              archiveData
            }).then(response => {
              onClose();
            })
          }
        }
  return (
    <div className={dst.modal}  onClick={handleBackdropClick} id="logoutModal" tabIndex="-1">
      <div>
        <div className={dst['modal-content']} style={{width: '500px', marginTop: '15%'}}>
          <div className={`text-center ${dst['modal-body']}`}>
            <i className={`fa-regular fa-circle-xmark fa-2xl mb-4`} style={{fontSize: '40px'}}></i>
            <h3>Confirm Archiving?</h3>
            <p style={{ color: '#a1a1a1' }}>Are you sure you want to archive?</p>

            <div className={`modal-footer d-flex justify-content-center ${dst['modal-footer']} ${dst['d-flex']} ${dst['justify-content-center']}`} style={{ border: 'none' }}>
            <button
                type="button"
                className="btn link modal-close"
                style={{ border: '1px solid #505050', width: '40%' , marginRight: '15px'}}
                onClick={onClose}
              >
                No, Cancel
              </button>
              <button
                type="submit"
                className="btn"
                style={{ backgroundColor: '#505050', color: '#fff', width: '40%', display: 'block' }}
                onClick={handleArchive}
              >
                OK
              </button>

            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ArchiveAccount;
