
import React, { useState } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import Success from '../modals/SuccessfulAction';

function ItemList({selectSearchedStockID, addQuantity, itemList}) {
  const [errorMessage, setErrorMessage] = useState('');
  const [quantity, setQuantity] = useState(0);
  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);

  const incrementQuantity = () => {
    if (quantity < selectSearchedStockID[0].remainingStock) {
      setQuantity(prevQuantity => prevQuantity + 1);
    }
  };

  const decrementQuantity = () => {
    if (quantity > 0) {
      setQuantity(prevQuantity => prevQuantity - 1);
    }
  };
  const openSuccessModal = () => {
    if (!quantity) {
      setErrorMessage('Please enter something!');
      return;
    }
    const itemIdExists = itemList.some(item => item.item_id === selectSearchedStockID[0]?.id);
    if (itemIdExists) {
      setErrorMessage('Item with the same ID already exists.');
      return;
    }
    
      setErrorMessage('');
      addQuantity(parseInt(quantity));
      setSuccessModalOpen(true);
    
  };
    const closeSuccessModal = () => {
      setSuccessModalOpen(false);
      window.location.reload();
    };

  return (
      <div className={dst.con} style={{width: '100%', minHeight: '55vh'}}>
        
        <div className={`row mt-1 p-4`}>
        <div style={{display: 'flex', padding: '3px'}}>
        </div>
        <div className="row ms-1 mt-3" style={{display: 'flex'}}>
          <div className={dst['pos-table']}>
                            <table class="table" style={{width: '100%', textAlign: 'left'}}>
                               {selectSearchedStockID && selectSearchedStockID.map((item, index) => (
                                <tbody>
                                    <tr>
                                    <th>Item ID: </th>
                                        <td>{item.id ? (<>I-{item.id}</>) : 
                                          (<></>)}
                                         </td>
                                    </tr>
                                    <tr>
                                        <th>Product Type: </th>
                                        <td>{item.prodType}</td>
                                    </tr>
                                    <tr>
                                        <th>Product Name: </th>
                                        <td>{item.prodName}</td>
                                    </tr>
                                    <tr>
                                        <th>Price: </th>
                                        <td>{item.formatted_price && (<>PHP{parseFloat(item.formatted_price).toFixed(2)}</>)}</td>
                                      </tr>
                                    <tr>
                                        <th>Category: </th>
                                        <td>{item.category}</td>
                                      </tr>
                                    <tr>
                                      <th>Remaining Stocks: </th>
                                        <td>{item.remainingStock}</td>
                                    </tr>
                                    <tr>
                                      <th>Quantity: </th>
                                        <td>{item.remainingStock && item.remainingStock.value !== 0 ? (<div style={{ display: 'flex', alignItems: 'center' }}>
                                          <button onClick={decrementQuantity} style={{background: 'none', border: '1px solid #ccc', padding: '4px'}}>-</button>
                                          <input
                                            type="number"
                                            name="quantity"
                                            id="quantity"
                                            style={{ width: '60px', height: '35px' }}
                                            value={quantity}
                                            min="0"
                                          />
                                          <button onClick={incrementQuantity} style={{background: 'none', border: '1px solid #ccc', padding: '4px'}}>+</button>
                                        </div>) :
                                        (<p className='mt-1 mb-2' style={{color: 'red', fontSize: '16px', margin: '0'}}>No stocks available!</p>)}</td>
                                    </tr>
                                </tbody>
                                ))}
                                {selectSearchedStockID.length === 0 && (
                                  <tbody>
                                    <tr>
                                      <td colSpan={6} style={{padding: '120px 0'}}>Nothing to show yet</td>
                                    </tr>
                                    </tbody>
                                  )}
                            </table>
                    </div>
                  
                </div>
                <p className='mt-1 mb-2' style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
            <div className='row align-items-center' style={{ alignSelf: 'flex-end' }}>
            <div className='col-10'></div>
            <div className='col-2'>
              <button onClick={openSuccessModal} className={`${dst.add} mt-3 mb-3`} style={{top: '0', left: '0'}}>Enter</button>
            </div>
            
          </div>
        </div>
        {isSuccessModalOpen && <Success onClose={closeSuccessModal}/>}
      </div>
      
    
  );
}

export default ItemList;
