import React from 'react';
import dst from '../../styles/DashboardStyle.module.css';

function PriceDisplay({recentItem, priceVal}){
  const latestItem = recentItem && recentItem.length > 0 ? recentItem[recentItem.length - 1] : null;

  return(
        <div className={`${dst.conGray} mt-0`} style={{width: '100%', height: 'fit-content', color: 'white', padding: '50px'}}>
            <div className="row g-3 align-items-center mt-4">
              <div className='col-3 text-start'>
                <h2>Total:</h2>
              </div>
              <div className='col-9 text-end '>
                <h1 className='fw-bold'>PHP{priceVal.toFixed(2)}</h1>
              </div>
            </div>
            <hr></hr>
            <div className="row g-3 align-items-center">
              <div className='col-3 text-start'>
                <h5>{latestItem && latestItem.prodType}</h5>
              </div>
              <div className='col-9 text-end '>
              <h5 className='fw-bold'>
                {latestItem && latestItem.price &&(<>PHP
                {(parseFloat(latestItem.price) * (parseFloat(latestItem.quantity))).toFixed(2)}
                </>)}
                </h5>
              </div>
            </div>
        </div>
    );

}
export default PriceDisplay;