import React, {useEffect, useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import bjmplogo from '../../assets/bjmp-logo.png';
import LoadDetails from '../modals/LoadDetails';
import axios from 'axios';
import domain from '../../constants/domain';

function LoadPDL(){

    const [pageNumber, setPageNumber] = useState(1);
    const [optionDisplay, setOptionDisplay] = useState([]);
    const [lenderDisplay, setLenderDisplay] = useState([]);
    const [wardenDisplay, setWardenDisplay] = useState([]);
    const [searchData, setSearchData] = useState([]);
    const [errorMessage, setErrorMessage] = useState('');
    const [imageSrc, setImageSrc] = useState(null);
    const [pdlData, setPdlData] = useState({});
    const [addValue, setAddValue] = useState(0);
    const [updatedBalance, setUpdatedBalance] = useState(0);
    const [selectLoadType, setSelectLoadType] = useState('');
    const [loadData, setLoadData] = useState({});
    const [textBoxActive, setTextBoxActive] = useState(0);
    const [spendingLimit, setSpendingLimit] = useState(0.00);

    useEffect(() => {
        // Fetch PDL values from your API or database
        axios.get(`${domain}/index.php?action=getTableData`)
          .then(response => {
            if (!response.data.fail) {
              // Update the state with the fetched PDL values
              setOptionDisplay(response.data);
            }
          })
          .catch(error => console.error('Error fetching PDL values:', error));
        if (selectLoadType === 'visitor' && searchData.pdlNo) {
            axios.get(`${domain}/index.php?action=getLenderDataApproved&id=${searchData.pdlNo}`)
                .then(response => {
                    if (!response.data.fail) {
                        // Update the state with the fetched PDL values
                        setLenderDisplay(response.data);
                    }
                })
                .catch(error => console.error('Error fetching PDL values:', error));
        }
        else{
            axios.get(`${domain}/index.php?action=getWarden`)
                .then(response => {
                    if (!response.data.fail) {
                        // Update the state with the fetched PDL values
                        setWardenDisplay(response.data);
                    }
                })
                .catch(error => console.error('Error fetching PDL values:', error));
        }   
        axios.get(`${domain}/index.php?action=getSpendingLimit`).then(
            response => {
                setSpendingLimit(parseFloat(response.data[0].spending_limit)
              )
            }
          )
    }, [searchData.pdlNo, selectLoadType]);
      
    const handleNextClick = () => {
        const {loadType} = loadData;
        if(pdlData && imageSrc && loadType){
            localStorage.setItem('searchValue', searchData.pdlNo);
            localStorage.setItem('loadType', loadData.loadType);
            setPageNumber(2);
            setErrorMessage('');
        }
        else{
            setErrorMessage('Please fill in all required fields');
        }

    }
    const handlePrevClick = () => {
        setPageNumber(1);
        setErrorMessage('');   
        setSearchData({'pdlNo': localStorage.getItem('searchValue')})
        setLoadData({'loadType': localStorage.getItem('loadType')})
    }
    
    const [isLoadModalOpen, setLoadModalOpen] = useState(false);

    const openLoadModal = () => {
        const {balanceAdd, lenderName} = loadData;
        if(balanceAdd && lenderName){
            setErrorMessage(''); 
            setLoadModalOpen(true);
        }
        else{
            setErrorMessage('Please fill up all fields'); 
        }
        
    }
    const closeLoadModal = () => {
        setLoadModalOpen(false);
    }
    const onSearch = () => {
        const {pdlNo} = searchData;
        if(!pdlNo){
            setErrorMessage('Please fill in all required fields');
        }
        else{
            axios.get(`${domain}/index.php?action=readPDLAlt&entry=${pdlNo}`)
            .then(function(response){
                if(response.data.fail){
                    setErrorMessage(response.data.message);
                    setPdlData({});
                    setImageSrc(null);
                }
                else{
                    setErrorMessage('');
                    setPdlData(response.data[0]); 
                    const imageUrl = response.data[0].pdl_image;
                    const fileName = imageUrl.split('/').pop();
                    setImageSrc(fileName);
                }
                
            })
        }
    }
    const handleChange = (e) => {
        const name = e.target.name;
        const value = e.target.value;
        const action1 = 'readPDLAlt'
        const action2 = 'loadPDL'
        setSearchData(values => ({...values, action1, [name]: value}));
        setLoadData(values => ({...values, action2, [name]: value}));
        if (name === 'balanceAdd') {
            const sanitizedValue = value.trim() === '' ? '0' : value;
            const newValue = parseFloat(sanitizedValue) + parseFloat(pdlData.formatted_balance);
            if (value < 0){
                setErrorMessage('Invalid amount!');
            }
            else if(newValue < spendingLimit){
                setErrorMessage('');
                setUpdatedBalance(newValue);
                setAddValue(value); // Update addValue as well
            }
            
            else{
                setErrorMessage(`Amount cannot exceed PHP${spendingLimit}!`);
            }
            return;
        }
        if (name === 'loadType'){
            setSelectLoadType(value);
            if(value === 'livelihood'){
                setTextBoxActive(1);
            }
            else{
                setTextBoxActive(2);
            }
        
            setLoadData(values => ({...values, action2, loadType: value}))
            return;
        }
        if (name === 'lenderName') {
            setLoadData(values => ({...values, action2, lenderName: value}));
        }
        return;
    }

    return(
        <div style={{margin: '30px'}}>
            {pageNumber === 1 && (<div className={dst.con} >
            <div className={`${dst.row} mt-1 `}>
            <div style={{display: 'flex', padding: '10px'}}>
            <h5 className={`fw-bold ms-3 textColor`} style={{textAlign: 'left', marginTop: '15px'}}>
                Load PDL  
            </h5>
            </div>
            <div className="row" style={{marginTop: '20px'}}>
                <div className="row" style={{display: 'flex'}}>
                    <div className='col-2'>
                        <label for="pdlNo" className="form-label mt-2">Enter PDL No.:</label>
                    </div>
                    <div className='col-8'>
                    <select
                    name="pdlNo"
                    className="form-select"
                    style={{ width: '100%', boxShadow: 'none' }}
                    onChange={handleChange}
                    value={searchData.pdlNo || ''}
                    >
                    <option value="" hidden disabled>Select PDL</option>   
                    {optionDisplay && optionDisplay.map((pdl, index) => (
                        <option key={index} value={pdl.id}>
                        {pdl.pdl_lastname}, {pdl.pdl_firstname} {pdl.pdl_middlename} - {pdl.id} 
                        </option>
                    ))}
                    </select>
                    </div>
                    <div className='col-2'>
                    <button
                            type="button"
                            className="btn"
                            onClick={onSearch}
                            style={{
                            backgroundColor: '#505050',
                            color: '#fff',
                            width: 'stretch'
                            }}
                        >
                            Search
                        </button>
                    </div>
                </div>
                <div className="row" style={{display: 'flex'}}>
                    <div className='col-2'>
                        <label for="pdlNo" className="form-label mt-2" style={{fontWeight: 'bold'}}>Loading Type:</label>
                    </div>
                    <div className='col-3'>
                        <select name="loadType" value={loadData.loadType} class="form-select mt-2" style={{width: '150px', boxShadow: 'none'}} onChange={handleChange}>
                        <option value=""selected disabled hidden>Loading Type</option>
                        <option value="livelihood">Livelihood</option>
                        <option value="visitor">Visitor</option>
                        </select>
                    </div>
                </div>
                <div className="row ms-4 mt-3" style={{display: 'flex'}}>
                    <div className={dst['pdl-profile']} >
                            <table class="table" style={{width: '100%', textAlign: 'left'}}>
                                <tbody>
                                    <tr>
                                        <th scope="row">Name </th>
                                        <td>{pdlData ? (
                                                <>
                                                    {pdlData.pdl_lastname && pdlData.pdl_lastname.toUpperCase()},{' '}
                                                    {pdlData.pdl_firstname} {pdlData.pdl_middlename}
                                                </>
                                                ) : (
                                                ''
                                                )}
                                                </td>
                                                </tr>
                                    <tr>
                                        <th scope="row">PDL No.</th>
                                        <td>{pdlData.id != null && (<>
                                        PDL-
                                        </>)}
                                        {pdlData.id}
                                        </td>
                                    </tr>
                                    <tr>
                                        <th scope="row">Age</th>
                                        <td>{pdlData.age}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">Gender</th>
                                        <td>{pdlData.gender}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">Date of Birth</th>
                                        <td>{pdlData.birthday}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">Offense</th>
                                        <td>{pdlData.offense}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">Sentence</th>
                                        <td>{pdlData.sentence}</td>
                                    </tr>
                                    <tr>
                                        <th scope="row">Estimated Release Date</th>
                                        <td>{pdlData.reldate}</td>
                                    </tr>

                                </tbody>
                            </table>
                    </div>
                    <div className={`${dst['pdl-prof-pic']} ms-2 mt-1`}>
                        
                        {imageSrc && (<img src={`../images/pdls/${imageSrc}`} className="p-4"
                        style={{width: '200px', height: '200px', display: 'block', marginLeft: 'auto', marginRight: 'auto', borderRadius: '250px'}}>
                                </img>)}
                        {!imageSrc && ( 
                            <svg xmlns="http://www.w3.org/2000/svg" className='mt-3' style={{margin: '0', padding: '0'}} width="163" height="163" viewBox={`"0 0 162 162"`} fill="none">
                                <rect x="0.5" y="0.5" width="162" height="162" rx="41" fill="white" stroke="#9D9D9D"
                                strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                            </svg>

                        )}
                        <p className="text-center fw-bold fs-3" style={{margin: '0'}}>
                                        {pdlData.pdl_lastname != null && (<>
                                        {pdlData.pdl_lastname.toUpperCase()}, {pdlData.pdl_firstname} {pdlData.pdl_middlename}
                                        </>)}
                                        {pdlData.pdl_lastname == null && (<>
                                        XXXXXXXXXX
                                        </>)}</p>
                        <p className="text-center" style={{fontSize: 'small'}}>
                                        {pdlData.id != null && (<>
                                        PDL-{pdlData.id}
                                        </>)}
                                        {pdlData.id == null && (<>
                                        PDL-XXXXXX
                                        </>)}
                                        </p>
                        <hr></hr>
                        <p className="position-absolute bottom-0 start-50 translate-middle-x fw-semibold">Balance: PHP 
                                        {pdlData.pdl_lastname != null && (<>
                                        {pdlData.formatted_balance}
                                        </>)}
                                        {pdlData.pdl_lastname == null && (<>
                                        XX.XX
                                        </>)}</p>
                    </div>
                </div>
                <p className='mt-4' style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
                <div className='row mt-4 ms-3 mb-3 '>
                    <div className ='col-10'>

                    </div>
                    <div className ='col-2'>
                    <button
                    type="button"
                    className="btn"
                    onClick={handleNextClick}
                    style={{
                    backgroundColor: '#505050',
                    color: '#fff',
                    width: 'stretch'
                    }}
                >
                    Next
                </button>
                    </div>
                    
                </div>
            </div>
            </div>
            
        </div>)}

        {pageNumber === 2 && (<div className={dst.con}>
            <div className={`${dst.row} mt-1`}>
            <div style={{display: 'flex', padding: '10px'}}>
            <h5 className={`fw-bold ms-3 textColor`} style={{textAlign: 'left', marginTop: '15px'}}>
                Load PDL  
            </h5>
            </div>
            <div className="row p-4" style={{marginTop: '20px'}}>
                <div className="row" style={{display: 'flex'}}>
                    <div className='col-2'>
                        <label for="balanceAdd" className="form-label mt-2 text-start">Enter amount to load:</label>
                    </div>
                    <div className='col-10'>
                    <input type="number" className="form-control" name="balanceAdd" placeholder="Enter amount" id="balanceAdd"
                         value={addValue} style={{width: '100%', boxShadow: 'none'}} onChange={handleChange}></input>
                    </div>
                </div>
                {textBoxActive === 1 && (<div className="row mt-3 text-start" style={{display: 'flex'}}>
                    <div className='col-2'>
                        <label for="lenderName" className="form-label mt-2">Select name of warden:</label>
                    </div>
                    <div className='col-10'>
                    <select
                    name="lenderName"
                    className="form-select"
                    style={{ width: '100%', boxShadow: 'none' }}
                    onChange={handleChange}
                    value={loadData.lenderName || ''}
                    >
                    
                    {wardenDisplay && wardenDisplay.map((len, index) => (
                        <>
                            <option value="" hidden disabled >Select Warden</option> 
                            <option key={index} value={`${(len.lastName).toUpperCase()}, ${len.firstName} ${len.middleName}`}>
                            {len.lastName}, {len.firstName} {len.middleName}
                            </option>
                         </>    
                    ))}
                    {Array.isArray(wardenDisplay)  && wardenDisplay.length === 0 && (
                        <option value="">
                            No valid warden available
                        </option>
                    )}
                    </select>
                    </div>
                </div>)}
                {textBoxActive === 2 && (<div className="row mt-3 text-start" style={{display: 'flex'}}>
                    <div className='col-2'>
                        <label for="lenderName" className="form-label mt-2">Select name of valid lenders:</label>
                    </div>
                    <div className='col-10'>
                    <select
                    name="lenderName"
                    className="form-select"
                    style={{ width: '100%', boxShadow: 'none' }}
                    onChange={handleChange}
                    value={loadData.lenderName || ''}
                    >
                    
                    {lenderDisplay && lenderDisplay.map((len, index) => (
                        <>
                            <option value="" hidden disabled >Select Lender</option> 
                            <option key={index} value={`${(len.lender_lastname).toUpperCase()}, ${len.lender_firstname} ${len.lender_middlename}`}>
                            {len.lender_lastname}, {len.lender_firstname} {len.lender_middlename} - {len.relationship}
                            </option>
                         </>    
                    ))}
                    {lenderDisplay.length === 0 && (
                        <option value="">
                            No valid lender available
                        </option>
                    )}
                    </select>
                    </div>
                </div>)}
                <div className="row ms-3 mt-3" style={{display: 'flex'}}>
                    <div className='col-5'>
                        <div className={dst['old-balance']}>
                        <p class="fw-semibold">Old Balance</p>
                            <div class={`${dst.details} p-3`}>
                            {imageSrc && (<img src={`../images/pdls/${imageSrc}`} style={{width: '150px', height: '150px',borderRadius: '100%'}}>
                                </img>)}
                            {!imageSrc && ( 
                                <svg xmlns="http://www.w3.org/2000/svg" className='mt-3' style={{margin: '0', padding: '0'}} width="163" height="163" viewBox={`"0 0 162 162"`} fill="none">
                                    <rect x="0.5" y="0.5" width="162" height="162" rx="41" fill="white" stroke="#9D9D9D"
                                    strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                                </svg>
                            )}
                            <p className="text-center fw-bold fs-3" style={{margin: '0'}}>
                                        {pdlData.pdl_lastname != null && (<>
                                        {pdlData.pdl_lastname.toUpperCase()}, {pdlData.pdl_firstname} {pdlData.pdl_middlename}
                                        </>)}
                                        {pdlData.pdl_lastname == null && (<>
                                        XXXXXXXXXX
                                        </>)}</p>
                             <p className="text-center" style={{fontSize: 'small'}}>
                                        {pdlData.id != null && (<>
                                        PDL-{pdlData.id}
                                        </>)}
                                        {pdlData.id == null && (<>
                                        PDL-XXXXXX
                                        </>)}
                                        </p>
                                <hr></hr>
                                <p className="fw-semibold">Balance: PHP 
                                        {pdlData.pdl_lastname != null && (<>
                                        {pdlData.formatted_balance}
                                        </>)}
                                        {pdlData.pdl_lastname == null && (<>
                                        XX.XX
                                        </>)}</p>
                            </div>
                        </div>
                    </div>
                    <div className='col-1'>
                        <i className='fas fa-arrow-right' style={{fontSize: '36px', marginTop: '160px'}}></i>
                    </div>
                    <div className='col-5'>
                        <div className={dst['update-balance']}>
                        <p class="fw-semibold">Updated Balance</p>
                            <div class={`${dst.details} p-3`}>
                            {imageSrc && (<img src={`../images/pdls/${imageSrc}`} style={{width: '150px', height: '150px',borderRadius: '100%'}}>
                                </img>)}
                            {!imageSrc && ( 
                                <svg xmlns="http://www.w3.org/2000/svg" className='mt-3' style={{margin: '0', padding: '0'}} width="163" height="163" viewBox={`"0 0 162 162"`} fill="none">
                                    <rect x="0.5" y="0.5" width="162" height="162" rx="41" fill="white" stroke="#9D9D9D"
                                    strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                                </svg>
                            )}
                            <p className="text-center fw-bold fs-3" style={{margin: '0'}}>
                                        {pdlData.pdl_lastname != null && (<>
                                        {pdlData.pdl_lastname.toUpperCase()}, {pdlData.pdl_firstname} {pdlData.pdl_middlename}
                                        </>)}
                                        {pdlData.pdl_lastname == null && (<>
                                        XXXXXXXXXX
                                        </>)}</p>
                             <p className="text-center" style={{fontSize: 'small'}}>
                                        {pdlData.id != null && (<>
                                        PDL-{pdlData.id}
                                        </>)}
                                        {pdlData.id == null && (<>
                                        PDL-XXXXXX
                                        </>)}
                                        </p>
                                <hr></hr>
                                <p className="fw-semibold">Balance: PHP 
                                        {pdlData.pdl_lastname != null && (<>
                                        {(parseFloat(updatedBalance)).toFixed(2)}
                                        </>)}
                                        {pdlData.pdl_lastname == null && (<>
                                        XX.XX
                                        </>)}</p>
                            </div>
                        </div>
                    </div>
                    <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
                </div>
                <div className='row mt-4 mb-3'>
                <div className ='col-2'>
                    <button
                    type="button"
                    className="btn"
                    onClick={handlePrevClick}
                    style={{
                    backgroundColor: '#505050',
                    color: '#fff',
                    width: 'stretch'
                    }}
                >
                    Back
                    </button>
                    </div>
                    <div className ='col-8'>

                    </div>
                    <div className ='col-2'>
                    <button
                    type="button"
                    className="btn"
                    onClick={openLoadModal}
                    style={{
                    backgroundColor: '#505050',
                    color: '#fff',
                    width: 'stretch'
                    }}
                >
                    Load Now!
                </button>
                    </div>
                    
                </div>
            </div>
            </div>
            
        </div>)}
        {isLoadModalOpen && <LoadDetails onClose={closeLoadModal} loadDetails={[loadData]} pdlDetails={[pdlData]}/>}
        </div>
    );
}

export default LoadPDL;
