import React, { useState, useEffect } from 'react';
import bjmplogo from '../../assets/bjmp-logo.png';
import lrst from '../../styles/LoginRegStyle.module.css';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import axios from 'axios';
import domain from '../../constants/domain';

function ForgotPassword() {

  const [passwordData, setPasswordData] = useState({});
  const {password, confirmPass} = passwordData;
  const [errorMessage, setErrorMessage] = useState("");
  const [imageSrc, setImageSrc] = useState(null);
  const handleChange = (e) => {
    const urlParams = new URLSearchParams(window.location.search);
    const resetToken = urlParams.get('token');
          
    if (!resetToken) {
        setErrorMessage('Reset token not found in the URL.');
            return;
    }
    else{
      const target = e.target;
      const name = target.name;
      const value = target.value;
      const action = 'resetPassword';
      setPasswordData(values => ({resetToken, action, ...values, [name]: value}));
    }
   
  }
    const handleSubmit = async (e) => {
      e.preventDefault();

      if(!password || !confirmPass){
        setErrorMessage('Field is empty.');
      }
      else if (password.length < 6) {
        setErrorMessage('Password must be longer than 6 characters');
        return; // Do not proceed to the next step
      }
      if (password != confirmPass) {
        setErrorMessage('Passwords do not match.');
        return;
      }
      else{
        try{
          const response = await axios.post(domain, passwordData)
          if (response.data.success) {
            setErrorMessage(response.data.message);
            window.location.href = '/';  // Display success message
        } else {
            setErrorMessage(response.data.message);  // Display failure message
        }
        }
        catch (error){
          console.error('Error:', error);
        }
      }
    }

    useEffect(() => {
      axios.get(`${domain}/index.php?action=getLogo`).then(
        response => {
          const imageUrl = response.data[0].logo_path;
          const fileName = imageUrl ? imageUrl.split('/').pop() : null;
          setImageSrc(fileName);
        }
      )
  
     }, [])
  return (
    <div className={`${lrst.container} mt-4`}>
      <Helmet>
        <title>BJMP | Forgot Password</title>
      </Helmet>
      <header className='text-center'>
        <img className="img" src={`../logo/${imageSrc}`} alt="BJMP Logo" />
        <h6 className={lrst.thb}>BUREAU OF JAIL MANAGEMENT AND PENOLOGY, REGION III</h6>
        <p className={lrst.lc}>“Changing Lives, Building a Safer Nation”</p>
      </header>
      <form className="d-flex justify-content-center" onSubmit={handleSubmit}>
        <div className="inputDetails" style={{ width: '400px' }}>
          <label htmlFor="password" className="form-label" style={{ textAlign: 'left' }}>
                New Password<span className={lrst.required}>*</span>
              </label>
              <input
                type="password"
                className="form-control mb-2"
                id="password" name="password"
                placeholder="Enter your password"
                onChange={handleChange}
              />

              <label htmlFor="confirmPass" className="form-label" style={{ textAlign: 'left' }}>
                Confirm Password<span className={lrst.required}>*</span>
              </label>
              <input
                type="password"
                className="form-control mb-2"
                id="confirmPass" name="confirmPass"
                placeholder="Confirm your password"
                onChange={handleChange}
              />
          <hr></hr>
            <button type="submit" className="nextBtn btn-secondary mt-4">
              Send Request
            </button>
            <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
        </div>
      </form>
    </div>
  );
}

export default ForgotPassword;
