import React, {useEffect, useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import RecentTransactions from '../modals/RecentTransactions';
import Lenders from '../modals/ListOfLenders';
import domain from '../../constants/domain';
import axios from 'axios';
function PDLOvDet({userData}) {
  
  const [isRecentTransactionsModalOpen, setRecentTransactionsModalOpen] = useState(false);
  const [isLendersModalOpen, setLendersModalOpen] = useState(false);
  const [recentTransaction, setRecentTransaction] = useState([]);

  useEffect(() => {
    if (userData && userData.length > 0 && userData[0].id) {
      axios
        .get(`${domain}/index.php?action=getRecentTransaction&pdlNo=${userData[0].id}`)
        .then(function (response) {
          setRecentTransaction(response.data);
        })
        .catch((error) => console.error('Error fetching transaction data:', error));
    }
  }, [userData]);  // Add userData as a dependency
  
  const openRecentTransactionsModal = () => {
    setRecentTransactionsModalOpen(true);
  }
  const closeRecentTransactionsModal = () =>{
    setRecentTransactionsModalOpen(false);
  }
  const openLendersModal = () => {
    setLendersModalOpen(true);
  }
  const closeLendersModal = () =>{
    setLendersModalOpen(false);
  }
  return (
      <div className={dst.overallCard} style={{textAlign: 'left'}}>
        <div className='row mt-3'>
          <div className='col-12'>
            <h6 className="m-3 mb-4 fw-bold" style={{textAlign: 'left'}}>Overall Details</h6>
          </div>
        </div>
        <div className="row" style={{padding: '0 40px'}}>
          
          <div className="col">
            <p className="mb-3 fw-semibold" style={{ color: '#E19133' }}>Last Transaction Made</p>
            <p className="mb-3">{recentTransaction[0] ? (<>
              {recentTransaction[0].date_time} <br/> {recentTransaction[0].description}
            </>
            ) : (<>Nothing yet.</>)}</p>
            <p className="mb-3" style={{ fontSize: 'small', color: '#858D9D' }}>Updated</p>
          </div>
          <div className="col ms-3" >
            <p className="mb-3 fw-semibold" style={{ color: '#1570EF' }}>List of Valid Lenders</p>
            <button className="btn" onClick={openLendersModal} style={{ color: '#48505E', border: '1px solid #D0D3D9' }}>
              Open
            </button>
          </div>
          <div className="col">
            <p className="mb-3 ms-3 fw-semibold" style={{ color: '#845EBC' }}>Recent Transaction</p>
            <button className="btn" onClick={openRecentTransactionsModal} style={{ color: '#48505E', border: '1px solid #D0D3D9' }}>
              Click to see recent transaction
            </button>
          </div>
        </div>
        {isLendersModalOpen && <Lenders onClose={closeLendersModal} pdlId={userData[0].id}/>}
        {isRecentTransactionsModalOpen && <RecentTransactions onClose={closeRecentTransactionsModal} pdlId={userData[0].id}/>}
      </div>
  );
}

export default PDLOvDet;
