import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import dst from '../styles/DashboardStyle.module.css';
import OverallDetails from '../components/sections/OverallDetails';
import DashboardSidebar from '../components/navbars/DashboardSideNavbar';
import TopNavBar from '../components/navbars/TopNavBar';
import LargestSpender from '../components/sections/LargestSpender';
import LowBalance from '../components/sections/LowBalance';
import PopularItems from '../components/sections/PopularItems';
import Spending from '../components/sections/SpendingTotal';
import Transactions from '../components/sections/TotalTransactions';
import { Helmet } from 'react-helmet-async';

function Dashboard({ isSidebarOpen, toggleSidebar }) {

  const history = useNavigate();

  useEffect(() => {
    // Check if user is logged in
    
    const token = localStorage.getItem('token');
    
    if (!token) {
      alert("User not logged in. Please login to continue");
      // If not logged in, redirect to login page
      history('/');
    }
  }, [history]);

  const calculateMargin = () => {
    if (window.innerWidth <= 1368) {
      return isSidebarOpen ? '170px' : '78px';
    } else {
      return isSidebarOpen ? '250px' : '78px';
    }
  };
  const [margin, setMargin] = useState(calculateMargin);

const updateMargin = () => {
  setMargin(calculateMargin());
};

useEffect(() => {
  window.addEventListener('resize', updateMargin);
  return () => {
    window.removeEventListener('resize', updateMargin);
  };
}, [isSidebarOpen]);

useEffect(() => {
  updateMargin(); // Update margin when sidebar state changes
}, [isSidebarOpen]);
  const contentStyle = {
  marginLeft: margin,
  transition: 'margin .5s ease',
  backgroundColor: '#e4e9f7',
  height: '100vh'
  };
  return (
    <div>
      <Helmet>
        <title>BJMP | Dashboard</title>
      </Helmet>
      
      <DashboardSidebar isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar} />
      <div style={contentStyle}>
        <TopNavBar />
        <OverallDetails/>
        <div className={dst.row} style={{display: 'flex'}}> 
          <div className='col-4'>
            <LargestSpender/>
            <Spending/>
          </div>
          <div className='col-4'>
            <LowBalance />
            <Transactions />
          </div>
          <div className='col-4 '>
            <PopularItems />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Dashboard;