import React from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import { useNavigate } from 'react-router-dom';

function LogoutModal({ onClose, method, passwordMessage }) {
  const history = useNavigate(); // Get the history object

  const handleLogout = () => {
    localStorage.clear();
    // Redirect to the login page or wherever you want after logout
    history('/');
  };

    const handleBackdropClick = (e) => {
        // Check if the click target is the backdrop element
        if (e.target.id === 'logoutModal') {
          if(onClose){
            onClose(); // Close the modal
          }
        }
      };


  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="logoutModal" tabIndex="-1">
      <div>
        <div className={dst['modal-content']} style={{width: '500px', marginTop: '15%'}}>

          <div className={`text-center ${dst['modal-body']}`}>
            {method === 'ChangeEmail' || method === 'ChangePassword' || method === 'ArchiveAccount' ? (
            <><i className={`fa-regular fa-circle-check`} style={{fontSize: '40px'}}></i>
            <h3>You will be logged out</h3>
            <p className={dst['color-a1a1a1']}>Reason: {method !== 'ArchiveAccount' ? (<>Changed {method === 'ChangeEmail' ? (<>email, please verify your account again!</>) : (<>password, {passwordMessage}</>)}</>) : (<>Archived account</>)}</p>

            <div className={`modal-footer d-flex justify-content-center ${dst['modal-footer']} ${dst['d-flex']} ${dst['justify-content-center']}`} style={{ border: 'none' }}>
                <button type="button" className={`btn btn-secondary`} style={{ backgroundColor: '#505050', color: '#fff', width: '175px'}} onClick={handleLogout}>Logout</button>
            </div></>
            ) : (<><i className={`fa-regular fa-circle-check`} style={{fontSize: '40px'}}></i>
            <h3>Logout?</h3>
            <p className={dst['color-a1a1a1']}>Are you sure to logout?</p>

            <div className={`modal-footer d-flex justify-content-center ${dst['modal-footer']} ${dst['d-flex']} ${dst['justify-content-center']}`} style={{ border: 'none' }}>
              <button type="button" className={`btn link modal-close ${dst.link}`} style={{ border: '1px solid #505050', width: '40%', marginRight: '15px'}} onClick={onClose}>No, Cancel</button>
                <button type="button" className={`btn btn-secondary`} style={{ backgroundColor: '#505050', color: '#fff', width: '175px'}} onClick={handleLogout}>Logout</button>
            </div></>)}
          </div>
        </div>
      </div>
    </div>
  );
}

export default LogoutModal;
