import React, { useState, useEffect, useRef } from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import { Bar } from "react-chartjs-2";
import Chart from "chart.js/auto";
import axios from 'axios';
import domain from '../../constants/domain';


function BalandPur({isSidebarOpen, pdlId}) {
  const [selectedPeriod, setSelectedPeriod] = useState('Weekly');

  const BarChart = () => {
    const [chartData, setChartData] = useState([]);

    useEffect(() => {
        axios.get(selectedPeriod === 'Weekly' ?
        `${domain}/index.php?action=getWeeklyPDLTransactions&pdlNo=${pdlId}` :
        `${domain}/index.php?action=getMonthlyPDLTransactions&pdlNo=${pdlId}`
        ).then((response) => {
            setChartData(response.data);
        }).catch((error) => {
          console.error(`Error fetching ${selectedPeriod} spending data: `, error);
        })
    }, [pdlId])
    
    const handlePeriodChange = (event) => {
      setSelectedPeriod(event.target.value);
    };

    const titleText = selectedPeriod === 'Weekly' ? 'Weekly Spending Total' : 'Monthly Spending Total';

    const labels = selectedPeriod === 'Weekly' ? ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'] : 
      ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sept', 'Oct', 'Nov', 'Dec'];
  
      const data = {
        labels,
        datasets: [
          {
            label: "Transactions" ,
            data: Array.isArray(chartData) && chartData.map(entry => (entry.transaction_count)),
            backgroundColor: ['#10069F'],
            borderRadius: 50,
            barThickness: 10,
          },
        ],
      };
      const options = {
        
        scales: {
          y: {
            beginAtZero: true,
          },
        },
        maintainAspectRatio: false, // Allow chart to resize to container
        responsive: true, // Enable responsiveness
      };

    return (
      <div className={dst.BalanceCard}>
        <div className={dst.cardHeader}>
        <div className='col-9'>
          <p className="fw-bold ms-3 p-1" style={{ position: 'relative', top: '5px', textAlign: 'left' }}>
            Total Transactions
          </p>
          </div>
          <div className='col-3 align-items-end justify-content-end'>
            <i className ="fa-solid fa-calendar-days" style={{color: '#505050'}}></i>
            <select value={selectedPeriod} className={dst.ddCont} onChange={handlePeriodChange} >

                  <option value="Weekly">Weekly</option>
                  <option value="Monthly">Monthly</option>
                </select>
          </div>
        </div>
        <div className={dst.chartContainer}>
          <div style={{ height: '100%' }}>
            <Bar data={data} options={options} />
          </div>
        </div>
      </div>
      
    );
  };


  return (
    <div className={`${isSidebarOpen ? 'sidebarOpen' : 'sidebarClosed'}`}>
      <div className='mt-2 ms-3'>
        <BarChart selectedPeriod={selectedPeriod} />
      </div>
    </div>
  );
}

export default BalandPur;
