
import React, { useState, useEffect } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import { Link } from 'react-router-dom';
import CreateStock from '../modals/CreateStockEntry';
import EditStock from '../modals/EditStockEntry';
import ConfirmArchive from '../modals/ConfirmArchive';
import StockDetails from '../modals/StockDetails';
import PageNavigator from '../navbars/PageNavigator';
import InvDetails from '../modals/InventoryDetails';
import axios from 'axios';
import domain from '../../constants/domain';

function InvList() {
  const [stockData, setStock] = useState([]);
  const [selectedStockID, setSelectedStockID] = useState(null);
  const [primaryID, setPrimaryId] = useState(null);
  const [selectSearchedStockID, setSelectSearchedStockId] = useState({});
  const [itemSearch, setItemSearch] = useState([]);
  const [errorMessage, setErrorMessage] = useState('');
  const [optionDisplay, setOptionDisplay] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [inventoryData, setInventoryData] = useState([]); // New state for inventory data
  const [searchedInventoryData, setSearchedInventoryData] = useState([]); // State for filtered inventory data
  const [activeTab, setActiveTab] = useState("stockManagement"); // Set an initial active tab
  const [selectedOption, setSelectedOption] = useState("");
  const [isCreateStockModalOpen, setCreateStockModalOpen] = useState(false);
  const [isEditStockModalOpen, setEditStockModalOpen] = useState(false);
  const [isArchiveModalOpen, setArchiveModalOpen] = useState(false);
  const [isInventoryModalOpen, setInventoryModalOpen] = useState(false);
  const [isDetailModalOpen, setDetailModalOpen] = useState(false);
  const [userType, setUserType] = useState(null);
  const email = localStorage.getItem("email");
  const [itemsPerPage, setItemsPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;

  useEffect(() => {
    axios
      .get(activeTab === 'stockManagement' || activeTab === 'invManagement' ? 
      `${domain}/index.php?action=displayStock` : 
      `${domain}/index.php?action=displayArchivedStocks`
      )
      .then((res) => {
        setStock([]);
        setInventoryData([]);
        setOptionDisplay([]);
        if (Array.isArray(res.data)) {
          setStock(res.data);
          setInventoryData(res.data); // Set the inventory data
          setOptionDisplay(res.data);
        } else {
          console.error("API response is not an array:", res.data);
        }
      })
      .catch((error) => console.error("Error fetching Stock data:", error));
  }, [activeTab]);

  useEffect(() => {setSearchedInventoryData(inventoryData);}, [inventoryData])

  useEffect(() => {
    axios.get(`${domain}/index.php?action=getUserType&email=${email}`)
    .then(response => {
      if(response.data){
        setUserType(response.data.userType);
        
      }
    })
  },[]) 
  const tabs = [
    { label: "Stock Management", id: "stockManagement" },
    userType != 1 ? { label: "Inventory Management", id: "invManagement" } :
    null,
    { label: "Archived Items", id: "archivedItems" },
    // Add more tabs as needed
  ].filter(tab => tab !== null);

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    const action = 'checkDetails';
    setItemSearch(values => ({...values, action, [name]: value}))
    return;
  }

  const handleSearchQueryChange = (e) => {
    const query = e.target.value;
    setSearchQuery(query);

    // Filter the results in real-time
    const filteredData = inventoryData.filter((item) => {
      const productType = item.prodType.toLowerCase();
      return productType.includes(query.toLowerCase());
    });

    if (filteredData.length === 0) {
      setErrorMessage('No matching results found.');
    } else {
      setErrorMessage('');
    }

    // Update the state with the filtered results
    setSearchedInventoryData(filteredData);
  };


  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
    // Implement logic to load data for the new page here
  };

  const openCreateStockModal = () => {
    setCreateStockModalOpen(true);
  };
  const closeCreateStockModal = () => {
    setCreateStockModalOpen(false);
  };

  const openEditStockModal = (id, e, primID) => {
    setEditStockModalOpen(true);
    setSelectedStockID(id);
    setPrimaryId(primID);
    const newUrl = `?itemNo=${id}`;
    window.history.pushState({}, null, newUrl);
  };
  const closeEditStockModal = () => {
    setSelectedStockID(null)
    setPrimaryId(null)
    setEditStockModalOpen(false);
  };


  const openArchiveModal = (id, e, primID) => {
    setArchiveModalOpen(true);
    setSelectedStockID(id);
    setPrimaryId(primID);
  };
  const closeArchivePDLModal = (id) => {
    setSelectedStockID(null)
    setPrimaryId(null)
    setArchiveModalOpen(false);
  };

  const handleOptionChange = (event) => {
    setSelectedOption(event.target.value);
  };

  const filteredStockData = stockData.filter((item) => {
    if (selectedOption === "" || selectedOption === "None") {
      return true; // If no category is selected, show all items
    } else {
      return item.category === selectedOption;
    }
  });

  const displayedItems = filteredStockData ? filteredStockData.slice(startIndex, endIndex) : null;


  const openDetailModal = (id, e) => {
    setDetailModalOpen(true);
    setSelectedStockID(id);
    const newUrl = `?itemNo=${id}`;
    window.history.pushState({}, null, newUrl);
  };
  const closeDetailPDLModal = () => {
    setSelectedStockID(null)
    setPrimaryId(null)
    setDetailModalOpen(false);
  };

  const openInventoryModal = (id, e) => {
    setSelectedStockID(id)
    setInventoryModalOpen(true);
  }
  const closeInventoryModal = () => {
    setSelectedStockID(null)
    setPrimaryId(null)
    setInventoryModalOpen(false);
  }

  const generateReport = () => {
    if(stockData){
      const action = "generateStocksReport";
      const actionData = { action };
      axios.post(domain, actionData)
          .then(response => {
              // Handle successful response, if needed
               // Response.data will contain the file URL
               const fileUrl = response.data.fileUrl;
              const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/reports/'));
              const newWindow = window.open(formattedFileUrl, '_blank');

              // Ensure the new window is fully loaded before attempting to print
              newWindow.onload = () => {
                  newWindow.print();
              };
          })
          .catch(error => {
              // Handle error, if needed
              console.error('Error generating report:', error);
          });
    }
}
console.log(stockData);
  return (
    <div className="" style={{margin: '30px'}}>
      <div className='row' style={{marginBottom:'-8px'}}>
        <div className='col-5 d-flex justify-content-start'>
        {tabs.map((tab) => (
              <button
                key={tab.id}
                className={activeTab === tab.id ? `${dst.tab} ${dst.TabActive} ` : dst.tab}
                onClick={() => setActiveTab(tab.id)}
              >
                {tab.label}
              </button>
            ))}
          </div>
        </div>
      <div className={dst.con} >
        {activeTab === 'archivedItems' && (
          <div className={`${dst.row} mt-1`}>
        <div style={{display: 'flex', padding: '10px'}}>
        <div className='col-3'>
          <p className={`fw-bold ms-3 textColor`} style={{textAlign: 'left', marginTop: '15px'}}>Archived Items</p>
          </div>
        </div>
        <div className='row'>
          <div className='col-12' style={{ width: '100%', padding: 0 }}>
          <table
              className={dst.tableZebra}
              style={{
                border: '#F0F1F3',
                color: '#48505E',
                height: 'auto',
                fontSize: 'small',
                padding: '0',
                textAlign: 'left',
                width: '93.5%', // Set the table width to 100%
                margin: '0 auto',
              }}
            >
              <thead>
                <tr>
                  <th scope="col" id="stockNo">
                    Item No.
                  </th>
                  <th scope="col">Product Type</th>
                  <th scope="col">Category</th>
                  <th scope="col">Price</th>
                  <th scope="col">Date Archived</th>
                </tr>
              </thead>
              <tbody>
                {displayedItems && displayedItems.map((item, index) => (
                  <tr key={index}>
                    <td>I-{item.id}</td>
                    <td>{item.prodType}</td>
                    <td>{item.category}</td>
                    <td>PHP{item.formatted_price}</td>
                    <td>{item.date_archived}</td>
                  </tr>
                ))}
                {filteredStockData && filteredStockData.length === 0 && (
              <tr>
                <td style={{padding: '150px', textAlign: 'center'}} colSpan={7}>Nothing to show</td>
              </tr>
              )}
              </tbody>
            </table>
          </div>
        </div>
        
          <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '15%'}}>
          <PageNavigator totalPages={Math.ceil(filteredStockData.length / itemsPerPage)} currentPage={currentPage} onPageChange={handlePageChange} />
          </div>
        </div>
        

        )}
        {activeTab === 'invManagement' && (
            <div className={`${dst.row} mt-1`}>
              <div className="row mt-2" style={{ display: 'flex' }}>
                <div className="col-5">
                  <p className={`fw-bold ms-3 textColor`} style={{ textAlign: 'left', marginTop: '15px' }}>
                    Inventory Management
                  </p>
                </div>
              </div>
              <div className="row mt-2" style={{ display: 'flex' }}>
                <div className="col-2">
                  <label htmlFor="searchItem" className="form-label mt-2">
                    Search by Product Type:
                  </label>
                </div>
                <div className="col-9">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Enter Product Type"
                    value={searchQuery}
                    onChange={handleSearchQueryChange}
                    id="searchItem"
                    style={{ width: '100%', boxShadow: 'none' }}
                    required
                  />
                </div>

              </div>
                    <hr style={{width: '96%'}}></hr>
              <div className="row mt-2" style={{display: 'flex'}}>
                  <div className='col-5'>
                    <p className={`fw-bold ms-3 textColor`} style={{textAlign: 'left', marginTop: '15px'}}>Results</p>
                  </div>
                </div>  
                <table
              className={`${dst.tableZebra} mt-1`}
              style={{
                border: "#F0F1F3",
                color: "#48505E",
                height: "auto",
                fontSize: "small",
                width: "93.5%",
                padding: "0",
                textAlign: 'left',
                margin: '0 auto',
              }}
            >
              <thead>
                <tr>
                  <th scope="col">Item No.</th>
                  <th scope="col">Product Type</th>
                  <th scope="col">Critical Stock Threshold</th>
                  <th scope="col">Remaining Stocks</th>
                  <th scope="col">Require Restock?</th>
                  <th scope="col">Actions</th>
                </tr>
              </thead>
              <tbody className={dst.shrinkMedia}>
                {searchedInventoryData && searchedInventoryData.slice(0.8).map((item, index) => (
                  <tr key={index}>
                    <td>I-{item.id}</td>
                    <td>{item.prodType}</td>
                    <td>{item.criticalStock}</td>
                    <td>{item.remainingStock}</td>
                    <td>{parseInt(item.criticalStock) >= parseInt(item.remainingStock) ? (
                      <>
                        Yes
                      </>
                    ) : (
                      <>
                        No
                      </>
                    )}</td>
                    <td>
                      <div className={`${dst.actionButtons}`}>
                        <p onClick={(e) => openInventoryModal(item.id, e)}>
                          <i className="fa-solid fa-boxes-packing fa-sm"></i>
                        </p>
                      </div>
                    </td>
                  </tr>
                ))}
                {searchedInventoryData && searchedInventoryData.length === 0 && (
                  <tr>
                    <td style={{ padding: '150px', textAlign: 'center' }} colSpan={6}>Nothing to show</td>
                  </tr>
                )}
              </tbody>
            </table>
              <p className='mt-4' style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
            </div>)}

        {activeTab === 'stockManagement' && (
        <div className={`${dst.row} mt-1`}>
        <div style={{display: 'flex', padding: '10px'}}>
        <div className='col-3'>
          <p className={`fw-bold ms-3 textColor`} style={{textAlign: 'left', marginTop: '15px'}}>Stock Management</p>
          </div>
          <div className='col-9 d-flex justify-content-end'>
          <button className={`${dst.add} me-2`} style={{left: '0', width: 'fit-content', padding: '0 10px', backgroundColor: '#222'}}type='button' onClick={generateReport}>Generate Report
          </button>
          <button className={`${dst.add} me-2`} type='button' onClick={openCreateStockModal} style={{left: '0'}}>Add Stocks</button>
              <select className={`${dst.sort} me-4`} value={selectedOption} onChange={handleOptionChange} style={{left: '0',  width: 'fit-content'}} >
                <option value="" disabled hidden>Filters</option>
                <option value="None">None</option>
                <option value="Personal Hygiene">Personal Hygiene</option>
                <option value="Consumables">Consumables</option>
                <option value="Toiletries">Toiletries</option>
              </select>
          </div>
        </div>
        <div className='row'>
          <div className='col-12' style={{ width: '100%', padding: 0 }}>
          <table
              className={dst.tableZebra}
              style={{
                border: '#F0F1F3',
                color: '#48505E',
                height: 'auto',
                fontSize: 'small',
                padding: '0',
                textAlign: 'left',
                width: '93.5%', // Set the table width to 100%
                margin: '0 auto',
              }}
            >
              <thead>
                <tr>
                  <th scope="col" id="stockNo">
                    Item No.
                  </th>
                  <th scope="col">Product Type</th>
                  <th scope="col">Category</th>
                  <th scope="col">Price</th>
                  <th scope="col">Critical Stock Threshold</th>
                  <th scope="col">Remaining Stocks</th>
                  <th scope="col">Actions</th>
                </tr>
              </thead>
              <tbody>
                {displayedItems && displayedItems.map((item, index) => (
                  <tr key={index}>
                    <td>I-{item.id}</td>
                    <td>{item.prodType}</td>
                    <td>{item.category}</td>
                    <td>PHP{item.formatted_price}</td>
                    <td>{item.criticalStock}</td>
                    <td>{item.remainingStock}</td>
                    <td>
                      <div className={`${dst.actionButtons}`}>
                        <p onClick={(e) => openDetailModal(item.id, e, item.stock_id)}>
                            <i className="fa-solid fa-eye fa-sm"></i>
                        </p>
                        <p onClick={(e) => openEditStockModal(item.id, e, item.stock_id)}>
                          <i className="fa-solid fa-pen-to-square fa-sm"></i>
                        </p>
                        <p onClick={(e) => openArchiveModal(item.id, e, item.stock_id)}>
                          {" "}
                          <i className="fa-solid fa-box-archive fa-sm"></i>
                        </p>
                      </div>
                    </td>
                  </tr>
                ))}
                {filteredStockData && filteredStockData.length === 0 && (
              <tr>
                <td style={{padding: '150px', textAlign: 'center'}} colSpan={7}>Nothing to show</td>
              </tr>
              )}
              </tbody>
            </table>
          </div>
        </div>
        
          <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '15%'}}>
          <PageNavigator totalPages={Math.ceil(filteredStockData.length / itemsPerPage)} currentPage={currentPage} onPageChange={handlePageChange} />
          </div>
        </div>
        
        )}
        
        <div className='row'>
          <p> </p>
        </div>
      </div>
      {isCreateStockModalOpen && <CreateStock onClose={closeCreateStockModal}/>}
      {isEditStockModalOpen && <EditStock onClose={closeEditStockModal} itemId={selectedStockID} primaryID={primaryID}/>}
      {isArchiveModalOpen && <ConfirmArchive onClose={closeArchivePDLModal} primaryStockID={primaryID} archiveType="Stock"/>}
      {isDetailModalOpen && <StockDetails onClose={closeDetailPDLModal} itemId={selectedStockID}/>}
      {isInventoryModalOpen && <InvDetails onClose={closeInventoryModal} itemId={selectedStockID}/>}
    </div>
    
  );
}

export default InvList;
