import React, {useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import Success from './SuccessfulAction';
import axios from 'axios';
import domain from '../../constants/domain';

function CreateLender({onClose, pdlId}) {
  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [formData, setFormData] = useState({});
    const handleBackdropClick = (e) => {
        // Check if the click target is the backdrop element
    if (e.target.id === 'createLender') {
        onClose();
        }
    };
  const action = 'createLender';
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.type === 'file' ? e.target.files[0] : e.target.value;
    if(e.target.type === 'file') {
      setFormData(values => ({...values, action, validID: value}));
      return;
    }
    else{
      setFormData(values => ({...values, action, pdlId, [name]: value}));
      return;
    }
  }
  const handleSubmit = async (e) => {
    e.preventDefault();
    const {first_name, middle_name, last_name, relationship, validID} = formData;
    const fileUp = new FormData();
    fileUp.append('validID', validID);
    fileUp.append('first_name', first_name);
    fileUp.append('middle_name', middle_name);
    fileUp.append('last_name', last_name);
    fileUp.append('pdlId', pdlId);
    fileUp.append('action', 'uploadLenderID')
    
    if(!first_name || !middle_name || !last_name || !relationship || !validID){
      setErrorMessage('Please fill in all required fields!');
      return;
    }
    else{
      setErrorMessage('');
      axios.post(domain, formData);
      setTimeout(async () => {
        try{
          axios.post(domain, fileUp);
          openSuccessModal();
        }
        catch (error) {
        }
      }, 1000)
    }
  }
  const openSuccessModal = () => {
    setSuccessModalOpen(true);
  };
  const closeSuccessModal = () => {
      setSuccessModalOpen(false);
      onClose();
  };
    

  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="createLender" tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '800px', marginTop: '16%'}}>
          <div className={dst['modal-header']} style={{ border: 'none', display: 'flex'}}>
            <h1 className="modal-title fs-5" id="exampleModalLabel">
              Create Lender
            </h1>
          </div>
          <hr style={{margin: '0'}}></hr>
          <div className={dst['modal-body']}>
            <form>
          <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="first_name" className="col-form-label">First Name:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="first_name" name="first_name" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter first name" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="last_name" className="col-form-label">Last Name:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="last_name" name="last_name" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter last name" onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="middle_name" className="col-form-label">Middle Name:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="middle_name" name="middle_name" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter middle name" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="relationship" className="col-form-label">Relationship:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="relationship" name="relationship" className="form-control" maxLength={6} style={{ boxShadow: 'none' }} placeholder="Enter relationship" onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="validID" className="col-form-label">Valid ID (PDF):</label>
                </div>
                <div className="col-10">
                  <input type="file" id="validID" name="validID" className="form-control" style={{ boxShadow: 'none' }} accept="application/pdf" onChange={handleChange} />
                </div>
              </div>
            <p className='mt-1 mb-2' style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
            <div className={dst['modal-footer']} style={{ border: 'none', display: 'flex', justifyContent: 'flex-end' }}>
            <button
              type="button"
              className="btn"
              onClick={onClose}
              style={{ borderColor: '#D0D3D9' }}
            >
              Discard
            </button>
            <button
              type="button"
              className="btn"
              style={{ 
                backgroundColor: '#505050',
                color: '#fff',
                marginLeft: '10px'
              }}
              onClick={handleSubmit}
            >
              Enter
            </button>  
          </div>
          </form>
          </div>
        </div>
        {isSuccessModalOpen && <Success onClose={closeSuccessModal}/>}
    </div>
  );
}

export default CreateLender;
