import React, { useEffect, useState } from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../constants/domain';

function NotificationModal({onClose}){
    
    const [notifications, setNotifications] = useState([]);
    const [sliceData, setSliceData] = useState(5);
    const email = localStorage.getItem("email");

    useEffect(() => {
        axios.get(`${domain}/index.php?action=getUserType&email=${email}`)
        .then((response) => {
            const userType = response.data.userType;
            if(userType != 0){
                axios.get(`${domain}/index.php?action=getNotifications&a=1`)
                .then((response) => {
                    setNotifications(response.data);
                    setSliceData(5);
                })
                .catch(error => console.error("Cannot get data: ", error));
            }
            else{
                axios.get(`${domain}/index.php?action=getNotifications&a=2`)
                .then((response) => {
                    setNotifications(response.data);
                    setSliceData(100);
                })
                .catch(error => console.error("Cannot get data: ", error));
            }
        })
        
    }, [])
 
    const handleBackdropClick = (e) => {
        const modal = document.getElementById('notif'); // Assuming 'notif' is the ID of your modal

        // Check if the click target is outside the modal box
        if (modal && !modal.contains(e.target)) {
          onClose(); // Close the modal
        }
        }
    return (
        <div className={dst.modal} onClick={handleBackdropClick} style={{background: 'none', backdropFilter: 'none'}}>
            <div className={dst['notification-modal']}id='notif'  >
            <div className='row'>
                <div className='col-12'>
                    <h6 className="fw-bold" style={{textAlign: 'left'}}>Notifications</h6>
                    <hr className='m-0'></hr>
                </div>
            </div>
            <div className='row' style={{minHeight: '300px', maxHeight: '300px', overflowY: 'auto'}}>
                <div className='col-12'>
                <table
                    className="table position-relative mt-2"
                    style={{
                    border: '#F0F1F3',
                    color: '#48505E',
                    height: 'auto',
                    fontSize: 'small',
                    textAlign: 'left'
                    }}
                        >
                            <tbody>
                                {Array.isArray(notifications) && notifications.length !== 0 && notifications.slice(0, sliceData).map((not, index) => (
                                    <>
                                        <tr key={index}>
                                            <th>{not.action}:</th>
                                            <td>
                                                {not.description}
                                                <span style={{float: 'right', fontSize: '8px', color: '#666', marginTop: '15px', marginBottom: '0'}}>{not.notif_date}</span>
                                            </td>
                                        </tr>
                                    </>
                                ))}
                                {Array.isArray(notifications) && notifications.length === 0 && (
                                    <>
                                    <h2><i className="fa-regular fa-bell fa-sm"
                                        style={{ marginTop: '15px', color: '#858D9D', fontSize: '60px', padding: '100px 100px 0 100px'}}>
                                    </i></h2>
                                    <p style={{textAlign: 'center', paddingBottom: '40px'}}>Notifications will go live here</p>
                                    </>
                                )}
                            </tbody>
                        </table>
                </div>
            </div>     
            </div>
        </div>
        
      );
}
export default NotificationModal;