import React, {useEffect, useState} from 'react';
import './App.css';
import { BrowserRouter as Router} from 'react-router-dom';
import { HelmetProvider } from 'react-helmet-async';
import RoutesWithSidebar from './config/routes'

function App() {  
    const isMobileDevice = () => {
    const userAgent = navigator.userAgent;
    return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(userAgent);
  };
  const [display, setDisplay] = useState(true);
  const disableMobileView = () => {
    const isMobile = isMobileDevice();
    if (isMobile) {
      setDisplay(false);
    }
    else{
      setDisplay(true);
    }
  };

  useEffect(() => {
    // Call the function to disable mobile view when the component mounts
    disableMobileView();
  }, []);

  return (
    <HelmetProvider>
      <Router>
          { display === true ? (<div className='App'>
            <div className='auth-wrapper'>
              <div className='auth-inner'>
                  <RoutesWithSidebar/>
              </div>
            </div>
          </div>) : (<>Mobile devices are not supported on this website</>)}
      </Router>
    </HelmetProvider>
  );
}

export default App;
