import React, { useState, useEffect } from "react";
import dst from "../../styles/DashboardStyle.module.css"; // Import your CSS module
import axios from "axios";
import { useNavigate } from "react-router-dom";
import domain from "../../constants/domain";
function StockEntryModal({ onClose }) {
  const navigate = useNavigate();

  const [inputs, setInputs] = useState({});
  const [selectCategory, setSelectCategory] = useState(""); // Initialize selectCategory
  const [formdata, setFormData] = useState({});
  const [errorMessage, setErrorMessage] = useState('');

  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    const action = "createStock";
    setInputs((values) => ({ action, ...values, [name]: value }));

    if (name === "category") {
      setSelectCategory(value);
      setFormData((values) => ({ ...values, action, category: value }));
      return;
    } else {
      setFormData((values) => ({ ...values, action, [name]: value }));
      return;
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const {productType, productName, category, price, dateAdded} = inputs
    if(!productType || !productName || !category || !price || !dateAdded){
      setErrorMessage('Please input something on the fields above!');
    }
    else{
      setErrorMessage('');
      axios
      .post(domain, inputs)
      .then(function (response) {
        window.location.reload();
      });
    }
    
  };
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === "exampleModal") {
      onClose(); // Close the modal
    }
  };

  return (
    <div
      className={dst.modal}
      onClick={handleBackdropClick}
      id="exampleModal"
      tabIndex="-1"
    >
      <div
        className={dst["modal-content"]}
        style={{ width: "900px", marginTop: "12%" }}
      >
        <div
          className={dst["modal-header"]}
          style={{ border: "none", display: "flex" }}
        >
          <h1 className="modal-title fs-5" id="exampleModalLabel">
            Create Stock Entry
          </h1>
          <button
            type="button"
            className="btn-close"
            style={{ boxShadow: "none", marginLeft: "75%" }}
            onClick={onClose}
          ></button>
        </div>
        <div className={dst["modal-body"]}>
          <form onSubmit={handleSubmit}>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="productType" className="col-form-label">
                  Product Type:
                </label>
              </div>
              <div className="col">
                <input
                  type="text"
                  id="productType"
                  name="productType"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter product type"
                  onChange={handleChange}
                />
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="productName" className="col-form-label">
                  Product Name:
                </label>
              </div>
              <div className="col">
                <input
                  type="text"
                  id="productName"
                  name="productName"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter product name"
                  onChange={handleChange}
                />
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="category" className="col-form-label">
                  Category:
                </label>
              </div>
              <div className="col">
                <select
                  id="category"
                  name="category"
                  className="form-control"
                  style={{ boxShadow: "none", color: "#666" }}
                  value={selectCategory}
                  onChange={handleChange}
                >
                  <option value="" disabled hidden>
                    Select category
                  </option>
                  <option value="Personal Hygiene">Personal Hygiene</option>
                  <option value="Consumables">Consumables</option>
                  <option value="Toiletries">Toiletries</option>
                </select>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="price" className="col-form-label">
                  Price:
                </label>
              </div>
              <div className="col">
                <input
                  type="number"
                  id="price"
                  name="price"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  placeholder="Enter a number"
                  onChange={handleChange}
                  step={0.01}
                />
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4">
                <label htmlFor="dateAdded" className="col-form-label">
                  Date Added:
                </label>
              </div>
              <div className="col">
                <input
                  type="date"
                  id="dateAdded"
                  name="dateAdded"
                  className="form-control"
                  style={{ boxShadow: "none" }}
                  onChange={handleChange}
                />
              </div>
            </div>
            <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
            <div className={dst["modal-footer"]} style={{ border: "none" }}>
              <div className="row">
                <div className="col-9">

                </div>
                <div className="col-3">
                <button
                type="button"
                className="btn"
                onClick={onClose}
                style={{ borderColor: "#D0D3D9"}}
              >
                Discard
              </button>
              <button
                type="submit"
                className="btn"
                style={{
                  backgroundColor: "#505050",
                  color: "#fff",
                  marginLeft: "10px",
                }}
              >
                Submit
              </button>
                </div>
              </div>
              
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default StockEntryModal;
