import React, {useEffect, useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import axios from 'axios';
import domain from '../../constants/domain';

function AuditDetails({onClose, logId}) {
  const [logData, setLogData] = useState({});

  useEffect(() => {
    getLog();
  }, [])

  function getLog(){
    axios.get(`${domain}/index.php?action=displaySpecificAudit&logId=${logId}`)
    .then(function(response){
        setLogData(response.data[0]);

    }).catch(error => console.error('Error getting data: ', error));
  }

    const handleBackdropClick = (e) => {
      // Check if the click target is the backdrop element
      if (e.target.id === 'exampleModal') {
        onClose(); // Close the modal
      }
    };

    const handleViewReceipt = () => {
      const receiptUrl = logData.transaction_path;  // Replace with the actual URL
      const formattedReceiptUrl = receiptUrl.substring(receiptUrl.indexOf('/receipts/'));
      window.open(formattedReceiptUrl, '_blank');
    };
  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="exampleModal" tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '600px', marginTop: '12%'}}>
          <div className={dst['modal-header']} style={{ border: 'none', display: 'flex'}}>
            <h1 className="modal-title fs-5" id="exampleModalLabel">
              Log Details
            </h1>
          </div>
          <div className={dst['modal-body']}>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-5">
                <label htmlFor="time" className="col-form-label">
                  Time: 
                </label>
              </div>
              <div className="col-5">
                <label htmlFor="time" className="col-form-label">
                  {logData.date_time}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-5">
                <label htmlFor="email" className="col-form-label">
                  Email:
                </label>
              </div>
              <div className="col-5">
                <label htmlFor="email" className="col-form-label">
                  {logData.email}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-5">
                <label htmlFor="event" className="col-form-label">
                  Transaction Type: 
                </label>
              </div>
              <div className="col-5">
                <label htmlFor="event" className="col-form-label">
                  {logData.transaction_type}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-5">
                <label htmlFor="desc" className="col-form-label">
                  Description: 
                </label>
              </div>
              <div className="col-5">
                <label htmlFor="desc" className="col-form-label">
                  {logData.description}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-5">
                <label htmlFor="rece" className="col-form-label">
                  Transaction Details: 
                </label>
              </div>
              <div className="col-5">
                <button onClick={handleViewReceipt} className="btn btn-secondary" style={{ fontSize: 'small', width: '220px' }}>Receipt.</button>
              </div>
            </div>

          </div>
        </div>
    </div>
  );
}

export default AuditDetails;
