import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import PurchaseSuccess from './PurchaseSuccess';
import Success from './SuccessfulAction';
import {v5 as uuidv5} from 'uuid';
import domain from '../../constants/domain';
import axios from 'axios';
/* 
   global selectChangeEvent 
   global readersDropDownPopulate 
   global populatePopUpModal 
   global enableDisableScanQualityDiv 
   global disableEnableExport 
   global FingerprintSdkTest 
   global inputOnStart
   global onStop
*/

function FingerprintModal({onClose, typeOfFinScan, pdlData, setFingerprintData, postData}) {
  const [typeOfScan, setTypeofFinScan] = useState(typeOfFinScan);
  const [testButton, setTestButton] = useState('');
  const [isPurchaseSuccessModalOpen, setPurchaseSuccessModalOpen] = useState(false);
  const [inputCheckerMessage, setInputCheckerMessage] = useState('');
  const [startButtonState, setStartButtonState] = useState(false);
  const [stopButtonState, setStopButtonState] = useState(true);
  const [fingerprintIcon, setFingerprintIcon] = useState(true);

  const generateDeterministicUUID = (pdlData, namespace) => {
    // Use SHA-256 or another hash function to generate a fixed-length hash
    // Generate a UUID based on the hashed data and custom namespace
    const uniqueID = uuidv5(pdlData, namespace);
    return uniqueID;
  };
  useEffect(() => {
    const script = document.createElement('script');
    script.src = '/websdk.js';
    script.async = true;
    script.defer = true;
    const imagediv = document.getElementById('imagediv');
    
    // Define a function to run when img element is detected inside imagediv
    const handleImgElementDetected = () => {
        const customNamespace = uuidv5(domain, uuidv5.URL);
        const sanitizedOffense = pdlData ? (pdlData.offense).replace(/[^\w\s]/g, '').replace(/\s+/g, '') : null;
        const frankenstein = 
        pdlData && typeOfFinScan === 'create' ? (pdlData.userID 
          + (pdlData.lastName).charAt(0)
          + (pdlData.firstName).charAt(0)
          + (pdlData.middleName).charAt(0)
          + sanitizedOffense) : 
          pdlData && typeOfFinScan === 'purchase' ?
            (pdlData.id 
            + (pdlData.pdl_lastname).charAt(0)
            + (pdlData.pdl_firstname).charAt(0)
            + (pdlData.pdl_middlename).charAt(0)
            + sanitizedOffense)
          : null;
        const uniqueID = generateDeterministicUUID(frankenstein, customNamespace);
        
        if(typeOfFinScan === 'create'){
          const formData = {
            fingerprintID: uniqueID,
            action: typeOfFinScan === 'create' ? 'setFingerprintData'
            : typeOfFinScan === 'purchase' ? 'purchaseWithFingerprint' : null,
            id: pdlData && typeOfFinScan === 'create' ? pdlData.userID : 
            pdlData && typeOfFinScan === 'purchase' ? pdlData.id : null,
            lastName: pdlData && typeOfFinScan === 'create' ? pdlData.lastName : 
            pdlData && typeOfFinScan === 'purchase' ? pdlData.pdl_lastname : null,
            firstName: pdlData && typeOfFinScan === 'create' ? pdlData.firstName : 
            pdlData && typeOfFinScan === 'purchase' ? pdlData.pdl_firstname : null,
            middleName: pdlData && typeOfFinScan === 'create' ? pdlData.middleName : 
            pdlData && typeOfFinScan === 'purchase' ? pdlData.pdl_middlename : null,
            offense: pdlData ? pdlData.offense : null,
          }
          setFingerprintData(uniqueID);
          axios.post(domain, formData).then(
            response => {
              if(response.data){
                openSuccessModal();
              }
            }
          ).catch(error => console.error('Error getting data: ', error));

        }
        else{
          axios.post(domain, postData).then(
            response => {
              if(response.data){
                openSuccessModal();
                localStorage.removeItem("pdlData");
                localStorage.removeItem("searchData");
                localStorage.removeItem("pdlPOSImageSrc");
                localStorage.removeItem("posPDL");
                localStorage.removeItem("responseData");
              }
            }
          ).catch(error => console.error('Error getting data: ', error));
        }
    };
    // Attach a callback function to execute after the script is loaded
    script.onload = () => {
      test = new FingerprintSdkTest();
      readersDropDownPopulate(true); //To populate readers for drop down selection
      enableDisableScanQualityDiv("content-reader"); // To enable disable scan quality div
      disableEnableExport(true);
    };

    document.body.appendChild(script);
    const observer = new MutationObserver((mutationsList) => {
      for (let mutation of mutationsList) {
        setFingerprintIcon(false);
        if (mutation.type === 'childList') {
          // Check if img element is added
          
          const imgElement = mutation.addedNodes[0];
          if (imgElement && imgElement.tagName.toLowerCase() === 'div') {
            setTimeout(() => {
              
              handleImgElementDetected();
            }, 1000);
          }
        }
      }
    });

    // Start observing the imagediv for changes in its children
    observer.observe(imagediv, { childList: true });
    
    // Cleanup: remove the script when the component is unmounted
    return () => {
      document.body.removeChild(script);
      observer.disconnect();
    };
  }, []);
  const openPurchaseSuccessModal = () => {
    
    setPurchaseSuccessModalOpen(true);
  };
  const closePurchaseSuccessModal = () => {
    setPurchaseSuccessModalOpen(false);
    onClose();
  };
  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const openSuccessModal = () => {
    setSuccessModalOpen(true);
  };
  const closeSuccessModal = () => {
    setSuccessModalOpen(false);
    onClose();
    window.location.reload();
  };

    const handleBackdropClick = (e) => {
      // Check if the click target is the backdrop element
      if (e.target.id === 'exampleModal') {
        onClose(); // Close the modal
      }
    };
    const inputChecker = () => {
      inputOnStart();
      setStartButtonState(true);
      setStopButtonState(false);
      setInputCheckerMessage('Scanning fingerprint...');
    }

    const stopInputChecker = () => {
      onStop();
      localStorage.removeItem("fingerprintImg");
      setStartButtonState(false);
      setStopButtonState(true);
      setInputCheckerMessage('');
    }
  
  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="exampleModal" tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '350px', marginTop: '13%'}}>
          <div className={`${dst['modal-header']} text-start`} style={{ border: 'none'}}>
            <h5 className="modal-title fw-bold" id="exampleModalLabel">
              Scan your fingerprint
            </h5>
            <p style={{fontSize: '12px', color: '#666'}}>
              Scan your fingerprint using the biometric device
            </p>
          </div>
          <div className={`content-capture ${dst['modal-body']}`}>
            <div className="row g-3 align-items-center mb-2">
              <div className="col">
              {typeOfScan === 'purchase' && (
              <>
              <div className='row mb-3'>
                  <div id='imagediv' style={{height: 'fit-content', margin: 'auto', borderRadius: '25px', width: 'fit-content'}}>
                        
                    </div>
                  {fingerprintIcon ? (<><i className="fas fa-fingerprint" style={{ fontSize: '150px' }} id="defaultFingerprintIcon"></i></>) : (<><i className="fas fa-fingerprint" style={{ fontSize: '150px', color: '#00ff00' }} id="defaultFingerprintIcon"></i></>)}
              
              </div>
              <div className='row'>
                <div className='col-6'> 
                <button className="btn btn-secondary" style={{ fontSize: 'small' }} onClick={inputChecker} disabled={startButtonState}>Start</button>
                </div>
                <div className='col-6'> 
                <button className="btn btn-secondary" style={{ fontSize: 'small' }} onClick={stopInputChecker} disabled={stopButtonState}>Stop</button>
                </div>
              </div>
              </>
              )
              }
              {typeOfScan === 'create' && (
                <>
                <div className='row mb-3'>
                    <div id='imagediv' style={{height: 'fit-content', margin: 'auto', borderRadius: '25px', width: 'fit-content'}}>
                          
                      </div>
                    {fingerprintIcon ? (<><i className="fas fa-fingerprint" style={{ fontSize: '150px' }} id="defaultFingerprintIcon"></i></>) : (<><i className="fas fa-fingerprint" style={{ fontSize: '150px', color: '#00ff00' }} id="defaultFingerprintIcon"></i></>)}
                
                </div>
                <div className='row'>
                  <div className='col-6'> 
                  <button className="btn btn-secondary" style={{ fontSize: 'small' }} onClick={inputChecker} disabled={startButtonState}>Start</button>
                  </div>
                  <div className='col-6'> 
                  <button className="btn btn-secondary" style={{ fontSize: 'small' }} onClick={stopInputChecker} disabled={stopButtonState}>Stop</button>
                  </div>
                </div>
              </>
                )
              }
              
                </div>
            </div>
          </div>
        </div>
        <div style={{display: 'none'}}>
                        <div id="content-capture">
                        <select className="form-control mb-2" id="readersDropDown"></select>
                        <div class="mt-2" style={{fontSize: '8px'}} id="ReaderInformationFromDropDown"></div>
                          <div id="status"></div>            
                          <div id="imagediv"></div>
                          <div id="imageGallery"></div>
                          <div id="deviceInfo"></div>   
                          <div id="Scores"><input id="qualityInputBox"></input></div>
                          
                        </div>
                      </div>
        {isPurchaseSuccessModalOpen && <PurchaseSuccess onClose={closePurchaseSuccessModal}/>}
        {isSuccessModalOpen && <Success onClose={closeSuccessModal}/>}
    </div>
  );
}

export default FingerprintModal;
