import React, {useEffect, useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import PageNavigator from '../navbars/PageNavigator';
import axios from 'axios';
import domain from '../../constants/domain';
import CreateLender from '../modals/CreateLender';
import EditLender from '../modals/EditLender';
import ArchiveModal from '../modals/ConfirmArchive';
import SuccAcc from './SuccessfulAction';
function LenderData({onClose, pdlId}) {
    const [lenderData, setLenderData] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [isCreateLenderOpen, setCreateLenderOpen] = useState(false);
    const [entriesPerPage] = useState(10);
    const [isEditLenderOpen, setEditLenderOpen] = useState(false);
    const [lenderID, setLenderID] = useState([]);
    const [lenderIDPath, setLenderIDPath] = useState(null);
    const [isArchiveModalOpen, setArchiveModalOpen] = useState(false);
    const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);

    const id = pdlId;
    const email = localStorage.getItem('email');
    const [userType, setUserType] = useState(null);
    useEffect(() => {
        axios.get(`${domain}/index.php?action=getUserType&email=${email}`)
        .then(response => {
          if(response.data){
            setUserType(response.data.userType);
            
          }
        })
      },[]) 

    useEffect(() => {
        axios.get(`${domain}/index.php?action=getLenderData&id=${pdlId}`)
        .then(response => {
            setLenderData(response.data);
        }).catch(error => console.error('Error fetching lender data: ', error));

       
    }, [])
    useEffect(() => {
        // This effect runs whenever lenderIDPath changes
        if (lenderIDPath && lenderIDPath.length > 0) {
          const idURL = lenderIDPath[0].valid_id_path;
          const formattedIDURL = idURL.substring(idURL.indexOf('/ids/'));
          window.open(formattedIDURL, '_blank');
        }
      }, [lenderIDPath]);  // Run this effect whenever lenderIDPath changes
    const handlePageChange = (newPage) => {
        setCurrentPage(newPage);
        // Implement logic to load data for the new page here
    };
    
    const openLenderModal = () => {
        setCreateLenderOpen(true);
    };
    const closeLenderModal = () => {
        setCreateLenderOpen(false);
        setEditLenderOpen(false);
    };
    const handleBackdropClick = (e) => {
        // Check if the click target is the backdrop element
    if (e.target.id === 'exampleModal') {
        onClose();
        }
    };
    
    const handleViewID = (id, e) => {
        axios.get(`${domain}/index.php?action=getSpecificLenderData&pdlNo=${pdlId}&lenID=${id}`)
        .then(response => {
            setLenderIDPath(response.data);
        }).catch(error => console.error('Error fetching lender data: ', error));
    }
    const totalEntries = lenderData.length;
    const totalPages = Math.ceil(totalEntries / entriesPerPage);
    const entriesToShow = lenderData.slice(
      (currentPage - 1) * entriesPerPage,
      currentPage * entriesPerPage
    );
    const openEditLenderModal = (id, e) => {
        setLenderID(id);
        setEditLenderOpen(true);
    }
    
    const openArchiveModal = (id, primaryID) => {
        setLenderID(id);
        setArchiveModalOpen(true);
    }
    const closeArchiveModal = () => {
        setArchiveModalOpen(false);
        window.location.reload();
    }

    const approveID = (id) => {
        setLenderID(id);
        axios.post(domain, {action: 'approveID', id: id}).then(
            response => {
                if(response.data) {
                    setSuccessModalOpen(true);
                }
            }
        )
    }
    const closeSuccessModal = () => {
        window.location.reload();
    }
    return (
        <div className={dst.modal} onClick={handleBackdropClick} id="exampleModal" tabIndex="-1">
            <div className={dst['modal-content']} style={{width: '600px', marginTop: '9%'}}>
            <div className={dst['modal-header']} style={{ border: 'none' }}>
            <div className='row'>
                <div className='col-9'>
                <h1 className="fs-5 fw-bold" id="exampleModalLabel" style={{ alignItems: 'flex-end', marginTop: '5px', lineHeight: '1.5' }}>
                    Associated Lenders
                </h1>
                </div>
                <div className='col-3'>
                <button onClick={openLenderModal} className={dst.add} style={{ left: '0', top: '0', height: '35px' }} type='button'>
                    Add Lender
                </button>
                </div>
            </div>
            </div>
            <div className={dst['modal-body']}>
            <table
                className={dst.tableZebra}
                style={{
                border: '#F0F1F3',
                color: '#48505E',
                height: 'auto',
                fontSize: 'small',
                width: '97%',
                padding: '0',
                textAlign: 'center'
                }}
            >
                <thead>
                <tr>
                    <th scope="col" >
                    Lender Name
                    </th>
                    <th scope="col" >
                    Relationship
                    </th>
                    <th scope="col" >
                    Verified ID
                    </th>
                    <th scope="col" >
                    Actions
                    </th>
                </tr>
                </thead>
                <tbody className={dst.shrinkMedia}>
                {entriesToShow.length > 0 ? (
                    entriesToShow.map((len, index) => (
                        <tr key={index}>
                        <td>{len ? (
                            <>
                            {len.lender_lastname && len.lender_lastname.toUpperCase()},{' '}
                            {len.lender_firstname} {len.lender_middlename}
                            </>
                        ) : (
                            ''
                        )}</td>
                        <td>{len.relationship}</td>
                        <td>{len.isIDApproved != 0 ? (<>Yes</>) : (<>No</>)}</td>
                        <td>
                        <div className={`${dst.actionButtons}`}>
                            <p onClick={(e) => handleViewID(len.lender_id, e)}><i className="fa-solid fa-eye fa-sm"></i></p>
                            {userType == 0 && (<><p onClick={(e) => openEditLenderModal(len.lender_id, e)}><i className="fa-solid fa-pen-to-square fa-sm"></i></p>
                            <p onClick={(e) => openArchiveModal(len.lender_id, e)}><i className="fa-solid fa-box-archive fa-sm"></i></p>
                            {len.isIDApproved != 1 ? (<p onClick={(e) => approveID(len.lender_id, len.related_pdl_id, e)} ><i className="fa-solid fa-check fa-sm"></i></p>) : (<></>)}
                            </>)}</div>
                        </td>
                        </tr>
                    ))
                ) : (
                    <tr>
                    <td style={{ padding: '150px' }} colSpan={4}>Nothing to show</td>
                    </tr>
                )}
                </tbody>
            </table>
                </div>
                <div className='row' style={{alignItems: 'center', justifyContent: 'center', marginTop: '15%'}}>
                <div style={{ textAlign: 'center', width: '100%' }}> 
                    <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
                </div>
                </div>
            </div>
            {isCreateLenderOpen && <CreateLender onClose={closeLenderModal} pdlId={id}/>}
            {isEditLenderOpen && <EditLender onClose={closeLenderModal} pdlId={id} lenderID={lenderID}/>}
            {isArchiveModalOpen && <ArchiveModal onClose={closeArchiveModal} primaryLenderID={lenderID} archiveType="Lender"/>}
            {isSuccessModalOpen && <SuccAcc onClose={closeSuccessModal}/>}
        </div>
    );
}
export default LenderData;
