import React, { useState } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module

function PageNavigator({ totalPages, currentPage, onPageChange }) {

  const handlePageClick = (page) => {
    if (page >= 1 && page <= totalPages && page !== currentPage) {
      onPageChange(page);
    }
  };
  const handlePreviousPage = () => {
    if (currentPage > 1) {
      onPageChange(currentPage - 1);
    }
  };

  const handleNextPage = () => {
    if (currentPage < totalPages) {
      onPageChange(currentPage + 1);
    }
  };

  return (
    <div className="page-navigator" style={{marginBottom: '20px', fontSize: '12px'}}>
      <button className={dst.Previous} onClick={handlePreviousPage} disabled={currentPage === 1} style={{display: totalPages === 1 || currentPage === 1 ? 'none' : 'initial'}}>
        Previous
      </button>
      {Array.from({ length: totalPages }, (_, index) => index + 1).map((page) => (
        <button
          key={page}
          className={page === currentPage ? `${dst.Numbers} ${dst.NumbersActive}` : dst.Numbers}
          onClick={() => handlePageClick(page)}
        >
          {page}
        </button>
      ))}
      <button className={dst.Next} onClick={handleNextPage} disabled={currentPage === totalPages} style={{display: totalPages === 1 || currentPage === totalPages || totalPages == 0 ? 'none' : 'initial'}}>
        Next
      </button>
    </div>
  );
}

export default PageNavigator;
