import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../constants/domain';

function OverallDetails() {
  const [isVisible, setVisibility] = useState(true)
  const [tableData, setTableData] = useState([]);

  useEffect(() => {
      getData();
  }, [])

  function getData(){
    axios.get(`${domain}/index.php?action=getOverallDetails`)
    .then(function(response){
      setTableData(response.data);
    }).catch(error => console.error('Error fetching data: ', error));
  }

  const toggleVisibility = () => {
    setVisibility((prevIsVisible) => !prevIsVisible);
  };

  return (
      <div className={`${dst.firstCard} mx-3`}>
      <div className='row d-flex'>
          <div className='col-11'>
            <h6 className="m-3 mb-4 fw-bold" style={{textAlign: 'left'}}>Overall Details</h6>
          </div>
          <div className='col-1'>
            <p className='text-end p-3' onClick={toggleVisibility}>
            <i
              className={`fa-solid ${isVisible ? 'fa-eye' : 'fa-eye-slash'} fa-sm`}
              style={{ fontSize: '16px', cursor: 'pointer' }}
            ></i>
            </p>
          </div>
        </div>
        {isVisible === true && (
        <div>
        <div className="row">
          <div className="col ms-5">
            <p className="mb-3 fw-semibold" style={{ color: '#1570EF', textAlign: 'left' }}>Spending Total</p>
            <p style={{textAlign: 'left'}}>{!tableData ? (<>PHP parseFloat(tableData.total_weekly_spend).toFixed(2)</>) : (<>Nothing yet...</>) }</p>
            <p style={{ fontSize: 'small', color: '#858D9D', textAlign: 'left' }}>Total</p>
          </div>
          <div className="col">
            <p className="mb-3 fw-semibold" style={{ color: '#E19133', textAlign: 'left' }}>Total Transactions</p>
            <div className="row">
              <div className="col">
                <p style={{textAlign: 'left'}}>{!tableData ? (<>{tableData.transactions_count}</>) : (<>0</>)}</p>
                <p style={{ fontSize: 'small', color: '#858D9D', textAlign: 'left' }}>Last 7 Days</p>
              </div>
              <div className="col">
                <p style={{textAlign: 'left'}}>{!tableData ? (<>{tableData.transactions_count_alltime}</>) : (<>0</>)}</p>
                <p style={{ fontSize: 'small', color: '#858D9D', textAlign: 'left' }}>Total</p>
              </div>
            </div>
          </div>
          <div className="col">
            <p className="mb-3 fw-semibold" style={{ color: '#845EBC', textAlign: 'left' }}>Most Popular Product</p>
            <p style={{textAlign: 'left'}}>{!tableData ? (<>{tableData.most_popular_item}</>) : (<>Nothing yet...</>)}</p>
            <p style={{ fontSize: 'small', color: '#858D9D', textAlign: 'left' }}>All Time</p>
          </div>
          <div className="col">
            <p className="mb-3 fw-semibold" style={{ color: '#F36960', textAlign: 'left' }}>PDLs with Low Balance</p>
            <p style={{textAlign: 'left'}}>{!tableData ? (<>{tableData.low_balance}</>) : (<>0</>)}</p>
            <p style={{ fontSize: 'small', color: '#858D9D', textAlign: 'left' }}>PDLs</p>
          </div>
          <div className="col">
            <p className="mb-3 fw-semibold" style={{ color: '#F36960', textAlign: 'left' }}>Largest Spender</p>
            <p style={{textAlign: 'left'}}>{!tableData ? (<>{tableData.largest_spender}</>) : (<>Nothing yet...</>)}</p>
            <p style={{ fontSize: 'small', color: '#858D9D', textAlign: 'left' }}>All Time</p>
          </div>
        </div>
      </div>)}
          {isVisible === false && (
            <div>
              <div className='row'>
                  <div className='col-4'>
                  </div>
                  <div className='col-4' style={{padding: '0 0 84px 0'}}>
                    Details Hidden
                  </div>
                  <div className='col-4'>
                  </div>
                </div>
            </div>
          )}
      </div>
  );
}
export default OverallDetails;