
import React, { useState, useEffect } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import { Link, useLocation } from 'react-router-dom';
import CreatePDL from '../modals/CreatePDL';
import EditPDL from '../modals/EditPDL';
import ConfirmArchive from '../modals/ConfirmArchive';
import PageNavigator from '../navbars/PageNavigator';
import axios from 'axios';
import domain from '../../constants/domain';

function PDLList() {
  
  const [pdlData, setPdlData] = useState([]);
  const [selectedPdlId, setSelectedPdlId] = useState(null);
  const [primaryID, setPrimaryID] = useState(null);
  const [sortedPdlData, setSortedPdlData] = useState([]);
  const [selectedOption, setSelectedOption] = useState(''); // Set the default option to "option4"
  const [isCreatePDLModalOpen, setCreatePDLModalOpen] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [entriesPerPage] = useState(10);
  const [activeTab, setActiveTab] = useState("pdlList");

  useEffect(() => {
    // Make an API call to fetch PDL data
    axios.get(activeTab === 'pdlList' ? 
    `${domain}/index.php?action=getTableData` :
    `${domain}/index.php?action=getArchivedTableData`)
      .then(response => {
        setPdlData([]);
        setSortedPdlData([]);
        if (Array.isArray(response.data)) {
          setPdlData(response.data);
          setSortedPdlData(response.data); 
        } else {
          console.error("API response is not an array:", response.data);
        }
    })
    .catch(error => console.error('Error fetching PDL data:', error));

  }, [activeTab]); // Empty dependency array means this effect runs once on component mount


  

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
    // Implement logic to load data for the new page here
  };
  
  
  const openCreatePDLModal = () => {
    setCreatePDLModalOpen(true);
  };
  const closeCreatePDLModal = () => {
    setCreatePDLModalOpen(false);
  };

  const [isEditPDLModalOpen, setEditPDLModalOpen] = useState(false);
  const openEditPDLModal = (id, e, primID) => {
    setEditPDLModalOpen(true);
    setSelectedPdlId(id);
    setPrimaryID(primID);
    const newUrl = `?pdlNo=${id}`
    window.history.pushState({}, null, newUrl);
  };
  const closeEditPDLModal = () => {
    setEditPDLModalOpen(false);
    const newUrl = `?`
    window.history.pushState({}, null, newUrl);
  };

  const [isArchiveModalOpen, setArchiveModalOpen] = useState(false);
  const openArchiveModal = (id, e, primID) => {
    setArchiveModalOpen(true);
    setSelectedPdlId(id);
    setPrimaryID(primID);
    
  };
  const closeArchivePDLModal = (id) => {
    setArchiveModalOpen(false);
  };


  const handleOptionChange = (event) => {
    const option = event.target.value
    setSelectedOption(option);
    const sortedData = sortPdlData(option);
    setSortedPdlData(sortedData)
  };

  
  const sortPdlData = (option) => {
    let sortedData = [...pdlData];
  
    switch (option) {
      case 'option1': // Sort alphabetically
        sortedData.sort((a, b) => {
          const nameA = `${a.pdl_lastname} ${a.pdl_firstname} ${a.pdl_middlename}`.toUpperCase();
          const nameB = `${b.pdl_lastname} ${b.pdl_firstname} ${b.pdl_middlename}`.toUpperCase();
          return nameA.localeCompare(nameB);
        });
        break;
  
      case 'option2': // Sort by highest balance
        sortedData.sort((a, b) => b.balance - a.balance);
        break;
  
      case 'option3': // Sort by lowest balance
        sortedData.sort((a, b) => a.balance - b.balance);
        break;
  
      case 'option4': // Sort by PDL number
        sortedData.sort((a, b) => a.id - b.id);
        break;
  
      default: // Default: Sort by PDL number
        sortedData.sort((a, b) => a.id - b.id);
        break;
    }
  
    return sortedData;
  };

    const totalEntries = sortedPdlData ? sortedPdlData.length : 0;
    const totalPages = Math.ceil(totalEntries / entriesPerPage);
    let entriesToShow = [];
    if (Array.isArray(sortedPdlData)) {
      entriesToShow = sortedPdlData.slice(
        (currentPage - 1) * entriesPerPage,
        currentPage * entriesPerPage
      );
  }

  const generateReport = () => {
      if(pdlData){
        const action = "generatePDLReport";
        const actionData = { action };
        
        axios.post(domain, actionData)
            .then(response => {
                // Handle successful response, if needed
                 // Response.data will contain the file URL
                 const fileUrl = response.data.fileUrl;
                const formattedFileUrl = fileUrl.substring(fileUrl.indexOf('/reports/'));
                const newWindow = window.open(formattedFileUrl, '_blank');

                // Ensure the new window is fully loaded before attempting to print
                newWindow.onload = () => {
                    newWindow.print();
                };
            })
            .catch(error => {
                // Handle error, if needed
                console.error('Error generating report:', error);
            });
      }
  }
  const tabs = [
    { label: "PDL List", id: "pdlList" },
    { label: "Archived PDLs", id: "archivedItems" },
    // Add more tabs as needed
  ];


  return (
    <div className="container-fluid p-5" >
      <div className='row' style={{marginBottom:'-8px'}}>
        <div className='col-5 d-flex justify-content-start'>
        {tabs.map((tab) => (
              <button
                key={tab.id}
                className={activeTab === tab.id ? `${dst.tab} ${dst.TabActive} ` : dst.tab}
                onClick={() => setActiveTab(tab.id)}
              >
                {tab.label}
              </button>
            ))}
          </div>
        </div>
      <div className={dst.con}>
        {activeTab === 'pdlList' ? (<div className={`${dst.row} mt-1`}>
        <div style={{display: 'flex', padding: '10px'}}>
          <div className='col-3'>
          <p className={`fw-bold ms-3 textColor`} style={{textAlign: 'left', marginTop: '15px'}}>PDLs</p>
          </div>
          <div className='col-9 d-flex justify-content-end'>
          <button className={`${dst.add} me-2`} style={{left: '0', width: 'fit-content', padding: '0 10px', backgroundColor: '#222'}}type='button' onClick={generateReport}>Generate Report
          </button>
          <button className={`${dst.add} me-2`} style={{left: '0'}}type='button' onClick={openCreatePDLModal}>Add PDLs
          </button>
          <select className={`${dst.sort} me-4`} style={{left: '0', width: 'fit-content'}} value={selectedOption} onChange={handleOptionChange} >
                <option value="" disabled hidden>Sort By</option>
                <option value="option1">Alphabetical</option>
                <option value="option2">Highest Balance</option>
                <option value="option3">Lowest Balance</option>
                <option value="option4">PDL No.</option>
              </select>
          </div>
        </div>
        <div className='row'>
          <div className='col-12' style={{ width: '100%', padding: 0 }}>
          <table
            className={dst.tableZebra}
            
            style={{
              border: '#F0F1F3',
              color: '#48505E',
              height: 'auto',
              fontSize: 'small',
              padding: '0',
              textAlign: 'left',
              width: '93.5%', // Set the table width to 100%
              margin: '0 auto',
            }}
          >
            <thead>
              <tr >
                <th scope="col">
                  PDL No.
                </th>
                <th scope="col" >
                  Name
                </th>
                <th scope="col" >
                  Balance
                </th>
                <th scope="col" >
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className={dst.shrinkMedia}>
            {sortedPdlData && entriesToShow.map((pdl, index) => (
                <tr key={index}>
                  <td>PDL-{pdl.id}</td>
                  <td>{pdl ? (
                    <>
                      {pdl.pdl_lastname && pdl.pdl_lastname.toUpperCase()},{' '}
                      {pdl.pdl_firstname} {pdl.pdl_middlename}
                    </>
                  ) : (
                    ''
                  )}</td>
                  <td>PHP {pdl.formatted_balance}</td>
                  <td>
                  <div className={`${dst.actionButtons}`}>
                      <Link exact to={`/dashboard/pdlprofile?pdlId=${pdl.pdl_id}`}><p><i className="fa-solid fa-eye fa-sm"></i></p></Link>
                      <p onClick={(e) => openEditPDLModal(pdl.id, e, pdl.pdl_id)}><i className="fa-solid fa-pen-to-square fa-sm"></i></p>
                      <p onClick={(e) => openArchiveModal(pdl.id, e, pdl.pdl_id)}> <i className="fa-solid fa-box-archive fa-sm"></i></p>
                    </div>
                  </td>
                </tr>
              ))}
            {sortedPdlData && entriesToShow.length === 0 && (
              <tr>
                <td style={{padding: '150px', textAlign: 'center' }} colSpan={6}>Nothing to show</td>
              </tr>
              )}
            </tbody>
          </table>
          </div>
          <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '15%', marginBottom: '20px'}}>
          <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
          </div>
          </div>
        </div>) : (
          <div className={`${dst.row} mt-1`}>
        <div style={{display: 'flex', padding: '10px'}}>
          <div className='col-3'>
          <p className={`fw-bold ms-3 textColor`} style={{textAlign: 'left', marginTop: '15px'}}>Archived PDLs</p>
          </div>
        </div>
        <div className='row'>
          <div className='col-12' style={{ width: '100%', padding: 0 }}>
          <table
            className={dst.tableZebra}
            
            style={{
              border: '#F0F1F3',
              color: '#48505E',
              height: 'auto',
              fontSize: 'small',
              padding: '0',
              textAlign: 'left',
              width: '93.5%', // Set the table width to 100%
              margin: '0 auto',
            }}
          >
            <thead>
              <tr >
                <th scope="col">
                  PDL No.
                </th>
                <th scope="col" >
                  Name
                </th>
                <th scope="col" >
                  Balance
                </th>
                <th scope="col" >
                  Date Archived
                </th>
              </tr>
            </thead>
            <tbody className={dst.shrinkMedia}>
            {sortedPdlData && entriesToShow.map((pdl, index) => (
                <tr key={index}>
                  <td>PDL-{pdl.id}</td>
                  <td>{pdl ? (
                    <>
                      {pdl.pdl_lastname && pdl.pdl_lastname.toUpperCase()},{' '}
                      {pdl.pdl_firstname} {pdl.pdl_middlename}
                    </>
                  ) : (
                    ''
                  )}</td>
                  <td>PHP {pdl.formatted_balance}</td>
                  <td>
                    {pdl.date_archived}
                  </td>
                </tr>
              ))}
            {sortedPdlData && entriesToShow.length === 0 && (
              <tr>
                <td style={{padding: '150px', textAlign: 'center' }} colSpan={6}>Nothing to show</td>
              </tr>
              )}
            </tbody>
          </table>
          </div>
          <div className='row' style={{flexWrap: 'nowrap', alignItems: 'center', justifyContent: 'center', marginTop: '15%', marginBottom: '20px'}}>
          <PageNavigator totalPages={totalPages} currentPage={currentPage} onPageChange={handlePageChange} />
          </div>
          </div>
        </div>
        )}
      </div>
      {isCreatePDLModalOpen && <CreatePDL onClose={closeCreatePDLModal} isVisible={isCreatePDLModalOpen}/>}
      {isEditPDLModalOpen && <EditPDL onClose={closeEditPDLModal} pdlId={selectedPdlId} primaryID={primaryID}/>}
      {isArchiveModalOpen && <ConfirmArchive onClose={closeArchivePDLModal} primaryPDLID={primaryID} archiveType="PDL"/>}
    </div>
    
  );
}

export default PDLList;
