import React, { useState, useEffect } from 'react';
import bjmplogo from '../../assets/bjmp-logo.png';
import lrst from '../../styles/LoginRegStyle.module.css';
import { useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import axios from 'axios';
import domain from '../../constants/domain';
function ForgotPassword() {
  const [imageSrc, setImageSrc] = useState(null);
  const history = useNavigate();
  useEffect(() => {
    // Check if user is logged in
    const token = localStorage.getItem('token');
    if (token) {
      // If not logged in, redirect to login page
      history('/dashboard');
    }
  }, [history]);
  const [emailData, setEmailData] = useState({});
  const {email} = emailData;
  const [errorMessage, setErrorMessage] = useState("");

  const handleChange = (e) => {
    const target = e.target;
    const name = target.name;
    const value = target.value;
    const action = 'forgotPassword';
    setEmailData(values => ({action, ...values, [name]: value}));
  }
    const handleSubmit = async (e) => {
      e.preventDefault();
      if(!email){
        setErrorMessage('Field is empty.');
      }
      else{
        try{
          const response = await axios.post(domain, emailData)
          if (response.data.success){
            setErrorMessage(response.data.message);
          }
          else{
            setErrorMessage(response.data.message);
          }
        }
        catch (error){
          console.error('Error:', error);
        }
      }
    }
    useEffect(() => {
      axios.get(`${domain}/index.php?action=getLogo`).then(
        response => {
          const imageUrl = response.data[0].logo_path;
          const fileName = imageUrl ? imageUrl.split('/').pop() : null;
          setImageSrc(fileName);
        }
      )
  
     }, [])
  return (
    <div className={`${lrst.container} mt-4`}>
      <Helmet>
        <title>BJMP | Forgot Password</title>
      </Helmet>
      <header className='text-center'>
        <img className="img" src={`../logo/${imageSrc}`} alt="BJMP Logo" />
        <h6 className={lrst.thb}>BUREAU OF JAIL MANAGEMENT AND PENOLOGY, REGION III</h6>
        <p className={lrst.lc}>“Changing Lives, Building a Safer Nation”</p>
      </header>
      <form className="d-flex justify-content-center" onSubmit={handleSubmit}>
        <div className="inputDetails" style={{ width: '400px' }}>
          <label htmlFor="email" className="form-label" style={{ textAlign: 'left' }}>
            Email address<span className={lrst.required}>*</span>
          </label>
          <input
            type="text"
            className="form-control mb-2"
            id="email"
            name="email"
            placeholder="Enter your email"
            onChange={handleChange}
          />
          <hr></hr>
            <button type="submit" className="nextBtn btn-secondary mt-4">
              Send Request
            </button>
            <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
        </div>
      </form>
    </div>
  );
}

export default ForgotPassword;
