import React, { useState, useEffect, useRef } from 'react';
import bjmplogo from '../../assets/bjmp-logo.png';
import lrst from '../../styles/LoginRegStyle.module.css';
import { Helmet } from 'react-helmet-async';
import axios from 'axios';
import ProgressBar from '../sections/ProgressBar';
import Verification from '../modals/VerificationModal';
import { useNavigate } from 'react-router-dom';
import domain from '../../constants/domain';
function RegistrationForm() {
  const [imageSrc, setImageSrc] = useState(null);
  const history = useNavigate();
  useEffect(() => {
    // Check if user is logged in
    const token = localStorage.getItem('token');
    if (token) {
      // If not logged in, redirect to login page
      history('/dashboard');
    }
  }, [history]);

  const [step, setStep] = useState(1);
  const [formData, setFormData] = useState({});
  const [errorMessage, setErrorMessage] = useState("");
  const [isVeriModalOpen, setVeriModalOpen] = useState(false);
  const fileInputRef = useRef(null);
  const openVeriModal = () => {
      setVeriModalOpen(true);
  }

  const handleChange = (e) => {
      const name = e.target.name;
      const value = e.target.type === 'file' ? e.target.files[0] : e.target.value;
      const action = 'registerUser';
      if (e.target.type === 'file') {
        const file = e.target.files[0];
        if (file) {
          // Set the file name as validID
          setFormData((values) => ({ ...values, action, validID: file.name, valid_ID: value }));
        } else {
          // Clear validID if no file is selected
          setFormData((values) => ({ ...values, action, validID: '' }));
        }
        return;
      }
      setFormData(values => ({action, ...values, [name]: value}));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { email, password, confirmPass } = formData;
    const { fName, mName, lName, contactNum, validID, valid_ID, compAddress, branch, position } = formData;
    const fileUp = new FormData();
    fileUp.append('valid_ID', valid_ID);
    fileUp.append('email', email);
    fileUp.append('action', 'uploadUserImage');

    if(!fName || !mName || !lName || !contactNum || !validID || !compAddress || !branch || !position){
      setErrorMessage('Please fill in all required fields in Step 2.');
      return; // Do not proceed to the next step
    }
    else{
      setErrorMessage("");
      axios.post(domain, formData).then(response => {
        if(response.data.status){
          axios.post(domain, fileUp);
          openVeriModal();
        }
            
      });  
    }
  };

  const handleNext = async () => {
    if (step === 1) {
      const { email, password, confirmPass } = formData;
      // Check if any of the required fields is empty
      if (!email || !password || !confirmPass) {
        setErrorMessage('Please fill in all required fields in Step 1.');
        return; // Do not proceed to the next step
      }
      else if (password.length < 6) {
        setErrorMessage('Password must be longer than 6 characters');
        return; // Do not proceed to the next step
      }
      if (password != confirmPass) {
        setErrorMessage('Password doesn\'t match');
        return; // Do not proceed to the next step
      }
      // Validate the input fields in step 1
      // If valid, proceed to step 2
      try {
        const response = await axios.post(
          domain,
          {
            email: email,
            action: 'registerUser'
          }
        );
        if (response.data.emailDetect) {
          setErrorMessage(response.data.message);
          return; // Do not proceed to the next step
        } else {
          // Proceed to step 2
          setStep(2);
          setErrorMessage('');
        }
      } catch (error) {
        console.error('Error checking email:', error);
      }

    }
  };
  useEffect(() => {
    axios.get(`${domain}/index.php?action=getLogo`).then(
      response => {
        const imageUrl = response.data[0].logo_path;
        const fileName = imageUrl ? imageUrl.split('/').pop() : null;
        setImageSrc(fileName);
      }
    )

   }, [])

  return (
    <div className={lrst.container}>
      <Helmet>
      <title>BJMP | Register</title>
    </Helmet>
      <header className={lrst['text-center']}>
          <img className="img" src={`../logo/${imageSrc}`}></img>
          <h6 className={lrst.thb}>BUREAU OF JAIL MANAGEMENT AND PENOLOGY, REGION III</h6>
          <p className={lrst.lc}>“Changing Lives, Building a Safer Nation”</p>
      </header>
      <ProgressBar currentStep={step-1} totalSteps={1}/>
      <form className={`${lrst['d-flex']} ${lrst['justify-content-center']} ${step === 2 ? lrst.secActive : ''}`} onSubmit={handleSubmit}>
        {/* Step 1 */}
        <div className={`${lrst.form} ${lrst.first}`}>
          <h6 className={lrst['text-center']}>Account Credentials</h6>
          <div className={lrst.fields}>
            <div className={lrst.inputDetails}>
              <label htmlFor="email" className="form-label">
                Email address<span className={lrst.required}>*</span>
              </label>
              <input
                type="text"
                className="form-control mb-2"
                id="email" name="email"
                placeholder="Enter your email"
                onChange={handleChange}
              />

              <label htmlFor="password" className="form-label">
                Password<span className={lrst.required}>*</span>
              </label>
              <input
                type="password"
                className="form-control mb-2"
                id="password" name="password"
                placeholder="Enter your password"
                onChange={handleChange}
              />

              <label htmlFor="confirmPass" className="form-label">
                Confirm Password<span className={lrst.required}>*</span>
              </label>
              <input
                type="password"
                className="form-control mb-2"
                id="confirmPass" name="confirmPass"
                placeholder="Confirm your password"
                onChange={handleChange}
              />
            </div>
          </div>
          <button className={lrst.nextBtn} type="button" onClick={handleNext} style={{marginBottom: '10px'}}>
            <span className={lrst.btnText} >Next</span>
          </button>
          <p style={{color: 'red', fontSize: '12px', margin: '0'}}>{errorMessage}</p>
        </div>

        {/* Step 2 */}
        <div className={`${lrst.form} ${lrst.second}`}>
          <h6 className={lrst['text-center']}>Basic Information</h6>
          <div className={`${lrst.fields}`}>
            <div className='row'>
              <div className='row'>
                <div className='col'>
                  <div className={lrst.basicInfo}>
                    <label htmlFor="fName" className="form-label">
                      First Name<span className={lrst.required}>*</span>
                    </label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      id="fName" name="fName"
                      placeholder="Enter your first name"
                      onChange={handleChange}
                    />
                  </div>
                </div>
                <div className='col'>
                  <div className={lrst.basicInfo}>
                    <label htmlFor="mName" className="form-label">
                      Middle Name<span className={lrst.required}>*</span>
                    </label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      id="mName" name="mName"
                      placeholder="Enter your middle name"
                      onChange={handleChange}
                    />
                  </div>
                </div>
              </div>
              <div className='row'>
                <div className='col'>
                  <div className={lrst.basicInfo}>
                    <label htmlFor="lName" className="form-label">
                      Last Name<span className={lrst.required}>*</span>
                    </label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      id="lName" name="lName"
                      placeholder="Enter your last name"
                      onChange={handleChange}
                    />
                  </div>
                </div>
                <div className='col'>
                  <div className={lrst.basicInfo}>
                    <label htmlFor="contactNum" className="form-label">
                      Contact Number<span className={lrst.required}>*</span>
                    </label>
                    <input type="text" id="contactNum" name="contactNum" className="form-control" maxLength={11} placeholder="Enter contact info" onChange={handleChange} />
                  </div>
                </div>
              </div>
              <div className='row'>
                <div className='col'>
                  <div className={lrst.basicInfo}>
                  <label htmlFor="validID" className="form-label">User Photo<span className={lrst.required}>*</span></label>
                      <input
                        type="file"
                        className="form-control mb-2"
                        id="validID"
                        name="validID"
                        ref={fileInputRef}
                        accept="image/*" // Accept only image files
                        onChange={handleChange}
                      />

                  </div>
                </div>
                <div className='col'>
                  <div className={lrst.basicInfo}>
                    <label htmlFor="compAddress" className="form-label">
                      Complete Address<span className={lrst.required}>*</span>
                    </label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      id="compAddress" name="compAddress"
                      placeholder="Enter your complete address"
                      onChange={handleChange}
                    />
                  </div>
                </div>
              </div>
              <div className="row">
                <div className="col">
                  <div className="basicInfo">
                    <label htmlFor="branch" className="form-label">
                      BJMP Branch Location<span className={lrst.required}>*</span>
                    </label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      id="branch" name="branch"
                      placeholder="Enter BJMP Branch"
                      onChange={handleChange}
                    />
                  </div>
                </div>
                <div className="col">
                  <div className="basicInfo">
                    <label htmlFor="position" className="form-label">
                      Position<span className={lrst.required}>*</span>
                    </label>
                    <input
                      type="text"
                      className="form-control mb-2"
                      id="position" name="position"
                      placeholder="Enter position"
                      onChange={handleChange}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className={lrst.buttons}>
            <button className={lrst.submit} type="submit" id='register' name='register' style={{marginBottom: '10px'}}>
              <span className="btn-secondary">Submit</span>
            </button>
            
          </div>
          <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
        </div>
      </form>
      {isVeriModalOpen && <Verification email={formData.email} fromWhere={'regForm'}/>}
    </div>
  );
};

export default RegistrationForm;
