import React, { useState, useEffect } from 'react';
import bjmplogo from '../../assets/bjmp-logo.png';
import lrst from '../../styles/LoginRegStyle.module.css';
import { Link, useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import axios from 'axios';
import Verification from '../modals/VerificationModal';
import domain from '../../constants/domain';

function LoginForm () {
  const [imageSrc, setImageSrc] = useState(null);
  const history = useNavigate();
  useEffect(() => {
    // Check if user is logged in
    const token = localStorage.getItem('token');
    if (token) {
      // If not logged in, redirect to login page
      history('/dashboard');
    }
    setFormData({
      rememberMe: false
    });
  }, [history]);
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    rememberMe: false  // Set rememberMe to false by default
  });

  useEffect(() => {
    axios.get(`${domain}/index.php?action=getLogo`).then(
      response => {
        const imageUrl = response.data[0].logo_path;
        const fileName = imageUrl ? imageUrl.split('/').pop() : null;
        setImageSrc(fileName);
      }
    )

   }, [])

  const { email, password, rememberMe } = formData;

  const [errorMessage, setErrorMessage] = useState("");

  const [isVeriModalOpen, setVeriModalOpen] = useState(false);
  const openVeriModal = () => {
      setVeriModalOpen(true);
  }
  const closeVeriModal = () => {
    setVeriModalOpen(false);
  }
  const handleChange = (e) => {
    const target = e.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;
    const action = 'loginUser';

    setFormData(values => ({action, ...values, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault(); // Prevent the default form submission behavior
    if(!email || !password){
      setErrorMessage('Fields are empty!');
    }
    else{
      if (rememberMe) {
        localStorage.setItem('email', email);
        localStorage.setItem('rememberMe', rememberMe);
      } else {
        localStorage.removeItem('email');
        localStorage.removeItem('rememberMe');
      }
      try{
        const response = await axios.post(domain, formData);
        if(response.data.verifyUser === true){
            openVeriModal();
        }
        else{
          if (response.data.success) {
            setErrorMessage("");
            const token = response.data.token;
            const email = response.data.email;
            // Set the token as a default header for Axios
            axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
            localStorage.setItem('token', token);
            localStorage.setItem('email', email);
            window.location.href = '/dashboard/';
          } else {
            setErrorMessage(response.data.message);
          }
        }      
      }
      catch (error){
        console.error('Error:', error);
      }
      
      // Perform form submission logic or API call here
    }
    
  };

  

  return (
    <div className={lrst.container}>
      <Helmet>
        <title>BJMP | Login</title>
      </Helmet>
      <header className='text-center'>
        <img className="img" src={`../logo/${imageSrc}`} alt="BJMP Logo" />
        <h6 className={lrst.thb}>BUREAU OF JAIL MANAGEMENT AND PENOLOGY, REGION III</h6>
        <p className={lrst.lc}>“Changing Lives, Building a Safer Nation”</p>
      </header>
      <form className="d-flex justify-content-center" onSubmit={handleSubmit}>
        <div className="inputDetails" style={{ width: '400px' }}>
          <label htmlFor="email" className="form-label" style={{ textAlign: 'left' }}>
            Email address<span className={lrst.required}>*</span>
          </label>
          <input
            type="text"
            className="form-control mb-2"
            id="email"
            name="email"
            placeholder="Enter your email"
            value={email}
            onChange={handleChange}
          />

          <label htmlFor="password" className="form-label" style={{ textAlign: 'left' }}>
            Password<span className={lrst.required}>*</span>
          </label>
          <input
            type="password"
            className="form-control mb-2"
            id="password"
            name="password"
            placeholder="Enter your password"
            value={password}
            onChange={handleChange}
          />
          <label htmlFor="rememberMe" style={{ display: 'flex', alignItems: 'center' }}>
            <input
              type="checkbox"
              id="rememberMe"
              name="rememberMe"
              checked={rememberMe}
              onChange={handleChange}
              style={{ width: '10%' }}
            />
            Remember me
          </label>
          
            <button type="submit" className="nextBtn btn-secondary mt-2" style={{marginBottom: '10px'}}>
              Login
            </button>
            <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
          <hr />
          <div className='row'>
            <div className='col-6'>
              <Link className={lrst['link-register']} to={"/forgotpassword"}>
                <button type="button" className="mt-2"
                  style={{
                    background: '#ffffff',
                    border: '2px solid #D0D3D9',
                    color: '#5D6679',
                  }}>
                  Forgot Password
                </button>
              </Link>
            </div>
            <div className='col-6'>
              <Link className={lrst['link-register']} to={"/register"}>
                <button type="button" className="mt-2"
                  style={{
                    background: '#ffffff',
                    border: '2px solid #D0D3D9',
                    color: '#5D6679',
                  }}>
                  Sign Up!
                </button>
              </Link>
            </div>
          </div>

        </div>
      </form>
      {isVeriModalOpen && <Verification email={formData.email} fromWhere={'loginForm'} onClose={closeVeriModal}/>}
    </div>
  );
}

export default LoginForm;
