// Sidebar.js

import React, {useState, useEffect} from 'react';
import bjmplogo from '../../assets/bjmp-logo.png';
import { NavLink } from 'react-router-dom';
import dst from '../../styles/DashboardStyle.module.css';
import SettingsModal from '../modals/SettingsModal';
import LogoutModal from '../modals/LogoutModal';
import axios from 'axios';
import domain from '../../constants/domain';

function DashboardSidebar({isSidebarOpen, toggleSidebar}) {

  const [isSettingsModalOpen, setSettingsModalOpen] = useState(false);
  const [isLogOutModalOpen, setLogOutModalOpen] = useState(false);
  const [userType, setUserType] = useState(null);
  const [imageSrc, setImageSrc] = useState(null);
  const email = localStorage.getItem("email");
  // Event handler to open the SettingsModal
  const openSettingsModal = () => {
    setSettingsModalOpen(true);
  };
  const closeSettingsModal = () => {
    setSettingsModalOpen(false);
  };
  const openLogoutModal = () => {
    setLogOutModalOpen(true)
  };
  const closeLogoutModal = () => {
    setLogOutModalOpen(false)
  };

  const [content, setContent] = useState(''); // State to hold the content

  useEffect(() => {
    const handleResize = () => {
      // Update content based on screen width
      if (window.innerWidth < 1368) {
        setContent('BJMP');
      } else {
        setContent('Bureau of Jail Management and Penology, Region III');
      }
    };

    // Listen for window resize
    window.addEventListener('resize', handleResize);

    // Call the handleResize function once to set the initial content
    handleResize();

    // Clean up the event listener on component unmount
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []); // Empty dependency array to run the effect only once


  useEffect(() => {
    axios.get(`${domain}/index.php?action=getUserType&email=${email}`)
    .then(response => {
      if(response.data){
        setUserType(response.data.userType);
        
      }
    })
  },[]) 

  useEffect(() => {
    axios.get(`${domain}/index.php?action=getLogo`).then(
      response => {
        const imageUrl = response.data[0].logo_path;
        const fileName = imageUrl ? imageUrl.split('/').pop() : null;
        setImageSrc(fileName);
      }
    )

   }, [])
  return (
    
    <>
      <div className={`${dst.sidebar} ${isSidebarOpen ? dst.open : ''}`} isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}>
        <div className={dst.logo_details}>
          <img className={dst.logo} src={`../logo/${imageSrc}`} alt="BJMP Logo" onClick={toggleSidebar} style={{cursor: 'pointer'}}/>
          <div className={dst.logo_name}>{content}</div>
          {isSidebarOpen && ( // Render only when the sidebar is open
            <i className="bx bx-menu" id="btn" onClick={toggleSidebar} style={{cursor: 'pointer'}}></i>
          )}
        </div>
        <br />
        <ul>
          <li>
            <NavLink to={"/dashboard/"} className={({isActive}) => (isActive ? dst.active : '')}>
              <i className="fa-solid fa-gauge"></i>
              <span className={isSidebarOpen ? dst.link_name : dst.tooltip}>Dashboard</span>
            </NavLink>
          </li>
          {userType != 2 && (<li>
            <NavLink to={"/dashboard/pdls"} className={({isActive}) => (isActive ? dst.active : '')}>
              <i className="fa-solid fa-person"></i>
              <span className={isSidebarOpen ? dst.link_name : dst.tooltip}>PDLs</span>
            </NavLink>
          </li>)}
          <li>
            <NavLink to={"/dashboard/inventory"} className={({isActive}) => (isActive ? dst.active : '')}>
              <i className="fa-solid fa-boxes-stacked"></i>
              <span className={isSidebarOpen ? dst.link_name : dst.tooltip}>Inventory</span>
            </NavLink>
          </li>
          {userType != 1 && (<li>
            <NavLink to={"/dashboard/pointofsales"} className={({isActive}) => (isActive ? dst.active : '')}>
              <i className="fa-solid fa-bag-shopping"></i>
              <span className={isSidebarOpen ? dst.link_name : dst.tooltip}>Point of Sales</span>
            </NavLink>
          </li>)}
          {userType != 2 && (<li>
            <NavLink to={"/dashboard/loadpdl"} className={({isActive}) => (isActive ? dst.active : '')}>
              <i className="fa-solid fa-money-bill-transfer"></i>
              <span className={isSidebarOpen ? dst.link_name : dst.tooltip}>Load PDL</span>
            </NavLink>
          </li>)}
          {userType != 1 && (<li>
            <NavLink to={"/dashboard/audit"} className={({isActive}) => (isActive ? dst.active : '')}> 
              <i className="fa-solid fa-chart-simple"></i>
              <span className={isSidebarOpen ? dst.link_name : dst.tooltip}>Audit Trail</span>
            </NavLink>
          </li>)}
          {userType == 0 && (<li>
            <NavLink to={"/dashboard/users"} className={({isActive}) => (isActive ? dst.active : '')}> 
              <i className="fa-solid fa-user"></i>
              <span className={isSidebarOpen ? dst.link_name : dst.tooltip}>Users</span>
            </NavLink>
          </li>)}
          <br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br />
          <li>
            <button type="button" onClick={openSettingsModal}>
              <i className="fa-solid fa-gear"></i>
              <span className={isSidebarOpen ? dst.link_name : dst.tooltip}>Settings</span>
            </button>
          </li>
          <li>
            <button type="button" onClick={openLogoutModal}>
            <i className="fa-solid fa-right-from-bracket"></i>
              <span className={isSidebarOpen ? dst.link_name : dst.tooltip}>Logout</span>
            </button>
          </li>
        </ul>
        {isSettingsModalOpen && <SettingsModal onClose={closeSettingsModal} />}
        {isLogOutModalOpen && <LogoutModal onClose={closeLogoutModal}/>}

        </div>
    </>
  );
}

export default DashboardSidebar;