import React, { useState, useRef, useEffect  } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import Fingerprint from './FingerprintIDScan';
import axios from 'axios';
import domain from '../../constants/domain';


function PdlModal({ onClose, pdlId, primaryID }) {

  const [fingerprintID, setFingerprintID] = useState('');
  const [formData, setFormData] = useState({});
  const [errorMessage, setErrorMessage] = useState("");
  const [imageSrc, setImageSrc] = useState(null);
  const [tempImgSrc, setTempImageSrc] = useState(null);
  const [selectMaritalStatus, setSelectMaritalStatus] = useState('');
  const fileInputRef = useRef(null);
  const email = localStorage.getItem("email");
  const handleSvgClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click(); // Trigger click on the file input
    }
  };

  const action = 'updatePDL';

  
  useEffect(() => {
      getUser(); 
    }, []);

    
   function getUser(){
      axios.get(`${domain}/index.php?action=readPDL&pdlNo=${primaryID}`)
      .then(function(response) {
        setFormData(response.data[0]);
        const imageUrl = response.data[0].pdl_image;
        const fileName = imageUrl.split('/').pop();
        setTempImageSrc(fileName);
      })
      .catch(error => console.error('Error fetching PDL data:', error));
   } 

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.type === 'file' ? e.target.files[0] : e.target.value;

    if (name === 'marital_status') {
      setSelectMaritalStatus(value); // Update maritalStatus directly
      setFormData(values => ({...values, action, marital_status: value }));
      return;
    }
    
    else if (e.target.type === 'file') {
      const reader = new FileReader();
  
      reader.onload = (e) => {
        setImageSrc(e.target.result);
        setTempImageSrc(null);
        setFormData(values => ({...values, action, pdl_image: value })); // Use e.target.files[0]
      };
      if (value) {
        reader.readAsDataURL(value);
    
      }
      else{
        setImageSrc(''); 
      }
      return;
    } 
    
    else {
      setFormData(values => ({...values, action, email, [name]: value }));
      return;
    }
};



  const handleSubmit = async (e) => {
    e.preventDefault();
    const {address, age, birthday, cell_no, contact_info, gender, id, marital_status, nationality, offense, pdl_image, pdl_firstname, pdl_middlename, pdl_lastname, reldate, sentence } = formData;
    const updatedFormData = {
      ...formData,
      action: action, // or whatever action you want
    };

    const fileUp = new FormData();
    fileUp.append('pdlimage', pdl_image);
    fileUp.append('pdlNo', id);
    fileUp.append('action', 'updatePDLImage');
    if (!pdl_firstname || !pdl_middlename || !pdl_lastname || !id || !age || !address || !contact_info || !birthday || !gender || !reldate || !offense || !nationality || !marital_status || !sentence || !cell_no || !pdl_image) {
      setErrorMessage('Please fill in all required fields!');
      return;
    } 
    else {
        setErrorMessage('');
        axios.post(`${domain}/index.php?action=updatePDL&entry=${primaryID}`, updatedFormData);
        setTimeout(async () => {
          try {
            axios.post(`${domain}/index.php?action=updatePDLImage&entry=${primaryID}`, fileUp);
            window.location.reload();
          } catch (error) {
          }
        }, 1000); // 3-second wait before making the requests
      }
  };

 
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === 'addPdl') {
      onClose(); // Close the modal
    }
  };

  const [isFingerprintIDModalOpen, setFingerprintIDModalOpen] = useState(false);
  const openFingerprintModal = () => {
    setFingerprintIDModalOpen(true);
  };
  const closeFingerprintModal = (scannedFingerprintID) => {
    setFingerprintIDModalOpen(false);
    if (scannedFingerprintID) {
      setFingerprintID(scannedFingerprintID);
    }
  };

  const rectWidth = imageSrc || tempImgSrc ? 162 : 502; // Adjust width based on imageSrc
  const rectHeight = imageSrc || tempImgSrc ? 162 : 162; // Adjust height based on imageSrc
  
  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="addPdl" tabIndex="-1">
      <div className="">
        <div className={dst['modal-content']} style={{marginTop: '4%', width: '1000px'}}>
            <div className='row mb-2'>
              <div className='col-11 text-start'>
                <h1 className="modal-title fs-5" id="addPdlLabel"><b>Edit PDL Profile</b></h1>
              </div>
              <div className='col-1'>
              <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none'}}></button>
              </div>
            </div> 
          <div className={dst['modal-body']}>
            <form onSubmit={handleSubmit}>
              <div className="row d-flex justify-content-center" style={{display: 'flex'}}>
                <div className="col-12 mb-4" >
                  <svg xmlns="http://www.w3.org/2000/svg" style={{margin: '0', padding: '0'}} width={rectWidth+1} height={rectHeight+1} viewBox={`"0 0 ${rectWidth+1} ${rectHeight+1}"`} onClick={handleSvgClick} fill="none">
                    <rect x="0.5" y="0.5" width={rectWidth} height={rectHeight} rx="41" fill="white" stroke="#9D9D9D"
                      strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                      {tempImgSrc && (
                          <image
                            x="20%"
                            y="20%"
                            xlinkHref={`../images/pdls/${tempImgSrc}`}
                            width="100"
                            height="100"
                          />
                        
                      )}
                      {imageSrc && (
                          <image
                            x="20%"
                            y="20%"
                            xlinkHref={imageSrc}
                            width="100"
                            height="100"
                          />
                      )}
                      {!imageSrc && !tempImgSrc && (
                        <text x="50%" y="50%" textAnchor="middle" alignmentBaseline="middle" fill="#000000" fontSize="14">
                        Upload image here
                      </text>
                      )}     
                  </svg>
                  <input
                    type="file"
                    name='pdl_image'
                    id='pdl_image'
                    ref={fileInputRef}
                    style={{ display: 'none' }}
                    accept="image/*"
                    onChange={handleChange}
                  />
                </div>
              </div>
              <div className="row g-3 align-items-center  mb-2">
                <div className="col-12 text-center">
                  <p style={{marginBottom: '5px'}}><b>Basic Info</b></p>
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="pdl_firstname" className="col-form-label">First Name:</label>
                </div>
                <div className="col-4">
                  <input value={formData.pdl_firstname} type="text" id="pdl_firstname" name="pdl_firstname" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter first name" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="pdl_lastname" className="col-form-label">Last Name:</label>
                </div>
                <div className="col-4">
                  <input value={formData.pdl_lastname} type="text" id="pdl_lastname" name="pdl_lastname" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter last name" onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="pdl_middlename" className="col-form-label">Middle Name:</label>
                </div>
                <div className="col-4">
                  <input value={formData.pdl_middlename} type="text" id="pdl_middlename" name="pdl_middlename" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter middle name" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="id" className="col-form-label">PDL No:</label>
                </div>
                <div className="col-4">
                  <input value={formData.id} type="text" id="id" name="id" className="form-control" maxLength={6} style={{ boxShadow: 'none' }} placeholder="Enter PDL No" onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                    <label htmlFor="age" className="col-form-label">Age:</label>
                  </div>
                  <div className="col-4">
                    <input value={formData.age} type="number" id="age" name="age" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter age" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label className="col-form-label">Sex:</label>
                </div>
                <div className="col-4" style={{display: 'flex'}}>
                  <div className="form-check" style={{display: 'flex'}}>
                    <input value="Male" type="radio" name="gender" id="genderMale"
                      style={{ boxShadow: 'none' }} onChange={handleChange} checked={formData.gender === 'Male'}/>
                    <label htmlFor="genderMale" style={{marginLeft: '10px', marginBottom: '0'}}>
                      Male
                    </label>
                  </div>
                  <div className="form-check ms-3 " style={{display: 'flex'}}>
                    <input value="Female" type="radio" name="gender" id="genderFemale"
                      style={{ boxShadow: 'none' }} onChange={handleChange} checked={formData.gender === 'Female'}/>
                    <label htmlFor="genderFemale" style={{marginLeft: '10px', marginBottom: '0'}}>
                      Female
                    </label>
                  </div>
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="address" className="col-form-label">Address:</label>
                </div>
                <div className="col-10">
                  <input value={formData.address} type="text" id="address" name="address" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter address"  onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="contact_info" className="col-form-label">Contact Info:</label>
                </div>
                <div className="col-4">
                  <input value={formData.contact_info} type="text" id="contact_info" name="contact_info" className="form-control" maxLength={11} style={{ boxShadow: 'none' }} placeholder="Enter contact info" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="birthday" className="col-form-label">Date of Birth:</label>
                </div>
                <div className="col-4">
                  <input value={formData.birthday} type="date" id="birthday" name="birthday" className="form-control" style={{ boxShadow: 'none' }} onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="nationality" className="col-form-label">Nationality:</label>
                </div>
                <div className="col-4">
                  <input value={formData.nationality} type="text" id="nationality" name="nationality" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter contact info" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="maritalStatus" className="col-form-label">Marital Status:</label>
                </div>
                <div className="col-4">
                  <select value={formData.marital_status} id="marital_status" name="marital_status" className="form-control" style={{ boxShadow: 'none' }} onChange={handleChange} >
                    <option value="" disabled hidden >Select status</option>
                    <option value="single">Single</option>
                    <option value="married">Married</option>
                    <option value="widowed">Widowed</option>
                    <option value="separated">Separated</option>
                    <option value="divorced">Divorced</option>
                    </select>
                </div>
              </div>
              <div className="row g-3 align-items-center  mb-2">
                <div className="col-12 text-center">
                  <p style={{marginBottom: '5px'}}><b>PDL Info</b></p>
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
              <div className="col-2">
                    <label htmlFor="offense" className="col-form-label">Offense:</label>
                  </div>
                  <div className="col-4">
                    <input value={formData.offense} type="text" id="offense" name="offense" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter offense" onChange={handleChange} />
                  </div>
                <div className="col-2">
                  <label htmlFor="sentence" className="col-form-label">Sentence:</label>
                </div>
                <div className="col-4">
                  <input value={formData.sentence} type="date" id="sentence" name="sentence" className="form-control" style={{ boxShadow: 'none' }} onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="reldate" className="col-form-label">Estimated Release Date:</label>
                </div>
                <div className="col-4">
                  <input value={formData.reldate} type="date" id="reldate" name="reldate" className="form-control" style={{ boxShadow: 'none' }} onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="cell_no" className="col-form-label">Cell No.:</label>
                </div>
                <div className="col-4">
                  <input value={formData.cell_no} type="text" id="cell_no" name="cell_no" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter cell no." onChange={handleChange} />
                </div>
              </div>
              <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
              <div className="modal-footer" style={{ border: 'none' }}>
                <button type="button" className="btn" onClick={onClose}>Discard</button>
                <button type="submit" className="btn" style={{ backgroundColor: '#505050', color: '#fff' }}>Submit</button>
              </div>
            </form>
          </div>
        </div>
      </div>
      {isFingerprintIDModalOpen && <Fingerprint onClose={closeFingerprintModal} typeOfFinScan={'create'}/>}
    </div>
  );
}

export default PdlModal;
