import React, {useState, useEffect} from 'react';
import dst from '../../styles/DashboardStyle.module.css';
import axios from 'axios';
import domain from '../../constants/domain';

function PopularItems() {
  const [isVisible, setVisibility] = useState(true)
  const [tableData, setTableData] = useState([]);
  useEffect(() => {
      getData();
  }, [])

  function getData(){
    axios.get(`${domain}/index.php?action=getPopularItems`)
    .then(function(response){
      setTableData(response.data);
    }).catch(error => console.error('Error fetching data: ', error));
  }

  const toggleVisibility = () => {
    setVisibility((prevIsVisible) => !prevIsVisible);
  };
  return (
    <div>
      <div className={`${dst.sixthCard} me-3`}>
        <div className='row d-flex'>
          <div className='col-11'>
            <p className="fw-bold">Popular Items</p>
          </div>
          <div className='col-1'>
            <p className='text-end' onClick={toggleVisibility}>
              <i
                className={`fa-solid ${isVisible ? 'fa-eye' : 'fa-eye-slash'} fa-sm`}
                style={{ fontSize: '16px', cursor: 'pointer' }}
              ></i>
            </p>
          </div>
        </div>
        {isVisible === true && (
          <table
            className="table text-center position-relative"
            style={{
              border: '#F0F1F3',
              color: '#48505E',
              height: 'auto',
              fontSize: 'small',
            }}
          >
            <thead>
              <tr>
                <th scope="col">Rank</th>
                <th scope="col">Items</th>
                <th scope="col">Sold</th>
              </tr>
            </thead>
            <tbody>
              {tableData.length !== 0 ? Array.from({ length: 10 }, (_, index) => {
                const data = tableData[index];
                return (
                  <tr key={index}>
                    <td>#{index + 1}</td>
                    <td>{data ? `${data.prodType} - ${data.prodName}` : ''}</td>
                    <td>{data ? `${data.items_qty_sum}pcs` : ''}</td>
                  </tr>
                );
              }) : (
                <tr>
                  <td style={{ padding: '172px 0', textAlign: 'center'}} colSpan={3}>
                    Nothing to show
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        )}
        {isVisible === false && (
          <div>
            <div className='row'>
              <div className='col-4'></div>
              <div className='col-4' style={{ padding: '197px 0' }}>
                Details Hidden
              </div>
              <div className='col-4'></div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default PopularItems;
