import React, { useCallback, useEffect, useState } from 'react';
import dst from '../styles/DashboardStyle.module.css';
import DashboardSidebar from '../components/navbars/DashboardSideNavbar';
import TopNavBar from '../components/navbars/TopNavBar';
import ItemCode from '../components/sections/EnterItemCode';
import ItemList from '../components/sections/ItemList';
import PriceDisplay from '../components/sections/PriceDisplay';
import CheckoutSummary from '../components/sections/CheckoutSummary';
import PDLDisplay from '../components/sections/EnterPDLID';
import { Helmet } from 'react-helmet-async';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import domain from '../constants/domain';


function PointOfSales({ isSidebarOpen, toggleSidebar}){
  const [itemList, setItemList] = useState([]);
  const [selectSearchedStockID, setSelectSearchedStockId] = useState([]);
  const [errorMessage, setErrorMessage] = useState('');
  const [itemSearch, setItemSearch] = useState([]);
  const [priceVal, setPriceVal] = useState([]);
  const [recentItem, setRecentItem] = useState([]);
  const [pdlData, setPdlData] = useState(JSON.parse(localStorage.getItem('pdlData')) || {});
  const action = "addPOSElement"
  const history = useNavigate();
  const email = localStorage.getItem("email");
  const formattedPrices = priceVal.map(item => (parseFloat(item.formatted_price) * parseFloat(item.quantity)));
  const totalFormattedPrice = formattedPrices.reduce((acc, val) => acc + val, 0);
    
    const calculateMargin = () => {
        if (window.innerWidth <= 1368) {
          return isSidebarOpen ? '170px' : '78px';
        } else {
          return isSidebarOpen ? '250px' : '78px';
        }
      };
      const [margin, setMargin] = useState(calculateMargin);
    
    const updateMargin = () => {
      setMargin(calculateMargin());
    };
    useEffect(() => {
      // Check if user is logged in
      const token = localStorage.getItem('token');
      
      if (!token) {
        alert("User not logged in. Please login to continue");
        // If not logged in, redirect to login page
        history('/');
      }
    
      const handleResize = () => {
        updateMargin();
      };
    
      window.addEventListener('resize', handleResize);
    
      // Update margin when sidebar state changes
      updateMargin();
      setRecentItem(JSON.parse(localStorage.getItem('responseData')));
      
      return () => {
        window.removeEventListener('resize', handleResize);
      };
    }, [history, isSidebarOpen]);
    


      const contentStyle = {
      marginLeft: margin,
      transition: 'margin .5s ease',
      backgroundColor: '#e4e9f7',
      height: '100vh'
      };

      const addQuantity = async (element) => {
        if(element){
          const requestData = {
            ...selectSearchedStockID,
            action: action,
            email: email,
            quantity: element
          };
          axios.post(domain, requestData).then(response => {
            console.log('Successfully added quantity:', response.data);
          })
          .catch(error => {
            console.error('Error adding quantity:', error);
          });
          console.log(requestData);
        }
      };

      const searchStock = () => {
        const {searchItem} = itemSearch;
        if (!searchItem){
          setErrorMessage('Please fill in required fields.');
        }
        else{
          axios.get(`${domain}/index.php?action=displaySpecificStock&itemId=${searchItem}`)
          .then(function(response) {
              if(response.data.fail){
                setErrorMessage(response.data.message);
                setSelectSearchedStockId([]);
              }
              else{
                setErrorMessage('');
                setSelectSearchedStockId(response.data[0]);
              }
          }).catch(error => console.error('Error fetching stock data: ', error));
        }
      }

    return (
        <div>
        <Helmet>
            <title>BJMP | Point Of Sales</title>
        </Helmet>
        <DashboardSidebar isSidebarOpen={isSidebarOpen} toggleSidebar={toggleSidebar}/>
        <div style={contentStyle}>
            <TopNavBar />
            <div className='row m-2'>
              <div className='col-7 align-items-center' style={{ alignSelf: 'flex-end' }}>
                    <div className='mb-4'>
                      <PDLDisplay setPdlData={setPdlData} pdlData={pdlData} />
                    </div>     
                    <ItemCode searchStock={searchStock} itemSearch={itemSearch} setItemSearch={setItemSearch} errorMessage={errorMessage}/>
                    <ItemList selectSearchedStockID={[selectSearchedStockID]} addQuantity={addQuantity} itemList={itemList} setRecentItem={setRecentItem}/>
                </div>
                <div className='col-5 align-items-center'>
                    <PriceDisplay recentItem={recentItem} priceVal={totalFormattedPrice}/>
                    <CheckoutSummary setItemList={setItemList} setPriceVal={setPriceVal} priceVal={totalFormattedPrice} pdlData={pdlData}/>
                </div>
              </div>
          </div>
        </div>
    );
    
}
export default PointOfSales;