import React from 'react';
import dst from '../../styles/DashboardStyle.module.css';

function PrisonerProfile({userData}) {

  return (
          <div className={`${dst.prisonerCard} mt-2 p-2 ms-3`}>
            

            <p className="fw-bold ms-3 text-start">Prisoner Profile</p>
            {userData.map ((data, index) => (<div className="row ms-3">
              <div className="col text-start">
              <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-user"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Name</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-id-card"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">PDL No.</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i class="fa-solid fa-person-walking-with-cane"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Age</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-venus-mars"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Gender</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-cake-candles"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Date of Birth</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-home me-2"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Address</p></div>
                </div>
              </div>
              <div key={index} className="col text-start">
                <p>{data ? (
                    <>
                      {data.pdl_lastname && data.pdl_lastname.toUpperCase()},{' '}
                      {data.pdl_firstname} {data.pdl_middlename}
                    </>
                  ) : (
                    ''
                  )}</p>
                <p>{data.id}</p>
                <p>{data.age}</p>
                <p>{data.gender}</p>
                <p>{data.birthday}</p>
                <p>{data.address}</p>
              </div>
              <div className="col text-start">
              <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-phone"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Contact Number</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-flag"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Nationality</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i class="fa-solid fa-ring"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Marital Status</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-gun"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Offense</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-handcuffs"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Sentence</p></div>
                </div>
                <div className='d-flex align-text-right'>
                  <div className='col-1'><i className="fa-solid fa-calendar-days me-2"/></div>
                  <div className='col-11 ms-2'><p className="fw-semibold">Est. Release Date</p></div>
                </div>
              </div>
              <div className="col text-start">
                <p>{data.contact_info}</p>
                <p>{data.nationality}</p>
                <p>{data.marital_status}</p>
                <p>{data.offense}</p>
                <p>{data.sentence}</p>
                <p>{data.reldate}</p>
              </div>
            </div>))}
          </div>
  );
}

export default PrisonerProfile;
