
import React, { useEffect, useState } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import PurchaseDetails from '../modals/PurchaseDetails';
import axios from 'axios';
import domain from '../../constants/domain';
function CheckoutSummary({setItemList, setPriceVal, priceVal, pdlData}) {
  const [changeQuantity, setChangeQuantity] = useState({onState: false, id: null, remainingStock: null});
  const [selectedItem, setSelectedItem] = useState([]);
  const [posElements, setPosElements] = useState([]);
  const [quantity, setQuantity] = useState(0);
  const [errorMessage, setErrorMessage] = useState('');
  useEffect(() => {
    axios.get(`${domain}/index.php?action=getPOSElements&email=${localStorage.getItem('email')}`)
    .then(response => {
      if(response.data){
        setPosElements(response.data);
        setItemList(response.data);
        setPriceVal(response.data); 
      }
    }).catch(error => console.error('Error fetching pos data: ', error));
  }, [])

  const setChangeQuantityTrue = (item, e) => {
    setSelectedItem(item);
    const newUrl = `?checkoutItem=${item}`
    axios.get(`${domain}/index.php?action=getSpecificPOSElement&email=${localStorage.getItem('email')}&itemID=${item}`)
    .then(response => {
      if(response.data){
          setChangeQuantity({onState: true, id: response.data[0].item_id, remainingStock: response.data[0].remaining_stocks});
          setQuantity(parseInt(response.data[0].quantity));
      }
    })
    window.history.pushState({}, null, newUrl);

  };
  const setChangeQuantityFalse = () => {
    setChangeQuantity({onState: false, id: null, remainingStock: null});
    setChangeQuantity(false);
    const newUrl = `?`
    window.history.pushState({}, null, newUrl);
  };
  const changeQuantityData = (item, e) => {
    const changeQty = {
      quantity: quantity,
      action: 'updateQuantity',
      email: localStorage.getItem('email'),
      itemID: item
    }

    axios.post(domain, changeQty).then(response => {
            axios.get(`${domain}/index.php?action=getSpecificPOSElement&email=${localStorage.getItem('email')}&itemID=${item}`)
            .then(response => {
              if(response.data){
                localStorage.setItem('responseData', JSON.stringify(response.data));
                window.location.reload();
              }
            })
          })
          .catch(error => {
            console.error('Error adding quantity:', error);
          });
  }
  const removeEntry = (itemID) => {
    const removeElement = {
      email: localStorage.getItem('email'),
      tempID: itemID,
      action: "removePOSElement"
    }
    axios
      .post(domain, removeElement)
      .then(response => {
        window.location.reload();
      })
      .catch(error => {
        console.error('Error removing entry:', error);
      });
  };

  const [isPurDetOpen, setPurDetOpen] = useState(false);
  const openPurDetModal = () => {
    if(posElements && posElements.length !== 0){
      setErrorMessage('');
      setPurDetOpen(true);
    }
    else{
      setErrorMessage('Nothing is in the checkout yet');
    }
  };
  const closePurDetModal = () => {
    setPurDetOpen(false);
  };
  const incrementQuantity = () => {
    if(quantity < parseInt(changeQuantity.remainingStock)){
      setQuantity(prevQuantity => prevQuantity + 1);
    }
    
  };

  const decrementQuantity = () => {
    if (quantity > 0) {
      setQuantity(prevQuantity => prevQuantity - 1);
    }
  };
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setQuantity((prevValue) => ({...prevValue, [name]: value}));

  }

  return (
        <div className={`${dst.con} mt-3`} style={{ width: '100%', maxHeight: '65vh', padding: '30px', position: 'relative' }}> 
        <div className={`row mt-1 p-1`}>
        <div className='col-12'>
        <table className={`${dst.tableZebra} mt-3 ${dst.shrinkMedia} `}
        style={{ border: '#F0F1F3', color: '#48505E', fontSize: 'small', overflowX: 'auto', width: '100%' }}>
            <thead>
              <tr>
                <th scope="col"  >
                  Item ID
                </th>
                <th scope="col" >
                  Product Type
                </th>
                <th scope="col" >
                  Product Name
                </th>
                <th scope="col" >
                  Price
                </th>
                <th scope="col" >
                  Quantity
                </th>
                <th scope="col" >
                  Action
                </th>
              </tr>
            </thead>
            <tbody className={dst.shrinkMedia}>
            {posElements.slice(0,10).map((item, index) => (
              <tr key={index}>
                <td>{item.item_id && (<>I-{item.item_id}</>)}</td>
                <td>{item.prodType}</td> 
                <td>{item.prodName}</td> 
                <td>PHP{item.formatted_price}</td>
                  {changeQuantity.onState === true && changeQuantity.id === item.item_id ?
                  (<>
                  <td><div style={{ display: 'flex', alignItems: 'center' }}>
                  <button onClick={decrementQuantity} style={{background: 'none', border: '1px solid #ccc', padding: '4px'}}>-</button>
                  <input
                    type="number"
                    name="quantity"
                    id="quantity"
                    onChange={handleChange}
                    style={{    
                      width: '60px',
                      height: '35px',
                      appearance: 'textfield', /* Hide spinners in Firefox */
                      '-moz-appearance': 'textfield', /* Hide spinners in Firefox */ /* Hide spinners in Firefox */}}
                    value={quantity}
                    min="0"
                  />
                  <button onClick={incrementQuantity} style={{background: 'none', border: '1px solid #ccc', padding: '4px'}}>+</button>
                </div>
                </td>
                <td>
                  <div className={`${dst.actionButtons}`}>
                    <p onClick={(e) => changeQuantityData(item.item_id, e)}>
                      <i className="fa-solid fa-check fa-sm"></i>
                    </p>
                    <p onClick={setChangeQuantityFalse}>
                      <i className="fa-solid fa-x fa-sm"></i>
                    </p>
                  </div>
                </td>
                </>)
                :
                (<>
                <td>
                  {item.quantity}
                </td>
                <td>
                  <div className={`${dst.actionButtons}`}>
                    <p onClick={(e) => setChangeQuantityTrue(item.item_id, e)}>
                      <i className="fa-solid fa-pen-to-square fa-sm"></i>
                    </p>
                    <p onClick={(e) => removeEntry(item.temp_id)}>
                      <i className="fa-solid fa-trash fa-sm"></i>
                    </p>
                  </div>
                </td></>)}
                
              </tr>
            ))}
            {posElements.length === 0 && (<tr>
                                      <td colSpan={6} style={{padding: '180px 0'}}>Nothing to show yet</td>
                                    </tr>
            )}
            </tbody>
          </table>
        </div>
        </div>
        <div className='row align-items-center' style={{ alignSelf: 'flex-end' }}>
        <p className='mt-1 mb-2' style={{color: 'red', fontSize: '16px', margin: '0', marginTop: '12%'}}>{errorMessage}</p>
        <hr/>
            <div className="col-9"></div>
            <div className="col-3">
                <button className={`${dst.add} mt-3 mb-3`} onClick={openPurDetModal} style={{ top: '0', left: '0' }}>Check Out</button>
            </div>
        </div>
        {isPurDetOpen && <PurchaseDetails onClose={closePurDetModal} priceVal={priceVal} posElements={posElements} pdlData={pdlData} />}
      </div>
  );
}

export default CheckoutSummary;
