import React, { useState, useRef,useEffect  } from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Update the CSS module path
import Fingerprint from './FingerprintIDScan';
import axios from 'axios';
import domain from '../../constants/domain';
import {v4 as uuidv4} from 'uuid';

function PdlModal({ onClose }) {
  const [fingerprintID, setFingerprintID] = useState('');
  const [formData, setFormData] = useState({});
  const [errorMessage, setErrorMessage] = useState("");
  const [imageSrc, setImageSrc] = useState(null);
  const email = localStorage.getItem("email");
  const [selectMaritalStatus, setSelectMaritalStatus] = useState('');
  const fileInputRef = useRef(null);

  useEffect(() => {
  }, [imageSrc]);

  const handleSvgClick = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click(); // Trigger click on the file input
    }
  };
  const action = 'createPDL';
  
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.type === 'file' ? e.target.files[0] : e.target.value;

    if (name === 'maritalStatus') {
      setSelectMaritalStatus(value); // Update maritalStatus directly
      setFormData(values => ({ ...values, action, maritalStatus: value }));

      return;
    }
    else if (e.target.type === 'file') {
      const reader = new FileReader();
  
      reader.onload = (e) => {
        setImageSrc(e.target.result);
        setFormData(values => ({ ...values, action, pdlimage: value })); // Use e.target.files[0]
      };
      if (value) {
        reader.readAsDataURL(value);
    
      }
      return;
    }

    
    else {
      setFormData(values => ({ ...values, action, email, [name]: value}));
      return;
    }
    
};
  
  const handleSubmit = async (e) => {
    e.preventDefault();

    const { first_name, middle_name, last_name, pdlNo, age, address, gender, contactInfo, dateOfBirth, estimatedReleaseDate, offense, nationality, maritalStatus, sentence, cellNo, pdlimage } = formData;
    const fileUp = new FormData();
    fileUp.append('pdl_image', pdlimage);
    fileUp.append('pdlNo', pdlNo);
    fileUp.append('action', 'uploadPDLImage');
    if (!first_name || !middle_name || !last_name || !pdlNo || !age || !address || !contactInfo || !gender || !dateOfBirth || !estimatedReleaseDate || !offense || !nationality || !maritalStatus || !sentence || !cellNo) {
      setErrorMessage('Please fill in all required fields!');
      return;
    } 
    else {
        setErrorMessage('');
        axios.post(domain, formData).then(response => {
          if(response.data.pdlIdDetect){
            setErrorMessage(response.data.message);
          }
          else{
            setErrorMessage('');
            setTimeout(async () => {
              try {
                axios.post(domain, fileUp);
                window.location.reload();
              } catch (error) {
              }
            }, 1000); // 3-second wait before making the requests
          }
        });
      }
  };

 
  const handleBackdropClick = (e) => {
    // Check if the click target is the backdrop element
    if (e.target.id === 'addPdl') {
      onClose(); // Close the modal
    }
  };


  const rectWidth = imageSrc ? 162 : 502; // Adjust width based on imageSrc
  const rectHeight = imageSrc ? 162 : 162; // Adjust height based on imageSrc


  return (
    <div className={dst.modal} onClick={handleBackdropClick} id="addPdl" tabIndex="-1">
      <div className="">
        <div className={dst['modal-content']} style={{marginTop: '4%', width: '1000px'}}>
            <div className='row mb-2'>
              <div className='col-11 text-start'>
                <h1 className="modal-title fs-5" id="addPdlLabel"><b>Create PDL Profile</b></h1>
              </div>
              <div className='col-1'>
              <button type="button" className="btn-close" onClick={onClose} style={{ boxShadow: 'none'}}></button>
              </div>
            </div> 
          <div className={dst['modal-body']}>
            <form onSubmit={handleSubmit}>
              <div className="row d-flex justify-content-center" style={{display: 'flex'}}>
                <div className="col-12 mb-4" >
                  <svg xmlns="http://www.w3.org/2000/svg" style={{margin: '0', padding: '0'}} width={rectWidth+1} height={rectHeight+1} viewBox={`"0 0 ${rectWidth+1} ${rectHeight+1}"`} onClick={handleSvgClick} fill="none">
                    <rect x="0.5" y="0.5" width={rectWidth} height={rectHeight} rx="41" fill="white" stroke="#9D9D9D"
                      strokeLinecap="round" strokeLinejoin="round" strokeDasharray="5 5" />
                      {imageSrc && (
                        <image
                          x="20%"
                          y="20%"
                          xlinkHref={imageSrc}
                          width="100"
                          height="100"
                        />
                      )}
                      {!imageSrc && (
                        <text x="50%" y="50%" textAnchor="middle" alignmentBaseline="middle" fill="#000000" fontSize="14">
                        Upload image here
                      </text>
                      )}     
                  </svg>
                  <input
                    type="file"
                    name='pdlimage'
                    id='pdlimage'
                    ref={fileInputRef}
                    style={{ display: 'none' }}
                    accept="image/*"
                    onChange={handleChange}
                  />
                </div>
              </div>
              <div className="row g-3 align-items-center  mb-2">
                <div className="col-12 text-center">
                  <p style={{marginBottom: '5px'}}><b>Basic Info</b></p>
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="first_name" className="col-form-label">First Name:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="first_name" name="first_name" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter first name" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="last_name" className="col-form-label">Last Name:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="last_name" name="last_name" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter last name" onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="middle_name" className="col-form-label">Middle Name:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="middle_name" name="middle_name" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter middle name" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="pdlNo" className="col-form-label">PDL No:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="pdlNo" name="pdlNo" className="form-control" maxLength={6} style={{ boxShadow: 'none' }} placeholder="Enter PDL No" onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                    <label htmlFor="age" className="col-form-label">Age:</label>
                  </div>
                  <div className="col-4">
                    <input type="number" id="age" name="age" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter age" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label className="col-form-label">Sex:</label>
                </div>
                <div className="col-4" style={{display: 'flex'}}>
                  <div className="form-check" style={{display: 'flex'}}>
                    <input value="Male" type="radio" name="gender" id="genderMale"
                      style={{ boxShadow: 'none' }} onChange={handleChange} />
                    <label htmlFor="genderMale" style={{marginLeft: '10px', marginBottom: '0'}}>
                      Male
                    </label>
                  </div>
                  <div className="form-check ms-3 " style={{display: 'flex'}}>
                    <input value="Female" type="radio" name="gender" id="genderFemale"
                      style={{ boxShadow: 'none' }} onChange={handleChange} />
                    <label htmlFor="genderFemale" style={{marginLeft: '10px', marginBottom: '0'}}>
                      Female
                    </label>
                  </div>
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="address" className="col-form-label">Address:</label>
                </div>
                <div className="col-10">
                  <input type="text" id="address" name="address" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter address"  onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="contactInfo" className="col-form-label">Contact Info:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="contactInfo" name="contactInfo" className="form-control" maxLength={11} style={{ boxShadow: 'none' }} placeholder="Enter contact info" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="dateOfBirth" className="col-form-label">Date of Birth:</label>
                </div>
                <div className="col-4">
                  <input type="date" id="dateOfBirth" name="dateOfBirth" className="form-control" style={{ boxShadow: 'none' }} onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="nationality" className="col-form-label">Nationality:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="nationality" name="nationality" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter nationality" onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="maritalStatus" className="col-form-label">Marital Status:</label>
                </div>
                <div className="col-4">
                  <select id="maritalStatus" name="maritalStatus" className="form-control" style={{ boxShadow: 'none' }} value={selectMaritalStatus} onChange={handleChange} >
                    <option value="" disabled hidden >Select status</option>
                    <option value="single">Single</option>
                    <option value="married">Married</option>
                    <option value="widowed">Widowed</option>
                    <option value="separated">Separated</option>
                    <option value="divorced">Divorced</option>
                    </select>
                </div>
              </div>
              <div className="row g-3 align-items-center  mb-2">
                <div className="col-12 text-center">
                  <p style={{marginBottom: '5px'}}><b>PDL Info</b></p>
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
              <div className="col-2">
                    <label htmlFor="offense" className="col-form-label">Offense:</label>
                  </div>
                  <div className="col-4">
                    <input type="text" id="offense" name="offense" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter offense" onChange={handleChange} />
                  </div>
                <div className="col-2">
                  <label htmlFor="sentence" className="col-form-label">Sentence:</label>
                </div>
                <div className="col-4">
                  <input type="date" id="sentence" name="sentence" className="form-control" style={{ boxShadow: 'none' }} onChange={handleChange} />
                </div>
              </div>
              <div className="row g-3 align-items-center mb-2">
                <div className="col-2">
                  <label htmlFor="estimatedReleaseDate" className="col-form-label">Estimated Release Date:</label>
                </div>
                <div className="col-4">
                  <input type="date" id="estimatedReleaseDate" name="estimatedReleaseDate" className="form-control" style={{ boxShadow: 'none' }} onChange={handleChange} />
                </div>
                <div className="col-2">
                  <label htmlFor="cellNo" className="col-form-label">Cell No.:</label>
                </div>
                <div className="col-4">
                  <input type="text" id="cellNo" name="cellNo" className="form-control" style={{ boxShadow: 'none' }} placeholder="Enter cell no." onChange={handleChange} />
                </div>
              </div>
              <p style={{color: 'red', fontSize: '16px', margin: '0'}}>{errorMessage}</p>
              <div className="modal-footer" style={{ border: 'none' }}>
                <button type="button" className="btn" onClick={onClose}>Discard</button>
                <button type="submit" className="btn" style={{ backgroundColor: '#505050', color: '#fff' }}>Submit</button>
              </div>
            </form>
          </div>
        </div>
      </div>
      
    </div>
  );
}

export default PdlModal;
