import React, {useState} from 'react';
import dst from '../../styles/DashboardStyle.module.css'; // Import your CSS module
import Success from './LoadSuccess';
import axios from 'axios';
import domain from '../../constants/domain';

function LoadDetails({onClose, loadDetails, pdlDetails}) {

  const [isSuccessModalOpen, setSuccessModalOpen] = useState(false);
  const action = 'loadPDL';

  const handleSubmit = async (e) => {
    e.preventDefault();
    const {id, pdl_lastname, pdl_firstname, pdl_middlename, formatted_balance} = pdlDetails[0];
    const {lenderName, balanceAdd, loadType} = loadDetails[0];
    const newBalance = parseFloat(pdlDetails[0].formatted_balance) + parseFloat(loadDetails[0].balanceAdd);

    const postData = {
      action,
      id,
      email: localStorage.getItem("email"),
      pdl_name: pdl_lastname.toUpperCase() + ', ' + pdl_firstname + " " + pdl_middlename,
      formatted_balance,
      lenderName,
      balanceAdd,
      loadType,
      newBalance: newBalance.toFixed(2) // Ensure newBalance is formatted to 2 decimal places
    };

    try {
      const response = await axios.post(domain, postData);
      if(response.data){
        openSuccessModal();
      }
    } catch (error) {
      console.error('Error sending POST request:', error);
    }
  };
  const openSuccessModal = () => {
    setSuccessModalOpen(true);
  };
  const closeSuccessModal = () => {
    setSuccessModalOpen(false);
    onClose();
  };
  return (
    <div className={dst.modal} id="exampleModal" tabIndex="-1">
        <div className={dst['modal-content']} style={{width: '600px', marginTop: '12%'}}>
          <div className={dst['modal-header']} style={{ border: 'none', textAlign: 'left'}}>
            <div className='row'>
            <h1 className="modal-title fs-5 fw-bold" id="exampleModalLabel">
              Load Details
            </h1>
            </div>
            <hr></hr>
          </div>
          <div className={dst['modal-body']} style={{padding: '0'}}>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4 fw-bold">
                <label htmlFor="itemNo" className="col-form-label">
                  PDL No:
                </label>
              </div>
              <div className="col-6">
                <label htmlFor="itemNo" className="col-form-label">
                {pdlDetails[0].id != null && (<>
                    PDL-
                    </>)}
                    {pdlDetails[0].id}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center">
              <div className="col-4 fw-bold">
                <label htmlFor="itemNo" className="col-form-label">
                  Name:
                </label>
              </div>
              <div className="col-6">
                <label htmlFor="itemNo" className="col-form-label">
                {pdlDetails[0] ? (
                                                <>
                                                    {pdlDetails[0].pdl_lastname && pdlDetails[0].pdl_lastname.toUpperCase()},{' '}
                                                    {pdlDetails[0].pdl_firstname} {pdlDetails[0].pdl_middlename}
                                                </>
                                                ) : (
                                                ''
                                                )}</label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4 fw-bold">
                <label htmlFor="itemNo" className="col-form-label">
                  Lender:
                </label>
              </div>
              <div className="col-6">
                <label htmlFor="itemNo" className="col-form-label">
                {loadDetails[0].lenderName}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4 fw-bold">
                <label htmlFor="itemNo" className="col-form-label">
                  Old Balance:
                </label>
              </div>
              <div className="col-6">
                <label htmlFor="itemNo" className="col-form-label">
                  PHP {pdlDetails[0].pdl_lastname != null && (<>
                      {pdlDetails[0].formatted_balance}
                      </>)}
                      {pdlDetails[0].pdl_lastname == null && (<>
                      XX.XX
                      </>)}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4 fw-bold">
                <label htmlFor="itemNo" className="col-form-label">
                  Amount Loaded:
                </label>
              </div>
              <div className="col-6">
                <label htmlFor="itemNo" className="col-form-label">
                  PHP {(parseFloat(loadDetails[0].balanceAdd)).toFixed(2)}
                </label>
              </div>
            </div>
            <div className="row g-3 align-items-center mb-2">
              <div className="col-4 fw-bold">
                <label htmlFor="itemNo" className="col-form-label">
                  Updated Balance:
                </label>
              </div>
              <div className="col-6">
                <label htmlFor="itemNo" className="col-form-label">
                  PHP {(parseFloat(pdlDetails[0].formatted_balance) + parseFloat(loadDetails[0].balanceAdd)).toFixed(2)}
                </label>
              </div>
            </div>
            <hr></hr>

            <div className={dst['modal-footer']} style={{ border: 'none' }}>
              <button
                type="button"
                className="btn"
                onClick={onClose}
                style={{ borderColor: '#D0D3D9', marginLeft: '335px' }}
              >
                Back
              </button>
              <button
                type="button"
                className="btn"
                style={{
                  backgroundColor: '#505050',
                  color: '#fff',
                  marginLeft: '10px'
                }}
                
                onClick={handleSubmit}
              >
                Load Now!
              </button>
              
            </div>
          </div>
        </div>
        {isSuccessModalOpen && <Success onClose={closeSuccessModal} newBalance={(parseFloat(pdlDetails[0].formatted_balance) + parseFloat(loadDetails[0].balanceAdd)).toFixed(2)}/>}
    </div>
  );
}

export default LoadDetails;
